import React from 'react';
import { Link } from 'react-router-dom';
import BookingForm from './BookingForm';
import TextLoader from '../common/Loaders/TextLoader';
import CircleLoader from '../common/Loaders/CircleLoader';
import DisabledBookingForm from './DisabledBookingForm';
import { connect } from 'react-redux';

class BookingCheckForm extends React.Component {
    render() {
        const { disabled, property, booking } = this.props;
        return (
            <>
                <h3>{property && property.name ? property.name : <TextLoader />}</h3>
                {disabled === true ? <DisabledBookingForm /> : <BookingForm />}
                {property && booking && booking.total > 0 ? (
                    <div className="px-3">
                        <div className="row">
                            <div className="col-sm-6 col-6">
                                <ul className="list-unstyled">
                                    <li>{booking.nights} nights</li>
                                    <li>Cleaning fee</li>
                                    <li>Security deposit</li>
                                    <li>Transaction fee ({property.transactionfee_rate}%)</li>
                                    <li>Tax ({property.tax_rate}%)</li>
                                </ul>
                            </div>
                            <div className="col-sm-6 col-6 text-right">
                                <ul className="list-unstyled">
                                    <li>${booking.propertyFee}</li>
                                    <li>${booking.cleaningFee}</li>
                                    <li>${booking.refundableAmount}</li>
                                    <li>${booking.transactionFee}</li>
                                    <li>${booking.tax}</li>
                                </ul>
                            </div>
                        </div>
                        <hr />
                        <div className="row">
                            <div className="col-sm-9 col-9">
                                <ul className="list-unstyled">
                                    <li>Total</li>
                                </ul>
                            </div>
                            <div className="col-sm-3 col-3 text-right">
                                <ul className="list-unstyled">
                                    <li>${booking.total}</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                ) : null}
                <p className="text-center">
                    For booking assistance please{' '}
                    <Link to="/contact">
                        <span className="underlined-link">Contact us</span>
                    </Link>
                </p>
            </>
        );
    }
}

const mapStateToProps = (state) => ({
    property: state.properties.activeItem,
    loading: state.properties.loading,
    booking: state.booking
});

export default connect(mapStateToProps, null)(BookingCheckForm);
