import { userConstants } from '../_constants';

export function users(state = {}, action) {
    switch (action.type) {
        case userConstants.GETALL_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userConstants.GETALL_SUCCESS:
            return {
                ...state,
                items: action.users,
                loading: false
            };
        case userConstants.GETALL_FAILURE:
            return {
                ...state,
                error: action.error,
                loading: false
            };
        case userConstants.GET_USER_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userConstants.GET_USER_SUCCESS:
            return {
                ...state,
                activeItem: action.user,
                loading: false
            };
        case userConstants.GET_USER_FAILURE:
            return {
                ...state,
                error: action.error,
                loading: false
            };
        case userConstants.CREATE_USER_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userConstants.CREATE_USER_SUCCESS:
            return {
                ...state,
                activeItem: action.user,
                loading: false
            };
        case userConstants.CREATE_USER_FAILURE:
            return {
                ...state,
                error: action.error,
                loading: false
            };
        case userConstants.UPDATE_USER_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userConstants.UPDATE_USER_SUCCESS:
            return {
                ...state,
                activeItem: action.user,
                loading: false
            };
        case userConstants.UPDATE_USER_FAILURE:
            return {
                ...state,
                error: action.error,
                loading: false
            };
        case userConstants.DELETE_USER_REQUEST:
            return {
                ...state,
                items: state.items.map((user) =>
                    user.id === action.id ? { ...user, deleting: true } : user
                )
            };
        case userConstants.DELETE_USER_SUCCESS:
            return {
                ...state,
                items: state.items.filter((user) => user.id !== action.id)
            };
        case userConstants.DELETE_USER_FAILURE:
            return {
                ...state,
                items: state.items.map((user) => {
                    if (user.id === action.id) {
                        // make copy of user without 'deleting:true' property
                        const { deleting, ...userCopy } = user;
                        return { ...userCopy };
                    }

                    return user;
                })
            };
        case userConstants.SEND_EMAIL_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userConstants.SEND_EMAIL_SUCCESS:
            return {
                ...state,
                loading: false
            };
        case userConstants.SEND_EMAIL_FAILURE:
            return {
                ...state,
                loading: false
            };
        default:
            return state;
    }
}
