import React, { useState } from 'react';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { alertActions, userActions } from '../../_actions';
import { connect, useDispatch, useSelector } from 'react-redux';
import { userService } from '../../_services';

// import { GoogleReCaptcha } from 'react-google-recaptcha-v3';

function ContactForm() {
    const [submitted, setSubmitted] = useState(false);

    const dispatch = useDispatch();
    const loading = useSelector((state) => state.users.loading);

    const handleSubmit = (fields) => {
        // window.open(`mailto:info@starsofboston.com?cc=${fields.email}?subject=${name}&body=${fields.message}`);

        dispatch(userActions.sendEmailRequest());
        userService.sendContactEmail(fields).then(
            (data) => {
                dispatch(userActions.sendEmailSuccess());
                dispatch(alertActions.success('Your email has been submitted successfully!'));
                setSubmitted(true);
            },
            (error) => {
                dispatch(userActions.sendEmailFailure());
                dispatch(
                    alertActions.error('Sorry, there was an error while submitting your email')
                );
            }
        );
    };

    return (
        //<GoogleReCaptcha onVerify={this.handleVerify}>
        <Formik
            initialValues={{
                firstName: '',
                lastName: '',
                email: '',
                message: ''
            }}
            validationSchema={Yup.object().shape({
                firstName: Yup.string().required('First Name is required'),
                lastName: Yup.string().required('Last Name is required'),
                email: Yup.string().email('Email is invalid').required('Email is required'),
                message: Yup.string().required('message is required')
            })}
            onSubmit={(fields) => handleSubmit(fields)}
            render={({ errors, status, touched }) => (
                <Form className="contact-form">
                    <div className="row">
                        <div className="col-sm-6">
                            <div className="col-md input-group">
                                <Field
                                    name="firstName"
                                    type="text"
                                    placeholder="   First Name"
                                    className={
                                        'form-control custom-form-control' +
                                        (errors.firstName && touched.firstName
                                            ? ' is-invalid'
                                            : ' mb-3')
                                    }
                                />
                                <ErrorMessage
                                    name="firstName"
                                    component="div"
                                    className="invalid-feedback"
                                />
                            </div>
                        </div>
                        <div className="col-sm-6">
                            <div className="col-md input-group">
                                <Field
                                    name="lastName"
                                    type="text"
                                    placeholder="   Last Name"
                                    className={
                                        'form-control custom-form-control' +
                                        (errors.lastName && touched.lastName
                                            ? ' is-invalid'
                                            : ' mb-3')
                                    }
                                />
                                <ErrorMessage
                                    name="lastName"
                                    component="div"
                                    className="invalid-feedback"
                                />
                            </div>
                        </div>
                        <div className="col-sm-12">
                            <div className="col-md input-group">
                                <Field
                                    name="email"
                                    type="text"
                                    placeholder="   Your Email"
                                    className={
                                        'form-control custom-form-control' +
                                        (errors.email && touched.email ? ' is-invalid' : ' mb-3')
                                    }
                                />
                                <ErrorMessage
                                    name="email"
                                    component="div"
                                    className="invalid-feedback"
                                />
                            </div>
                        </div>
                        <div className="col-sm-12">
                            <div className="col-md input-group">
                                <Field
                                    name="message"
                                    component="textarea"
                                    placeholder="   Your Message"
                                    rows="5"
                                    className={
                                        'form-control custom-form-control' +
                                        (errors.message && touched.message
                                            ? ' is-invalid'
                                            : ' mb-3')
                                    }
                                />
                                <ErrorMessage
                                    name="message"
                                    component="div"
                                    className="invalid-feedback"
                                />
                            </div>
                        </div>
                        <div className="col-sm-12">
                            <div className="col-md input-group">
                                <button
                                    type="submit"
                                    disabled={submitted || loading}
                                    className="btn btn-outline-primary pull-left user-btn user-btn-outline">
                                    Submit
                                </button>
                            </div>
                        </div>
                    </div>
                </Form>
            )}
        />
        // </GoogleReCaptcha>
    );
}

export default ContactForm;
