import React from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { amenityAction } from '../../../_actions/amenity.actions';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CFormGroup,
    CInput,
    CLabel,
    CRow
} from '@coreui/react';
import AmenityMediaManager from '../../../components/admin/AmenityMediaManager';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';

class AmenityEdit extends React.Component {
    state = {
        amenity: {
            id: '',
            name: '',
            image: ''
        },
        loading: false
    };

    componentDidMount() {
        this.props.getAmenity(this.props.match.params.id);
    }

    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        this.setState({
            amenity: nextProps.amenity,
            loading: nextProps.loading
        });
    }

    handleChange = (e) => {
        let { amenity } = this.state;
        amenity = {
            ...amenity,
            [e.target.name]: e.target.value
        };
        this.setState({ amenity: amenity });
    };

    handleReset = () => {
        const { amenity } = this.state;
        amenity.name = '';
        amenity.image = '';
        this.setState({ amenity: amenity });
    };

    handleSelectImage = (image) => {
        const { amenity } = this.state;
        amenity.image = image;
        this.setState({ amenity: amenity });
    };

    handleSubmit = () => {
        this.props.updateAmenity(this.state.amenity, this.props.history);
        console.log(this.state);
    };

    render() {
        const { amenity, loading } = this.state;
        return (
            <div className="animated fadeIn">
                <CRow>
                    <CCol lg={6} sm={12} className="mx-auto">
                        <CCard custom>
                            <CCardHeader>
                                <h4>Edit Amenity</h4>
                            </CCardHeader>
                            <CCardBody>
                                <CFormGroup className="mb-3">
                                    <CLabel for="title">Name</CLabel>
                                    {amenity ? (
                                        <CInput
                                            type="text"
                                            name="name"
                                            required
                                            onChange={this.handleChange}
                                            value={amenity.name}
                                        />
                                    ) : (
                                        <CircleLoader />
                                    )}
                                </CFormGroup>
                                {amenity && amenity.image && (
                                    <div
                                        style={{
                                            backgroundImage: `url(${amenity.image.file})`,
                                            backgroundPosition: 'center',
                                            backgroundSize: 'cover',
                                            height: '200px',
                                            width: '40%',
                                            margin: 'auto'
                                        }}></div>
                                )}
                                {amenity && (
                                    <AmenityMediaManager
                                        openButtonText={
                                            amenity.image ? 'Change Image' : 'Add Image'
                                        }
                                        selectedItem={amenity.image !== null ? amenity.image : ''}
                                        onSelectItem={this.handleSelectImage}
                                    />
                                )}

                                <CRow>
                                    <CCol xs="12 text-center" sm="6">
                                        <CButton
                                            type="submit"
                                            color="primary"
                                            className="mr-1"
                                            onClick={this.handleSubmit}>
                                            {loading ? 'Saving' : 'Save'}
                                        </CButton>
                                    </CCol>
                                    <CCol xs="12 text-center" sm="6">
                                        <CButton
                                            type="reset"
                                            color="danger"
                                            className="mr-1"
                                            onClick={this.handleReset}>
                                            Reset
                                        </CButton>
                                    </CCol>
                                </CRow>
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    amenity: state.amenities.activeItem,
    loading: state.amenities.loading
});

const actionCreators = {
    getAmenity: amenityAction.getAmenity,
    updateAmenity: amenityAction.updateAmenity
};

export default connect(mapStateToProps, actionCreators)(withRouter(AmenityEdit));
