import React from 'react';
import { Link, withRouter } from 'react-router-dom';

// property
import BookingCheckForm from '../../../components/guest/BookingCheckForm';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';
import { PayPalButton } from 'react-paypal-button-v2';

import moment from 'moment';

// redux
import { alertActions, bookingActions } from '../../../_actions';
import { propertyAction } from '../../../_actions/property.actions';
import { connect } from 'react-redux';
import WithPixel from '../../../components/WithPixel';
import WithGATracker from '../../../components/WithGATracker';
import ReactGA from 'react-ga';

class CheckOutPayment extends React.Component {
    state = {
        paypalLoaded: false
    };

    componentDidMount() {
        if (this.props.booking.checkinDate === '') {
            this.props.history.goBack();
        }
    }

    handleBook = () => {
        const { booking } = this.props;
        ReactGA.event({
            category: 'ecommerce',
            action: 'purchase'
        });
        this.props.addBookingPayment(
            {
                bookerville_id: booking.bookervilleID,
                checkin_date: moment(booking.checkinDate).format('YYYY-MM-DD'),
                checkout_date: moment(booking.checkoutDate).format('YYYY-MM-DD'),
                adults: booking.adultsCount,
                children: booking.childrenCount === '' ? 0 : booking.childrenCount,
                property_fee: booking.propertyFee,
                cleaning_fee: booking.cleaningFee,
                refundable_amount: booking.refundableAmount,
                transaction_fee: booking.transactionFee,
                tax: booking.tax,
                total: booking.total,
                guest: {
                    first_name: booking.guest.firstName,
                    last_name: booking.guest.lastName,
                    email: booking.guest.email,
                    phone_number: booking.guest.phoneNumber
                },
                billing: {
                    country: booking.billing.country,
                    state: booking.billing.state,
                    city: booking.billing.city,
                    street: booking.billing.street,
                    zip_code: booking.billing.zipCode
                }
            },
            this.props.history
        );
    };

    render() {
        const { booking, property } = this.props;
        return (
            <>
                <section className="main-background-2"></section>
                <section className="checkout-payment">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 col-sm-12 col-xl-7">
                                <div className="d-flex justify-content-between mb-4">
                                    <h3 className="text-blue">Payment Information</h3>
                                    <p>Step 3 of 3</p>
                                </div>
                                <h5 className="text-blue">Payment Method</h5>
                                {this.state.paypalLoaded !== true && <CircleLoader />}
                                {property && booking && booking.total > 0 && (
                                    <PayPalButton
                                        amount={booking.total}
                                        // disableCard
                                        style={{ color: 'blue' }}
                                        shippingPreference="NO_SHIPPING"
                                        onButtonReady={() => {
                                            this.setState({
                                                paypalLoaded: true
                                            });
                                        }}
                                        onSuccess={(details, data) => {
                                            console.log(
                                                '_Transaction completed by_',
                                                details,
                                                data,
                                                details.payer.name.given_name
                                            );
                                            this.handleBook();
                                        }}
                                        catchError={(error) => {
                                            console.log('_Transaction Error_', error);
                                        }}
                                        options={{
                                            clientId:
                                                'AVGSCRet9DZ7Ct0uNXIXGlZDO4EIlbnmGty4_jUvnG5Wn0GPTYJudDiB1tqkM2srJGWNZPEE1ZKt4_71'
                                        }}
                                        // Sandbox
                                        // options={{
                                        //     clientId: "AQod8JtRRk59F5HjcGOH7ZNymA7N6s2B1WhXH_g_UCBY40Y1LPSt0fxhdaR2rYmIQPNP2k7uZ5gL95Oe",
                                        //     currency: "USD"
                                        // }}
                                    />
                                )}
                                <p>
                                    <Link to="/contact" target="_blank">
                                        <span className="underlined-link">Contact us</span>
                                    </Link>{' '}
                                    if you want more information about long-term (+30 days)
                                    reservations.
                                </p>
                            </div>
                            <div className="col-12 col-sm-12 col-xl-5">
                                <div className="booking-information">
                                    <BookingCheckForm disabled={true} />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        );
    }
}

const mapStateToProps = (state) => {
    const { properties, booking } = state;
    return {
        property: properties.activeItem,
        booking: booking
    };
};

const actionCreators = {
    getPropertyDetail: propertyAction.getPropertyDetail,
    addBookingPayment: bookingActions.addBookingPayment,
    getBookingQuote: bookingActions.getBookingQuote,
    alertError: alertActions.error
};

export default connect(
    mapStateToProps,
    actionCreators
)(WithPixel(WithGATracker(withRouter(CheckOutPayment))));
