import { propertyConstants } from '../_constants';

const initialState = { items: [], activeItem: {}, loading: false };

export function properties(state = initialState, action) {
    switch (action.type) {
        case propertyConstants.GET_ALL_REQUEST:
            return {
                ...state,
                items: [],
                loading: true
            };
        case propertyConstants.GET_ALL_SUCCESS:
            return {
                ...state,
                items: action.items,
                loading: false
            };
        case propertyConstants.GET_ALL_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case propertyConstants.GET_PROPERTY_LOCATIONS_REQUEST:
            return {
                ...state,
                items: [],
                loading: true
            };
        case propertyConstants.GET_PROPERTY_LOCATIONS_SUCCESS:
            return {
                ...state,
                items: action.items,
                loading: false
            };
        case propertyConstants.GET_PROPERTY_LOCATIONS_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case propertyConstants.GET_PROPERTY_REQUEST:
            return {
                ...state,
                activeItem: {},
                loading: true
            };
        case propertyConstants.GET_PROPERTY_SUCCESS:
            return {
                ...state,
                activeItem: action.property,
                loading: false
            };
        case propertyConstants.GET_PROPERTY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case propertyConstants.GET_PROPERTY_DETAIL_REQUEST:
            return {
                ...state,
                activeItem: {},
                loading: true
            };
        case propertyConstants.GET_PROPERTY_DETAIL_SUCCESS:
            return {
                ...state,
                activeItem: action.property,
                loading: false
            };
        case propertyConstants.GET_PROPERTY_DETAIL_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case propertyConstants.CREATE_PROPERTY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case propertyConstants.CREATE_PROPERTY_SUCCESS:
            return {
                ...state,
                activeItem: action.property,
                loading: false
            };
        case propertyConstants.CREATE_PROPERTY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case propertyConstants.UPDATE_PROPERTY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case propertyConstants.UPDATE_PROPERTY_SUCCESS:
            return {
                ...state,
                activeItem: action.property,
                loading: false
            };
        case propertyConstants.UPDATE_PROPERTY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case propertyConstants.DELETE_PROPERTY_REQUEST:
            return {
                ...state,
                items: state.items.map((property) =>
                    property.id === action.id ? { ...property, deleting: true } : property
                )
            };
        case propertyConstants.DELETE_PROPERTY_SUCCESS:
            return {
                ...state,
                items: state.items.filter((property) => property.id !== action.id)
            };
        case propertyConstants.DELETE_PROPERTY_FAILURE:
            return {
                ...state,
                items: state.items.map((property) => {
                    if (property.id === action.id) {
                        const { deleting, ...propertyCopy } = property;
                        return { ...propertyCopy };
                    }
                    return property;
                }),
                error: action.error
            };
        case propertyConstants.GET_PROPERTY_PRICE_ITEMS_REQUEST:
            return {
                ...state,
                loading: true
            };
        case propertyConstants.GET_PROPERTY_PRICE_ITEMS_SUCCESS:
            return {
                ...state,
                activeItem: {
                    ...state.activeItem,
                    pricingItems: action.pricingItems
                },
                loading: false
            };
        case propertyConstants.GET_PROPERTY_PRICE_ITEMS_FAILURE:
            return {
                ...state,
                activeItem: {
                    ...state.activeItem,
                    pricingItems: []
                },
                loading: false
            };
        case propertyConstants.CREATE_PROPERTY_PRICE_ITEM_REQUEST:
            return {
                ...state,
                loading: true
            };
        case propertyConstants.CREATE_PROPERTY_PRICE_ITEM_SUCCESS:
            return {
                ...state,
                // activeItem: {
                //     ...state.activeItem,
                //     pricingItems: [
                //         ...state.activeItem.pricingItems,
                //         action.pricingItem
                //     ]
                // },
                loading: false
            };
        case propertyConstants.CREATE_PROPERTY_PRICE_ITEM_FAILURE:
            return {
                ...state,
                loading: false
            };
        default:
            return state;
    }
}
