import React, { Component } from 'react';

import { connect } from 'react-redux';

class EditableTitle extends Component {
    render() {
        const { id, texts } = this.props;
        return <>{texts && texts.length > 0 && texts[id].title}</>;
    }
}

const mapStateToProps = (state) => {
    const { texts } = state;
    return {
        texts: texts.items
    };
};

export default connect(mapStateToProps, null)(EditableTitle);
