import React from 'react';
import PlacesAutocomplete from 'react-places-autocomplete';
import { CInput } from '@coreui/react';
import CIcon from '@coreui/icons-react/es/CIcon';

class LocationSearchInput extends React.Component {
    constructor(props) {
        super(props);
        this.placesService = new window.google.maps.places.PlacesService(
            document.createElement('div')
        );
    }

    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        this.setState({ address: nextProps.address });
    }

    handleSelect = (address, placeId) => {
        this.setState({ address });

        const request = {
            placeId: placeId,
            fields: [
                'name',
                'geometry',
                'address_components',
                'formatted_address',
                'icon',
                'id',
                'name',
                'place_id',
                'reference',
                'scope',
                'types',
                'url',
                'utc_offset',
                'vicinity'
            ]
        };

        this.placesService.getDetails(request, (place, status) => {
            if (status === window.google.maps.places.PlacesServiceStatus.OK) {
                this.props.onPlaceChanged(place);
                console.log(place.geometry.location.lat(), place.geometry.location.lng());
            }
        });
    };

    render() {
        const { name, address, onChange, onBlur, onReset, valid, invalid } = this.props;
        return (
            <PlacesAutocomplete value={address} onChange={onChange} onSelect={this.handleSelect}>
                {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                    <div className="autocomplete-container">
                        <div className="place-input-wrapper">
                            <CInput
                                {...getInputProps({
                                    name,
                                    placeholder: 'Search for address...',
                                    valid,
                                    invalid,
                                    onBlur
                                })}
                            />
                            {/*<CIcon name="cil-x-circle" onClick={onReset}/>*/}
                        </div>
                        <div className="autocomplete-dropdown-container">
                            {loading && <div>Loading...</div>}
                            {suggestions.map((suggestion, index) => {
                                const className = suggestion.active
                                    ? 'suggestion-item active'
                                    : 'suggestion-item';

                                return (
                                    <div
                                        key={index}
                                        {...getSuggestionItemProps(suggestion, {
                                            className
                                        })}>
                                        <CIcon name="cil-location-pin" />
                                        <span>{suggestion.description}</span>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                )}
            </PlacesAutocomplete>
        );
    }
}

export default LocationSearchInput;
