import React from 'react';
import 'react-dropzone-uploader/dist/styles.css';
import Dropzone from 'react-dropzone-uploader';
import appConfig from '../../../_config/config';
import { alertActions } from '../../../_actions';
import { connect } from 'react-redux';

// function UploadSubmitButton() {
//     return (
//         <CButton color='dark' className='m-2'>Upload</CButton>
//     )
// }

function DropZoneUploader(props) {
    const getUploadParams = ({ file, meta }) => {
        const body = new FormData();
        body.append('file', file);
        if (props.propertyId) {
            body.append('property', props.propertyId);
        }
        return {
            url: `${appConfig.apiEndpoint}/media/create`,
            headers: { Authorization: 'Bearer ' + localStorage.getItem('access_token') },
            body: body
        };
    };

    const handleChangeStatus = ({ meta, file, xhr }, status) => {
        console.log(status, meta, file);

        if (status === 'done') {
            let response = JSON.parse(xhr.response);
            if (props.onUploadSuccess !== undefined) {
                props.onUploadSuccess(response);
            }
            props.alertSuccess(`${file.name} has been uploaded successfully`);
        }

        if (status === 'error_upload') props.alertError(`${file.name} upload failed`);
    };

    const handleSubmit = (files) => {
        console.log(files.map((f) => f.meta));
    };

    return (
        <Dropzone
            getUploadParams={getUploadParams}
            onChangeStatus={handleChangeStatus}
            onSubmit={handleSubmit}
            maxFiles="100"
            inputContent="Drop files or Click to browse (max 100 files)"
            styles={{
                dropzone: { width: '100%', maxHeight: '70vh' },
                dropzoneActive: { borderColor: 'green' },
                dropzoneReject: { borderColor: 'red', backgroundColor: '#DAA' }
            }}
            // autoUpload={false}
            // submitButtonDisabled={false}
            SubmitButtonComponent={null}
            djsConfig={{
                headers: { Authorization: 'Bearer ' + localStorage.getItem('access_token') }
            }}
            accept="image/*,audio/*,video/*"
        />
    );
}

function mapState(state) {
    const { media } = state;
    return { media };
}

const actionCreators = {
    alertSuccess: alertActions.success,
    alertError: alertActions.error
};

export default connect(mapState, actionCreators)(DropZoneUploader);
