import React, { Suspense, useEffect } from 'react';
import { Switch, Route, Redirect, Link } from 'react-router-dom';

import CovidBanner from './CovidBanner';
import CookieConsent from 'react-cookie-consent';
import HeaderNavbar from './HeaderNavbar';
import Footer from './Footer';
import guestRoutes from '../../routes/guestRoutes';
import PulseLoader from '../../components/common/Loaders/PulseLoader';
import ChatWidget from '../../components/guest/chat-widget/ChatWidget';

export default function GuestLayout(props) {
    // const handleAccept = () => {
    //     ReactPixel.grantConsent()
    //     console.log('Reactpixel', ReactPixel)
    // }
    //
    // useEffect(() => {
    //     ReactPixel.revokeConsent();
    //     console.log('revokeConsent')
    // }, [])

    return (
        <>
            <CovidBanner />
            <HeaderNavbar />
            <Suspense fallback={<PulseLoader />}>
                <Switch>
                    {guestRoutes.map((route, idx) => {
                        return route.component ? (
                            <Route
                                key={idx}
                                path={route.path}
                                exact={route.exact}
                                name={route.name}
                                render={(props) => <route.component {...props} />}
                            />
                        ) : null;
                    })}
                </Switch>
            </Suspense>
            <Footer />
            <CookieConsent
                location="bottom"
                buttonText="I understand"
                cookieName="acceptPolicy"
                style={{ background: '#2B373B' }}
                buttonClasses={'accept-button'}
                // enableDeclineButton
                // declineButtonText="decline"
                // declineButtonClasses={'decline-button'}
                // onAccept={handleAccept}
                expires={60}>
                <p>
                    This website uses cookies to enhance the user experience. <br />
                    Please read our full <Link to={'cookie-policy'}> Cookie Policy here</Link>
                </p>
            </CookieConsent>
            <ChatWidget />
        </>
    );
}
