import {
    CButton,
    CCard,
    CCardBody,
    CCol,
    CContainer,
    CForm,
    CFormFeedback,
    CFormGroup,
    CInput,
    CRow
} from '@coreui/react';
import React, { Component } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { userActions } from '../../../_actions';
import connect from 'react-redux/es/connect/connect';

const initialValues = {
    first_name: '',
    last_name: '',
    username: '',
    email: '',
    role: 'E',
    password: '',
    confirm_password: ''
};

class CreateUser extends Component {
    handleSubmit = (fields) => {
        console.log(fields);
        this.props.createUser({
            first_name: fields.first_name,
            last_name: fields.last_name,
            username: fields.username,
            email: fields.email,
            role: fields.role,
            password: fields.password
        });
    };

    render() {
        const { loading } = this.props;
        console.log('loading', loading);

        return (
            <div className="c-app c-default-layout flex-row align-items-center">
                <CContainer>
                    <CRow className="justify-content-center">
                        <CCol md="9" lg="7" xl="6">
                            <CCard className="mx-4">
                                <CCardBody className="p-4">
                                    <h3 className="text-center">Create a new user</h3>
                                    <Formik
                                        initialValues={initialValues}
                                        validationSchema={Yup.object().shape({
                                            first_name: Yup.string()
                                                .min(
                                                    2,
                                                    `First name has to be at least 2 characters`
                                                )
                                                .required('First name is required'),
                                            last_name: Yup.string()
                                                .min(1, `Last name has to be at least 1 character`)
                                                .required('Last name is required'),
                                            username: Yup.string()
                                                .min(5, `Username has to be at least 5 characters`)
                                                .required('Username is required'),
                                            email: Yup.string()
                                                .email('Invalid email address')
                                                .required('Email is required!'),
                                            role: Yup.string().required('Role is required!'),
                                            password: Yup.string()
                                                .min(
                                                    6,
                                                    `Password has to be at least ${6} characters!`
                                                )
                                                // .matches(/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,}/, 'Password must contain: numbers, uppercase and lowercase letters\n')
                                                .required('Password is required'),
                                            confirm_password: Yup.string()
                                                .oneOf(
                                                    [Yup.ref('password'), null],
                                                    'Passwords must match'
                                                )
                                                .required('Password confirmation is required')
                                        })}
                                        onSubmit={(fields) => this.handleSubmit(fields)}>
                                        {({
                                            values,
                                            errors,
                                            touched,
                                            status,
                                            dirty,
                                            handleChange,
                                            handleBlur,
                                            handleSubmit,
                                            isValid,
                                            handleReset
                                        }) => (
                                            <CRow>
                                                <CCol lg="12">
                                                    <CForm
                                                        onSubmit={handleSubmit}
                                                        noValidate
                                                        name="userForm">
                                                        <CFormGroup>
                                                            {/*<CLabel for="first_name">First Name</CLabel>*/}
                                                            <CInput
                                                                type="text"
                                                                name="first_name"
                                                                id="first_name"
                                                                placeholder="First Name"
                                                                autoComplete="given-name"
                                                                valid={!errors.first_name}
                                                                invalid={
                                                                    touched.first_name &&
                                                                    !!errors.first_name
                                                                }
                                                                autoFocus={true}
                                                                required
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                                value={values.first_name}
                                                            />
                                                            <CFormFeedback>
                                                                {errors.first_name}
                                                            </CFormFeedback>
                                                        </CFormGroup>
                                                        <CFormGroup>
                                                            {/*<CLabel for="last_name">Last Name</CLabel>*/}
                                                            <CInput
                                                                type="text"
                                                                name="last_name"
                                                                id="last_name"
                                                                placeholder="Last Name"
                                                                autoComplete="family-name"
                                                                valid={!errors.last_name}
                                                                invalid={
                                                                    touched.last_name &&
                                                                    !!errors.last_name
                                                                }
                                                                required
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                                value={values.last_name}
                                                            />
                                                            <CFormFeedback>
                                                                {errors.last_name}
                                                            </CFormFeedback>
                                                        </CFormGroup>
                                                        <CFormGroup>
                                                            {/*<CLabel for="username">User Name</CLabel>*/}
                                                            <CInput
                                                                type="text"
                                                                name="username"
                                                                id="username"
                                                                placeholder="User Name"
                                                                autoComplete="username"
                                                                valid={!errors.username}
                                                                invalid={
                                                                    touched.username &&
                                                                    !!errors.username
                                                                }
                                                                required
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                                value={values.username}
                                                            />
                                                            <CFormFeedback>
                                                                {errors.username}
                                                            </CFormFeedback>
                                                        </CFormGroup>
                                                        <CFormGroup>
                                                            {/*<CLabel for="email">Email</CLabel>*/}
                                                            <CInput
                                                                type="email"
                                                                name="email"
                                                                id="email"
                                                                placeholder="Email"
                                                                autoComplete="email"
                                                                valid={!errors.email}
                                                                invalid={
                                                                    touched.email && !!errors.email
                                                                }
                                                                required
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                                value={values.email}
                                                            />
                                                            <CFormFeedback>
                                                                {errors.email}
                                                            </CFormFeedback>
                                                        </CFormGroup>
                                                        <CFormGroup>
                                                            {/*<CLabel htmlFor="role">Role :</CLabel>*/}
                                                            <CInput
                                                                type="select"
                                                                name="role"
                                                                id="role"
                                                                valid={!errors.role}
                                                                invalid={
                                                                    touched.role && !!errors.role
                                                                }
                                                                required
                                                                onBlur={handleBlur}
                                                                value={values.role}
                                                                onChange={handleChange}>
                                                                <option value="E" selected>
                                                                    Editor
                                                                </option>
                                                                <option value="A">Admin</option>
                                                                <option value="S">
                                                                    SuperAdmin
                                                                </option>
                                                            </CInput>
                                                        </CFormGroup>
                                                        <CRow>
                                                            <CCol md={6}>
                                                                <CFormGroup>
                                                                    {/*<CLabel for="password">Password</CLabel>*/}
                                                                    <CInput
                                                                        type="password"
                                                                        name="password"
                                                                        id="password"
                                                                        placeholder="Password"
                                                                        valid={!errors.password}
                                                                        invalid={
                                                                            touched.password &&
                                                                            !!errors.password
                                                                        }
                                                                        required
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                        value={values.password}
                                                                    />
                                                                    {/*<CFormFeedback>Required password containing at least: number, uppercase and lowercase letter, 8 characters</CFormFeedback>*/}
                                                                    <CFormFeedback>
                                                                        {errors.password}
                                                                    </CFormFeedback>
                                                                </CFormGroup>
                                                            </CCol>
                                                            <CCol md={6}>
                                                                <CFormGroup>
                                                                    {/*<CLabel for="confirmPassword">Password</CLabel>*/}
                                                                    <CInput
                                                                        type="password"
                                                                        name="confirm_password"
                                                                        id="confirm_password"
                                                                        placeholder="Confirm password"
                                                                        valid={
                                                                            !errors.confirm_password
                                                                        }
                                                                        invalid={
                                                                            touched.confirm_password &&
                                                                            !!errors.confirm_password
                                                                        }
                                                                        required
                                                                        onChange={handleChange}
                                                                        onBlur={handleBlur}
                                                                    />
                                                                    <CFormFeedback>
                                                                        {errors.confirm_password}
                                                                    </CFormFeedback>
                                                                </CFormGroup>
                                                            </CCol>
                                                        </CRow>
                                                        <CRow>
                                                            <CCol
                                                                xs="12"
                                                                sm="6"
                                                                className="text-center">
                                                                <CButton
                                                                    type="submit"
                                                                    color="success"
                                                                    className="mr-1"
                                                                    disabled={loading || !isValid}>
                                                                    {loading ? 'Wait...' : 'Save'}
                                                                </CButton>
                                                            </CCol>
                                                            <CCol
                                                                xs="12"
                                                                sm="6"
                                                                className="text-center">
                                                                <CButton
                                                                    type="reset"
                                                                    color="danger"
                                                                    className="mr-1"
                                                                    onClick={handleReset}>
                                                                    Reset
                                                                </CButton>
                                                            </CCol>
                                                        </CRow>
                                                    </CForm>
                                                </CCol>
                                            </CRow>
                                        )}
                                    </Formik>
                                </CCardBody>
                            </CCard>
                        </CCol>
                    </CRow>
                </CContainer>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    loading: state.users.loading
});

const actionCreators = {
    createUser: userActions.createUser
};

export default connect(mapStateToProps, actionCreators)(CreateUser);
