import React, { Component } from 'react';
import { CCarousel, CCarouselItem, CCarouselControl } from '@coreui/react';

class PropertyCarousel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeIndex: 0
        };
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
        this.goToIndex = this.goToIndex.bind(this);
        this.onExiting = this.onExiting.bind(this);
        this.onExited = this.onExited.bind(this);
    }

    // UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
    //     console.log('______PropertyCarousel_________', nextProps.items)
    //     if (this.state.items !== nextProps.items){
    //         const array = []
    //         nextProps.images.map(image => {
    //             array.push({
    //                 src: image.file,
    //                 altText: image.name,
    //                 caption: image.caption
    //             })
    //         })
    //         this.setState({items: array})
    //     }
    //
    // }

    onExiting() {
        this.animating = true;
    }

    onExited() {
        this.animating = false;
    }

    next() {
        if (this.animating) return;
        const nextIndex =
            this.state.activeIndex === this.props.items.length - 1 ? 0 : this.state.activeIndex + 1;
        this.setState({ activeIndex: nextIndex });
    }

    previous() {
        if (this.animating) return;
        const nextIndex =
            this.state.activeIndex === 0 ? this.props.items.length - 1 : this.state.activeIndex - 1;
        this.setState({ activeIndex: nextIndex });
    }

    goToIndex(newIndex) {
        if (this.animating) return;
        this.setState({ activeIndex: newIndex });
    }

    render() {
        const { activeIndex } = this.state;
        const { items } = this.props;

        const slides = items.map((item, index) => {
            return (
                <CCarouselItem onExiting={this.onExiting} onExited={this.onExited} key={index}>
                    <img className="d-block w-100" src={item.file} alt={item.name} />
                </CCarouselItem>
            );
        });

        return (
            <CCarousel
                custom
                activeIndex={activeIndex}
                next={this.next}
                previous={this.previous}
                controls
                autoPlay
                ride="carousel">
                {slides}
                <CCarouselControl
                    direction="prev"
                    directionText="Previous"
                    onClickHandler={this.previous}
                />
                <CCarouselControl
                    direction="next"
                    directionText="Next"
                    onClickHandler={this.next}
                />
            </CCarousel>
        );
    }
}

export default PropertyCarousel;
