import { alertActions } from './alert.actions';
import { textConstants } from '../_constants';
import { textService } from '../_services/text.service';

export const textAction = {
    getAllTexts,
    createText,
    getText,
    updateText,
    deleteText
};

function getAllTexts() {
    return (dispatch) => {
        dispatch(request());
        textService.getAll().then(
            (items) => {
                dispatch(success(items));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: textConstants.GET_ALL_REQUEST };
    }

    function success(items) {
        return { type: textConstants.GET_ALL_SUCCESS, items };
    }

    function failure(error) {
        return { type: textConstants.GET_ALL_FAILURE, error };
    }
}

function createText(Text) {
    return (dispatch) => {
        dispatch(request());
        textService.create(Text).then(
            (res) => {
                dispatch(success(res));
                dispatch(alertActions.success('A Text has been created successfully'));
                // history.push('/admin/text')
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: textConstants.CREATE_TEXT_REQUEST };
    }

    function success(text) {
        return { type: textConstants.CREATE_TEXT_SUCCESS, text };
    }

    function failure(error) {
        return { type: textConstants.CREATE_TEXT_FAILURE, error };
    }
}

function getText(id) {
    return (dispatch) => {
        dispatch(request());
        textService.getById(id).then(
            (text) => dispatch(success(text)),
            (error) => dispatch(failure(error.toString()))
        );
    };

    function request() {
        return { type: textConstants.GET_TEXT_REQUEST };
    }

    function success(text) {
        return { type: textConstants.GET_TEXT_SUCCESS, text };
    }

    function failure(error) {
        return { type: textConstants.GET_TEXT_FAILURE, error };
    }
}

function updateText(Text) {
    return (dispatch) => {
        dispatch(request());
        textService.update(Text).then(
            (text) => {
                dispatch(success(text));
                dispatch(alertActions.success('Text has been updated successfully!'));
                getAllTexts();
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );

        function request() {
            return { type: textConstants.UPDATE_TEXT_REQUEST };
        }

        function success(text) {
            return { type: textConstants.UPDATE_TEXT_SUCCESS, text };
        }

        function failure(error) {
            return { type: textConstants.UPDATE_TEXT_FAILURE, error };
        }
    };
}

function deleteText(id) {
    return (dispatch) => {
        dispatch(request(id));
        textService.delete(id).then(
            (res) => {
                dispatch(success(id));
                dispatch(alertActions.success('A Text has been deleted successfully'));
                // history.push('/admin/texts')
            },
            (error) => dispatch(failure(id, error.toString()))
        );
    };

    function request(id) {
        return { type: textConstants.DELETE_TEXT_REQUEST, id };
    }

    function success(id) {
        return { type: textConstants.DELETE_TEXT_SUCCESS, id };
    }

    function failure(id, error) {
        return { type: textConstants.DELETE_TEXT_FAILURE, id, error };
    }
}
