import React from 'react';
import {
    CButton,
    CCol,
    CFormGroup,
    CInput,
    CLabel,
    CModal,
    CModalBody,
    CModalHeader,
    CNav,
    CNavItem,
    CNavLink,
    CRow,
    CTabContent,
    CTabPane
} from '@coreui/react';
import DropZoneUploader from './DropZoneUploader';
import SortableMediaGallery from '../SortableMediaGallery';
import CircleLoader from '../../common/Loaders/CircleLoader';
import MediaSidebar from './MediaSidebar';

class CreateGallery extends React.Component {
    state = {
        modal: false,
        activeTab: '2',
        selectedItems: [],
        activeItem: '',
        loading: false
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        // if (this.state.selectedItems !== this.props.selectedItems) {
        //     this.setState({selectedItems: this.props.selectedItems})
        // }
        if (prevState.modal !== this.state.modal && this.state.modal === true) {
            this.setState({ selectedItems: this.props.selectedItems });
        }
        if (
            this.state.modal === true &&
            prevState.activeTab !== this.state.activeTab &&
            this.state.activeTab === '2'
        ) {
            this.setState({ selectedItems: this.props.selectedItems });
        }
    }

    handleImageClick = (image) => {
        let { selectedItems } = this.state;
        let index = selectedItems.findIndex((item) => item.id === image.id);
        if (index === -1) {
            selectedItems.push(image);
        } else {
            selectedItems = selectedItems.filter((item) => item.id !== image.id);
        }
        this.setState({
            activeItem: image,
            selectedItems
        });
    };

    handleSaveSelectedItems = () => {
        this.props.onSave(this.state.selectedItems);
        this.setState({ modal: !this.state.modal });
    };

    handleUploadSucess = (item) => {
        this.props.addToImages(item);
    };

    render() {
        const { items } = this.props;
        return (
            <div>
                <CButton
                    onClick={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    color="primary"
                    className="mr-1">
                    Add gallery
                </CButton>
                <CModal
                    show={this.state.modal}
                    toggle={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    className="media-modal">
                    <CModalHeader
                        toggle={() => {
                            this.setState({ modal: !this.state.modal });
                        }}>
                        Add Gallery
                    </CModalHeader>
                    <CModalBody>
                        <CNav variant="tabs">
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '1'}
                                    onClick={() => {
                                        this.setState({ activeTab: '1' });
                                    }}>
                                    Upload Files
                                </CNavLink>
                            </CNavItem>
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '2'}
                                    onClick={() => {
                                        this.setState({ activeTab: '2' });
                                    }}>
                                    Media Library
                                </CNavLink>
                            </CNavItem>
                        </CNav>
                        <CTabContent activeTab={this.state.activeTab}>
                            <CTabPane tabId="1">
                                <DropZoneUploader onUploadSuccess={this.handleUploadSucess} />
                                <CFormGroup row hidden>
                                    <CCol md="3">
                                        <CLabel className="custom-file" htmlFor="custom-file-input">
                                            Custom file input
                                        </CLabel>
                                    </CCol>
                                    <CCol xs="12" md="9">
                                        <CLabel className="custom-file">
                                            <CInput
                                                className="custom-file"
                                                type="file"
                                                id="custom-file-input"
                                                name="file-input"
                                            />
                                        </CLabel>
                                    </CCol>
                                </CFormGroup>
                            </CTabPane>
                            <CTabPane tabId="2">
                                <CRow>
                                    <CCol sm="10">
                                        {this.state.loading === true ? <CircleLoader /> : ''}
                                        <SortableMediaGallery
                                            items={items}
                                            selectedItems={this.state.selectedItems}
                                            onClick={this.handleImageClick}
                                            onSortEnd={this.props.onSordEnd}
                                        />
                                    </CCol>
                                    <CCol sm="2">
                                        <MediaSidebar
                                            showCrop={false}
                                            image={this.state.activeItem}
                                            onSaveImageDetail={this.props.onSaveImageDetail}
                                            onDeleteImage={this.props.onDeleteImage}
                                        />
                                    </CCol>
                                </CRow>
                                <CRow className="media-modal-action">
                                    <CButton
                                        color="primary"
                                        className="mr-1 mt-3"
                                        onClick={this.handleSaveSelectedItems}>
                                        Add gallery
                                    </CButton>
                                </CRow>
                            </CTabPane>
                        </CTabContent>
                    </CModalBody>
                </CModal>
            </div>
        );
    }
}

export default CreateGallery;
