import React from 'react';
import {
    CButton,
    CCol,
    CFormGroup,
    CInput,
    CLabel,
    CModal,
    CModalBody,
    CModalHeader,
    CNav,
    CNavItem,
    CNavLink,
    CRow,
    CTabContent,
    CTabPane
} from '@coreui/react';
import DropZoneUploader from './DropZoneUploader';
import SortableMediaGallery from '../SortableMediaGallery';
import CircleLoader from '../../common/Loaders/CircleLoader';
import MediaSidebar from './MediaSidebar';
import arrayMove from 'array-move';
import { mediaAction } from '../../../_actions';
import { connect } from 'react-redux';

class EditGallery extends React.Component {
    state = {
        modal: false,
        activeTab: '2',
        propertyId: '',
        items: [],
        selectedItems: [],
        activeItem: '',
        loading: false
    };

    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.property) {
            this.setState({
                propertyId: nextProps.property.id,
                selectedItems: nextProps.property.gallery_imgs,
                items: nextProps.images
            });
        }
        // if (this.state.propertyId !== nextProps.property.id) {
        //     this.setState({ propertyId: nextProps.property.id })
        // }
        // if (this.state.selectedItems !== nextProps.property.gallery_imgs) {
        //     this.setState({ selectedItems: nextProps.property.gallery_imgs })
        // }
        // if (this.state.items !== nextProps.images) {
        //     this.setState({ items: nextProps.images })
        // }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.modal !== this.state.modal && this.state.modal === true) {
            this.props.getAllMedia(this.state.propertyId);
        }
        if (
            this.state.modal === true &&
            prevState.activeTab !== this.state.activeTab &&
            this.state.activeTab === '2'
        ) {
            this.props.getAllMedia(this.state.propertyId);
        }
        // update list after creating cropped image
        if (this.state.modal === true && prevProps.activeImage !== this.props.activeImage) {
            this.props.getAllMedia(this.state.propertyId);
        }
    }

    handleImageClick = (image) => {
        let { selectedItems } = this.state;
        let index = selectedItems.findIndex((item) => item.id === image.id);
        if (index === -1) {
            selectedItems.push(image);
        } else {
            selectedItems = selectedItems.filter((item) => item.id !== image.id);
        }
        this.setState({
            activeItem: image,
            selectedItems
        });
    };

    handleSortEnd = ({ oldIndex, newIndex }) => {
        this.setState({
            items: arrayMove(this.state.items, oldIndex, newIndex)
        });
    };

    handleSaveSelectedItems = () => {
        this.setState({ modal: !this.state.modal });
        this.props.updateBulkMedia(this.state.items);
        let galleryImgs = this.state.items.filter((item) =>
            this.state.selectedItems.find((obj) => obj.id === item.id)
        );
        this.props.onSave(galleryImgs);
    };

    handleChangeImageDetail = (e) => {
        const { activeItem } = this.state;
        activeItem[e.target.name] = e.target.value;
        this.setState({ activeItem: activeItem });
    };

    handleSaveImageDetail = (image) => {
        const { items } = this.state;
        items.find((item) => {
            if (item.id === image.id) {
                item.title = image.title;
            }
        });
        this.setState({ items: items });
    };

    handleDeleteImage = (id) => {
        this.setState({ activeItem: '' });
        const { items } = this.state;
        this.setState({ items: items.filter((item) => item.id !== id) });
    };

    render() {
        return (
            <div>
                <CButton
                    onClick={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    color="primary"
                    className="mr-1">
                    Edit Gallery
                </CButton>
                <CModal
                    show={this.state.modal}
                    toggle={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    className="media-modal">
                    <CModalHeader
                        toggle={() => {
                            this.setState({ modal: !this.state.modal });
                        }}>
                        Gallery
                    </CModalHeader>
                    <CModalBody>
                        <CNav variant="tabs">
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '1'}
                                    onClick={() => {
                                        this.setState({ activeTab: '1' });
                                    }}>
                                    Upload Files
                                </CNavLink>
                            </CNavItem>
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '2'}
                                    onClick={() => {
                                        this.setState({ activeTab: '2' });
                                    }}>
                                    Media Library
                                </CNavLink>
                            </CNavItem>
                        </CNav>
                        <CTabContent activeTab={this.state.activeTab}>
                            <CTabPane tabId="1">
                                <DropZoneUploader propertyId={this.state.propertyId} />
                                <CFormGroup row hidden>
                                    <CCol md="3">
                                        <CLabel className="custom-file" htmlFor="custom-file-input">
                                            Custom file input
                                        </CLabel>
                                    </CCol>
                                    <CCol xs="12" md="9">
                                        <CLabel className="custom-file">
                                            <CInput
                                                className="custom-file"
                                                type="file"
                                                id="custom-file-input"
                                                name="file-input"
                                            />
                                        </CLabel>
                                    </CCol>
                                </CFormGroup>
                            </CTabPane>
                            <CTabPane tabId="2">
                                <CRow>
                                    <CCol sm="10">
                                        {this.state.items && this.state.selectedItems ? (
                                            <SortableMediaGallery
                                                items={this.state.items}
                                                selectedItems={this.state.selectedItems}
                                                onClick={this.handleImageClick}
                                                onSortEnd={this.handleSortEnd}
                                            />
                                        ) : (
                                            <CircleLoader />
                                        )}
                                    </CCol>
                                    <CCol sm="2">
                                        <MediaSidebar
                                            showCrop={true}
                                            image={this.state.activeItem}
                                            onChangeImageDetail={this.handleChangeImageDetail}
                                            onSaveImageDetail={this.handleSaveImageDetail}
                                            onDeleteImage={this.handleDeleteImage}
                                        />
                                    </CCol>
                                </CRow>
                                <CRow className="media-modal-action">
                                    <CButton
                                        color="primary"
                                        className="mr-1 mt-3"
                                        onClick={this.handleSaveSelectedItems}>
                                        Save gallery
                                    </CButton>
                                </CRow>
                            </CTabPane>
                        </CTabContent>
                    </CModalBody>
                </CModal>
            </div>
        );
    }
}

function mapState(state) {
    const { properties, media } = state;
    return {
        property: properties.activeItem,
        images: media.items,
        activeImage: media.activeItem
    };
}

const actionCreators = {
    getAllMedia: mediaAction.getAllMedia,
    updateBulkMedia: mediaAction.updateBulkMedia
};

export default connect(mapState, actionCreators)(EditGallery);
