import React, { Component } from 'react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CForm,
    CFormFeedback,
    CFormGroup,
    CInput,
    CLabel,
    CRow
} from '@coreui/react';

import { Formik } from 'formik';
import * as Yup from 'yup';
import { categoryAction as categoryActions } from '../../../_actions/category.actions';
import { connect } from 'react-redux';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';

class CategoryEdit extends Component {
    componentDidMount() {
        this.props.getCategory(this.props.match.params.id);
    }

    handleSubmit = (fields, { setSubmitting, setErrors }) => {
        this.props.updateCategory(fields, this.props.history);
        this.props.getCategory(this.props.match.params.id);
        setSubmitting(false);
    };

    render() {
        const { loading, category } = this.props;
        return (
            <div className="animated fadeIn">
                <CRow>
                    <CCol lg={6} sm={12} className="mx-auto">
                        <CCard custom>
                            <CCardHeader>
                                <h4>Edit category</h4>
                            </CCardHeader>
                            <CCardBody>
                                {category && category.id ? (
                                    <Formik
                                        enableReinitialize
                                        initialValues={category}
                                        validate={{
                                            name: Yup.string()
                                                .min(2, `name has to be at least 2 characters`)
                                                .required('name is required')
                                        }}
                                        onSubmit={this.handleSubmit}>
                                        {({
                                            values,
                                            errors,
                                            touched,
                                            status,
                                            dirty,
                                            handleChange,
                                            handleBlur,
                                            handleSubmit,
                                            isSubmitting,
                                            isValid,
                                            handleReset
                                        }) => (
                                            <CForm
                                                onSubmit={handleSubmit}
                                                noValidate
                                                name="categoryForm">
                                                <CFormGroup>
                                                    <CLabel for="name">Category Name</CLabel>
                                                    <CInput
                                                        type="text"
                                                        name="name"
                                                        id="name"
                                                        valid={!errors.name}
                                                        invalid={touched.name && !!errors.name}
                                                        autoFocus={true}
                                                        required
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.name}
                                                    />
                                                    <CFormFeedback>{errors.name}</CFormFeedback>
                                                </CFormGroup>
                                                <CRow>
                                                    <CCol xs="12" sm="6" className="text-center">
                                                        <CButton
                                                            type="submit"
                                                            color="success"
                                                            className="mr-1"
                                                            disabled={isSubmitting || !isValid}>
                                                            {loading ? 'Saving' : 'Save'}
                                                        </CButton>
                                                    </CCol>
                                                    <CCol xs="12" sm="6" className="text-center">
                                                        <CButton
                                                            type="reset"
                                                            color="danger"
                                                            className="mr-1"
                                                            onClick={handleReset}>
                                                            Reset
                                                        </CButton>
                                                    </CCol>
                                                </CRow>
                                            </CForm>
                                        )}
                                    </Formik>
                                ) : (
                                    <CircleLoader />
                                )}
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    loading: state.categories.loading,
    category: state.categories.activeItem
});

const actionCreators = {
    getCategory: categoryActions.getCategory,
    updateCategory: categoryActions.updateCategory
};

export default connect(mapStateToProps, actionCreators)(CategoryEdit);
