import config from '../_config/config';
import { authHeader } from '../_helpers';
import { userService } from './user.service';

const apiUrl = `${config.apiEndpoint}/accommodation/property`;

export const propertyService = {
    getListing,
    getFilteredListing,
    getLocationListing,
    getDetail,
    getList,
    getLatest,
    getById,
    create,
    update,
    delete: _delete,
    getPriceItems,
    createPriceItem,
    updatePriceItem,
    deletePriceItem
};

function getListing() {
    const requestOptions = {
        method: 'GET'
        // headers: authHeader()
    };
    return fetch(`${apiUrl}/listing`, requestOptions).then(handleResponse);
}

function getFilteredListing(terms) {
    console.log('+++++++terms+++++', terms);
    let queryString = '';
    for (let key in terms) {
        if (queryString !== '') {
            queryString += '&';
        }
        queryString += key + '=' + encodeURIComponent(terms[key]);
    }
    const requestOptions = {
        method: 'GET'
        // headers: authHeader()
    };
    return fetch(`${apiUrl}/listing?${queryString}`, requestOptions).then(handleResponse);
}

function getLocationListing() {
    const requestOptions = {
        method: 'GET'
        // headers: authHeader()
    };
    return fetch(`${apiUrl}/locations`, requestOptions).then(handleResponse);
}

function getDetail(slug) {
    const requestOptions = {
        method: 'GET',
        // headers: authHeader()
    };

    return fetch(`${apiUrl}/listing/${slug}`, requestOptions).then(handleResponse);
}

function getList() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`${apiUrl}/`, requestOptions).then(handleResponse);
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${apiUrl}/${id}`, requestOptions).then(handleResponse);
}

function getLatest() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${apiUrl}/latest`, requestOptions).then(handleResponse);
}

function create(property) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(property)
    };

    return fetch(`${apiUrl}/create`, requestOptions).then(handleResponse);
}

function update(property) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(property)
    };

    return fetch(`${apiUrl}/${property.id}`, requestOptions).then(handleResponse);
}

function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${apiUrl}/${id}`, requestOptions).then(handleResponse);
}

function getPriceItems(propertyId) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(
        `${config.apiEndpoint}/accommodation/pricing/filtered_list/?property_id=${propertyId}`,
        requestOptions
    ).then(handleResponse);
}

function createPriceItem(priceItem) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(priceItem)
    };
    return fetch(`${config.apiEndpoint}/accommodation/pricing/`, requestOptions).then(
        handleResponse
    );
}

function updatePriceItem(priceItem) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(priceItem)
    };
    return fetch(
        `${config.apiEndpoint}/accommodation/pricing/${priceItem.id}`,
        requestOptions
    ).then(handleResponse);
}

function deletePriceItem(priceItemId) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };
    return fetch(`${config.apiEndpoint}/accommodation/pricing/${priceItemId}`, requestOptions).then(
        handleResponse
    );
}

function handleResponse(response) {
    return response.text().then((text) => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            console.log('handleResponse', response, data);

            if (response.status === 401) {
                userService.logout();
                window.location.reload(true);
            }

            const error = getObjectValues(data) || response.statusText;
            return Promise.reject(error);
        }
        return data;
    });
}

function getObjectValues(obj) {
    return obj && typeof obj === 'object' ? Object.values(obj).map(getObjectValues).flat() : [obj];
}
