import React from 'react';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';

export default class SortableMediaGallery extends React.Component {
    render() {
        const { items, selectedItems, onSortEnd, onClick } = this.props;

        const SortableItem = SortableElement(({ item }) => {
            let active = !!selectedItems.find((obj) => obj.id === item.id);
            return (
                <div className="gallery-item" onClick={() => onClick(item)}>
                    <div>
                        <div
                            style={{
                                backgroundImage: `url(${item.file})`,
                                backgroundPosition: 'center',
                                backgroundSize: 'cover',
                                height: '100px'
                            }}
                            className={active === true ? 'item-active' : ''}></div>
                    </div>
                </div>
            );
        });

        const SortableList = SortableContainer(({ items }) => (
            <div className="row media-scroll-gallery">
                {items.map((item, index) => {
                    return (
                        <SortableItem
                            key={`${item.id}`}
                            index={index}
                            item={item}
                            // disabled
                        />
                    );
                })}
            </div>
        ));

        return (
            <SortableList
                items={items}
                onSortEnd={onSortEnd}
                axis="xy"
                distance={3}
                helperClass="SortableHelper"
            />
        );
    }
}
