import { userConstants } from '../_constants';
import jwt_decode from 'jwt-decode';

let decoded_token = {};
let user = {};
let initialState = {};

if (localStorage.getItem('access_token') !== null) {
    decoded_token = jwt_decode(localStorage.getItem('access_token'));
    user = {
        username: decoded_token.username,
        role: decoded_token.role,
        id: decoded_token.user_id
    };
    initialState = {
        loggedIn: true,
        user
    };
} else {
    initialState = {
        loggedIn: false,
        user: null
    };
}

export function authentication(state = initialState, action) {
    switch (action.type) {
        case userConstants.LOGIN_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userConstants.LOGIN_SUCCESS:
            return {
                ...state,
                loggedIn: true,
                loading: false,
                user: action.user
            };
        case userConstants.LOGIN_FAILURE:
            return {
                ...state,
                loading: false
            };
        case userConstants.PASSWORD_RESET_TOKEN_REQUEST:
            return {
                ...state,
                loading: true
            };
        case userConstants.PASSWORD_RESET_TOKEN_SUCCESS:
            return {
                ...state,
                loading: false
            };
        case userConstants.PASSWORD_RESET_TOKEN_FAILURE:
            return {
                ...state,
                loading: false
            };
        case userConstants.LOGOUT:
            return {};
        default:
            return state;
    }
}
