import React from 'react';
import {
    EmailShareButton,
    FacebookShareButton,
    RedditShareButton,
    TelegramShareButton,
    TwitterShareButton,
    WhatsappShareButton,
    EmailIcon,
    FacebookIcon,
    TwitterIcon,
    WhatsappIcon,
    RedditIcon,
    LinkedinIcon,
    TelegramIcon,
    LinkedinShareButton
} from 'react-share';

export default function SocialShareButtons(props) {
    return (
        <div>
            <EmailShareButton subject={`Check out this page`} body={`${props.text}: ${props.url}`}>
                <EmailIcon round={true} size="2rem" />
            </EmailShareButton>
            <FacebookShareButton url={props.url} quote={props.text}>
                <FacebookIcon round={true} size="2rem" />
            </FacebookShareButton>
            <TwitterShareButton url={props.url} title={props.text}>
                <TwitterIcon round={true} size="2rem" />
            </TwitterShareButton>
            <LinkedinShareButton url={props.url} title={props.text}>
                <LinkedinIcon round={true} size="2rem" />
            </LinkedinShareButton>
            <RedditShareButton url={props.url} title={props.text}>
                <RedditIcon round={true} size="2rem" />
            </RedditShareButton>
            <TelegramShareButton url={props.url} title={props.text}>
                <TelegramIcon round={true} size="2rem" />
            </TelegramShareButton>
            <WhatsappShareButton url={props.url} title={props.text}>
                <WhatsappIcon round={true} size="2rem" />
            </WhatsappShareButton>
        </div>
    );
}
