import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
    CBadge,
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CRow,
    CTable,
    CModal,
    CModalBody,
    CModalFooter,
    CLink
} from '@coreui/react';

import { CIcon } from '@coreui/icons-react';
import { userActions } from '../../../_actions/index';
import { connect } from 'react-redux';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';

function UserRow(props) {
    const { user, index } = props;
    const userLink = `/admin/users/${user.id}`;

    const getBadge = (status) => {
        return status === 'active'
            ? 'success'
            : status === 'pending'
            ? 'info'
            : status === 'blocked'
            ? 'danger'
            : 'primary';
    };

    return (
        <tr key={user.id.toString()}>
            <td scope="row">
                <Link to={userLink}>{index}</Link>
            </td>
            <td>{user.username}</td>
            <td>{user.first_name}</td>
            <td>{user.last_name}</td>
            <td>{user.email}</td>
            <td scope="col">{user.role}</td>
            <td>
                <Link to={userLink}>
                    <CBadge color={getBadge(user.status)}>{user.status}</CBadge>
                </Link>
            </td>
            <td>
                <CRow>
                    <CCol xl={6} className="text-center">
                        <Link to={userLink}>
                            <CButton color="dark" className="m-2">
                                Edit
                            </CButton>
                        </Link>
                    </CCol>
                    <CCol xl={6} className="text-center">
                        <CButton
                            color="danger"
                            className="m-2"
                            onClick={() => props.requestDeleteUser(user.id)}>
                            {user.deleting ? 'Deleting...' : 'Delete'}
                        </CButton>
                    </CCol>
                </CRow>
            </td>
        </tr>
    );
}

class Users extends Component {
    state = {
        selectedUserId: '',
        showModal: false
    };

    componentDidMount() {
        this.props.getUsers();
    }

    requestDeleteUser = (id) => {
        this.toggleModal();
        this.setState({ selectedUserId: id });
    };

    confirmDelete = () => {
        this.toggleModal();
        this.props.deleteUser(this.state.selectedUserId);
    };

    toggleModal = () => {
        this.setState((prevState) => ({
            showModal: !prevState.showModal
        }));
    };

    handleSelectChange = (e) => {
        console.log(e.target.value);
    };

    render() {
        const { users } = this.props;
        return (
            <div className="animated fadeIn">
                <CModal show={this.state.showModal} onClose={this.toggleModal}>
                    <CModalBody>Are you sure?</CModalBody>
                    <CModalFooter>
                        <CButton color="primary" onClick={this.confirmDelete}>
                            Delete
                        </CButton>
                        <CButton color="secondary" onClick={this.toggleModal}>
                            Cancel
                        </CButton>
                    </CModalFooter>
                </CModal>
                <CRow>
                    <CCol xl={12}>
                        <CCard custom>
                            <CCardHeader>
                                <h5>Users</h5>
                            </CCardHeader>
                            <CCardBody>
                                <CLink to="/admin/users/new">
                                    <CButton
                                        color="primary"
                                        style={{ float: 'right', margin: '0 30px 10px' }}>
                                        <CIcon
                                            name="cil-plus"
                                            className="c-d-dark-none"
                                            alt="add"
                                        />
                                        Add new
                                    </CButton>
                                </CLink>
                                <CTable custom responsive hover>
                                    <thead>
                                        <tr>
                                            <th scope="col">ID</th>
                                            <th scope="col">Username</th>
                                            <th scope="col">First Name</th>
                                            <th scope="col">Last Name</th>
                                            <th scope="col">Email</th>
                                            <th scope="col">Role</th>
                                            <th scope="col">Status</th>
                                            <th
                                                scope="col"
                                                style={{ width: '15%', textAlign: 'center' }}>
                                                Action
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {users.items &&
                                            users.items.map((user, index) => (
                                                <UserRow
                                                    key={index}
                                                    index={index + 1}
                                                    user={user}
                                                    handleSelectChange={this.handleSelectChange}
                                                    requestDeleteUser={(id) =>
                                                        this.requestDeleteUser(id)
                                                    }
                                                />
                                            ))}
                                    </tbody>
                                </CTable>
                                {users.loading && <CircleLoader />}
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

function mapState(state) {
    const { users } = state;
    return { users };
}

const actionCreators = {
    getUsers: userActions.getAll,
    deleteUser: userActions.deleteUser
};

export default connect(mapState, actionCreators)(Users);
