import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import DiningImage from '../../../assets/dining.jpg';
import EditableBlock from '../../common/EditableBlock';
import ScrollableAnchor from 'react-scrollable-anchor';

export default class CorporateHousing extends Component {
    render() {
        return (
            <ScrollableAnchor id={'corporate'}>
                <section className="corporate-housing-block">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm-12 col-lg-5">
                                <EditableBlock id={4} lg />
                                <Link to="/property">
                                    <button
                                        type="button"
                                        className="btn user-btn btn-outline-primary user-btn-outline mb-4">
                                        Read More
                                    </button>
                                </Link>
                            </div>
                            <div className="col-sm-12 col-lg-7">
                                <LazyLoadImage src={DiningImage} alt="corporate" />
                            </div>
                        </div>
                    </div>
                </section>
            </ScrollableAnchor>
        );
    }
}
