import React from 'react';
import moment from 'moment';

// redux
import { alertActions } from '../../../_actions';
import { connect } from 'react-redux';

import HeaderBanner from '../../../containers/GuestLayout/HeaderBanner';
import MainSearchForm from '../../../components/guest/MainSearchForm';
import RentalSolutions from '../../../components/guest/apartments/RentalSolutions';
import MedicalTravel from '../../../components/guest/apartments/MedicalTravel';
import CorporateHousing from '../../../components/guest/apartments/CorporateHousing';
import StarsExperience from '../../../components/guest/apartments/StarsExperience';
import StayBoston from '../../../components/guest/apartments/StayBoston';
import ExploreLocations from '../../../components/guest/apartments/ExploreLocations';
import Subscription from '../../../components/guest/apartments/Subscription';
import GuestReviews from '../../../components/guest/apartments/GuestReviews';
import LookRentals from '../../../components/guest/apartments/LookRentals';
import Insurance from '../../../components/guest/apartments/Insurance';
import withGATracker from '../../../components/WithGATracker';
import WithPixel from '../../../components/WithPixel';
import ReactGA from 'react-ga';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            properties: [],
            solutions: [],
            checkinDate: '',
            checkoutDate: '',
            adultsCount: '',
            childrenCount: ''
        };
    }

    handleCheckInChange = (value, formattedValue) => {
        this.setState({
            checkinDate: value // ISO String, ex: "2016-11-19T12:00:00.000Z"
        });
        if (this.state.checkinDate < this.state.checkoutDate) {
            this.setState({ checkoutDate: '' });
        }
    };

    handleCheckOutChange = (value, formattedValue) => {
        this.setState({
            checkoutDate: value
        });
    };

    handleAdultsCountChange = (e) => {
        const reg = /^[0-9\b]+$/;
        if (e.target.value === '' || reg.test(e.target.value)) {
            this.setState({ adultsCount: e.target.value });
        }
    };

    handleChildrenCountChange = (e) => {
        const reg = /^[0-9\b]+$/;
        if (e.target.value === '' || reg.test(e.target.value)) {
            this.setState({ childrenCount: e.target.value });
        }
    };

    handleBrowseButtonClick = (e) => {
        this.props.history.push('/property');
    };

    handleSearchButtonClick = (e) => {
        if (this.state.checkinDate === '' || this.state.checkoutDate === '') {
            this.props.alertError('Please choose date ');
            return;
        }
        if (this.state.adultsCount === '') {
            this.props.alertError('Please insert number of people ');
            return;
        }
        const formattedcheckinDate = moment(this.state.checkinDate).format('YYYY-MM-DD');
        const formattedcheckoutDate = moment(this.state.checkoutDate).format('YYYY-MM-DD');
        let queryString = `checkin_date=${formattedcheckinDate}&checkout_date=${formattedcheckoutDate}&adults=${this.state.adultsCount}&children=${this.state.childrenCount}`;
        ReactGA.event({
            category: 'engagement',
            action: 'search',
            label: 'search rentals'
        });
        ReactGA.event({
            category: 'engagement',
            action: 'view_search_results',
            label: 'view rentals search results'
        });
        this.props.history.push(`/property?${queryString}`);
    };

    render() {
        return (
            <>
                <HeaderBanner className="main-background" />
                <section className="home-search-form-container">
                    <div className="container">
                        <MainSearchForm
                            checkinDate={this.state.checkinDate}
                            checkoutDate={this.state.checkoutDate}
                            adultsCount={this.state.adultsCount}
                            childrenCount={this.state.childrenCount}
                            handleCheckInChange={this.handleCheckInChange}
                            handleCheckOutChange={this.handleCheckOutChange}
                            handleAdultsCountChange={this.handleAdultsCountChange}
                            handleChildrenCountChange={this.handleChildrenCountChange}
                            handleBrowseButtonClick={this.handleBrowseButtonClick}
                            handleSearchButtonClick={this.handleSearchButtonClick}
                        />
                    </div>
                </section>
                <RentalSolutions />
                <MedicalTravel />
                <Insurance />
                <CorporateHousing />
                <StarsExperience />
                <StayBoston />
                <ExploreLocations />
                <LookRentals />
                <GuestReviews />
                <Subscription />
            </>
        );
    }
}

const actionCreators = {
    alertError: alertActions.error
};

export default connect(null, actionCreators)(WithPixel(withGATracker(Home)));
