import { alertActions } from './alert.actions';
import { amenityConstants } from '../_constants';
import { amenityService } from '../_services/amenity.service';

export const amenityAction = {
    getAllAmenity,
    getAmenityListing,
    createAmenity,
    getAmenity,
    updateAmenity,
    deleteAmenity
};

function getAllAmenity() {
    return (dispatch) => {
        dispatch(request());
        amenityService.getAll().then(
            (items) => {
                dispatch(success(items));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: amenityConstants.GET_ALL_REQUEST };
    }

    function success(items) {
        return { type: amenityConstants.GET_ALL_SUCCESS, items };
    }

    function failure(error) {
        return { type: amenityConstants.GET_ALL_FAILURE, error };
    }
}

function getAmenityListing() {
    return (dispatch) => {
        dispatch(request());
        amenityService.getListing().then(
            (items) => {
                dispatch(success(items));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: amenityConstants.GET_ALL_REQUEST };
    }

    function success(items) {
        return { type: amenityConstants.GET_ALL_SUCCESS, items };
    }

    function failure(error) {
        return { type: amenityConstants.GET_ALL_FAILURE, error };
    }
}

function createAmenity(amenity, history) {
    return (dispatch) => {
        dispatch(request());
        amenityService.create(amenity).then(
            (res) => {
                dispatch(success(res));
                dispatch(alertActions.success('Amenity has been created successfully'));
                history.push('/admin/amenities');
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: amenityConstants.CREATE_AMENITY_REQUEST };
    }

    function success(amenity) {
        return { type: amenityConstants.CREATE_AMENITY_SUCCESS, amenity };
    }

    function failure(error) {
        return { type: amenityConstants.CREATE_AMENITY_FAILURE, error };
    }
}

function getAmenity(id) {
    return (dispatch) => {
        dispatch(request());
        amenityService.getById(id).then(
            (amenity) => dispatch(success(amenity)),
            (error) => dispatch(failure(error.toString()))
        );
    };

    function request() {
        return { type: amenityConstants.GET_AMENITY_REQUEST };
    }

    function success(amenity) {
        return { type: amenityConstants.GET_AMENITY_SUCCESS, amenity };
    }

    function failure(error) {
        return { type: amenityConstants.GET_AMENITY_FAILURE, error };
    }
}

function updateAmenity(amenity, history) {
    return (dispatch) => {
        dispatch(request());
        amenityService.update(amenity).then(
            (amenity) => {
                dispatch(success(amenity));
                dispatch(alertActions.success('Amenity has been updated successfully!'));
                history.push('/admin/amenities');
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );

        function request() {
            return { type: amenityConstants.UPDATE_AMENITY_REQUEST };
        }

        function success(amenity) {
            return { type: amenityConstants.UPDATE_AMENITY_SUCCESS, amenity };
        }

        function failure(error) {
            return { type: amenityConstants.UPDATE_AMENITY_FAILURE, error };
        }
    };
}

function deleteAmenity(id) {
    return (dispatch) => {
        dispatch(request(id));
        amenityService.delete(id).then(
            (res) => {
                dispatch(success(id));
                dispatch(alertActions.success('Amenity has been deleted successfully'));
                // history.push('/admin/categories')
            },
            (error) => dispatch(failure(id, error.toString()))
        );
    };

    function request(id) {
        return { type: amenityConstants.DELETE_AMENITY_REQUEST, id };
    }

    function success(id) {
        return { type: amenityConstants.DELETE_AMENITY_SUCCESS, id };
    }

    function failure(id, error) {
        return { type: amenityConstants.DELETE_AMENITY_FAILURE, id, error };
    }
}
