import { userConstants } from '../_constants';
import { userService } from '../_services';
import { alertActions } from './';

export const userActions = {
    login,
    logout,
    getPasswordResetToken,
    confirmPasswordChange,
    register,
    getAll,
    createUser,
    getUser,
    updateUser,
    deleteUser,
    sendEmailRequest,
    sendEmailSuccess,
    sendEmailFailure
};

function login(username, password, history) {
    return (dispatch) => {
        dispatch(request());
        userService.login(username, password).then(
            (user) => {
                dispatch(success(user));
                history.push('/admin');
                dispatch(alertActions.success('Successfully logged in!'));
            },
            (error) => {
                dispatch(failure(error));
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };

    function request() {
        return { type: userConstants.LOGIN_REQUEST };
    }

    function success(user) {
        return { type: userConstants.LOGIN_SUCCESS, user };
    }

    function failure(error) {
        return { type: userConstants.LOGIN_FAILURE, error };
    }
}

function getPasswordResetToken(email, history) {
    return (dispatch) => {
        userService.getPasswordResetToken(email).then(
            (data) => {
                console.log(data);
                history.push('/admin/reset_password_done');
            },
            (error) => {
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };
}

function confirmPasswordChange(formValues, props) {
    const { uid, token } = props.match.params;
    const { history } = props;
    const data = {
        ...formValues,
        uid,
        token
    };

    console.log('==========data=======', data);

    return (dispatch) => {
        userService.confirmPasswordChange(data).then(
            (data) => {
                console.log(data);
                history.push('/admin/login');
                dispatch(
                    alertActions.success('Password has been reset successfully, please log in')
                );
            },
            (error) => {
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };
}

function sendEmailRequest() {
    return { type: userConstants.SEND_EMAIL_REQUEST };
}

function sendEmailSuccess() {
    return { type: userConstants.SEND_EMAIL_SUCCESS };
}

function sendEmailFailure() {
    return { type: userConstants.SEND_EMAIL_FAILURE };
}

function logout() {
    userService.logout();
    // this.props.history.push('/admin/login');
    window.location.reload(true);
    return { type: userConstants.LOGOUT };
}

function register(user) {
    return (dispatch) => {
        dispatch(request(user));

        userService.create(user).then(
            (user) => {
                dispatch(success(user));
                // this.props.history.push('/admin/login');
                // history.push('/admin/login');
                dispatch(alertActions.success('Registration successful'));
            },
            (error) => {
                dispatch(failure(error));
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };

    function request(user) {
        return { type: userConstants.REGISTER_REQUEST, user };
    }

    function success(user) {
        return { type: userConstants.REGISTER_SUCCESS, user };
    }

    function failure(error) {
        return { type: userConstants.REGISTER_FAILURE, error };
    }
}

function getAll() {
    return (dispatch) => {
        dispatch(request());
        userService.getAll().then(
            (users) => {
                dispatch(success(users));
            },
            (error) => {
                dispatch(failure(error));
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };

    function request() {
        return { type: userConstants.GETALL_REQUEST };
    }

    function success(users) {
        return { type: userConstants.GETALL_SUCCESS, users };
    }

    function failure(error) {
        return { type: userConstants.GETALL_FAILURE, error };
    }
}

function createUser(user) {
    return (dispatch) => {
        dispatch(request());
        userService.create(user).then(
            (res) => {
                dispatch(success(res));
                dispatch(alertActions.success('A user has been created successfully'));
                // this.props.history.push('/admin/users')
            },
            (error) => {
                console.log(JSON.stringify(error));
                console.log('+++++++error+', error, Object.values(error).toString());
                dispatch(failure(error));
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };

    function request() {
        return { type: userConstants.CREATE_USER_REQUEST };
    }

    function success(user) {
        return { type: userConstants.CREATE_USER_SUCCESS, user };
    }

    function failure(error) {
        return { type: userConstants.CREATE_USER_FAILURE, error };
    }
}

function getUser(id) {
    return (dispatch) => {
        dispatch(request());
        userService.getById(id).then(
            (user) => dispatch(success(user)),
            (error) => {
                dispatch(failure(error));
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };

    function request() {
        return { type: userConstants.GET_USER_REQUEST };
    }

    function success(user) {
        return { type: userConstants.GET_USER_SUCCESS, user };
    }

    function failure(error) {
        return { type: userConstants.GET_USER_FAILURE, error };
    }
}

function updateUser(user) {
    return (dispatch) => {
        dispatch(request());
        // setTimeout(() => {
        //   dispatch(success(user))
        // }, 5000)
        userService.update(user).then(
            (user) => {
                dispatch(success(user));
                dispatch(alertActions.success('A user has been updated successfully!'));
            },
            (error) => {
                dispatch(failure(error));
                dispatch(alertActions.error(error.toString()));
            }
        );

        function request() {
            return { type: userConstants.UPDATE_USER_REQUEST };
        }

        function success(user) {
            return { type: userConstants.UPDATE_USER_SUCCESS, user };
        }

        function failure(error) {
            return { type: userConstants.UPDATE_USER_FAILURE, error };
        }
    };
}

function deleteUser(id) {
    return (dispatch) => {
        dispatch(request(id));
        userService.delete(id).then(
            (user) => {
                dispatch(success(id));
                dispatch(alertActions.success('A user has been deleted successfully'));
            },
            (error) => {
                dispatch(failure(id));
                dispatch(alertActions.error(getErrorMessage(error)));
            }
        );
    };

    function request(id) {
        return { type: userConstants.DELETE_USER_REQUEST, id };
    }

    function success(id) {
        return { type: userConstants.DELETE_USER_SUCCESS, id };
    }

    function failure(id) {
        return { type: userConstants.DELETE_USER_FAILURE, id };
    }
}

function getErrorMessage(error) {
    let msg = '';
    for (let [key, value] of Object.entries(error)) {
        console.log(`${key}: ${value}`);
        msg = msg + value + '\n';
    }
    msg = msg.substring(0, msg.length - 1);
    return msg;
}
