import React from 'react';
import {
    CButton,
    CCol,
    CFormGroup,
    CInput,
    CLabel,
    CModal,
    CModalBody,
    CModalHeader,
    CNav,
    CNavItem,
    CNavLink,
    CRow,
    CTabContent,
    CTabPane
} from '@coreui/react';
import DropZoneUploader from './DropZoneUploader';
import MediaSidebar from './MediaSidebar';
import { connect } from 'react-redux';

class CreateFeaturedImage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
            activeTab: '2',
            selectedItem: '',
            activeItem: '',
            loading: false
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.modal !== this.state.modal && this.state.modal === true) {
            this.setState({ selectedItem: this.props.selectedItem });
        }
        if (
            this.state.modal === true &&
            prevState.activeTab !== this.state.activeTab &&
            this.state.activeTab === '2'
        ) {
            this.setState({ selectedItem: this.props.selectedItem });
        }
        // update list after creating cropped image
        if (prevProps.activeImage !== this.props.activeImage) {
            const image = this.props.activeItem;
            this.props.addToImages(image);
        }
    }

    handleImageClick = (image) => {
        this.setState({
            activeItem: image,
            selectedItem: image
        });
    };

    handleSaveSelectedItem = () => {
        this.setState({ modal: !this.state.modal });
        this.props.onSave(this.state.activeItem);
    };

    handleUploadSucess = (item) => {
        this.props.addToImages(item);
    };

    // handleSaveImageDetail = (image) => {
    //     this.props.handleSaveImageDetail(image)
    // }
    //
    // handleDeleteImage = (id) => {
    //     this.handleDeleteImage(id)
    // }

    render() {
        const { items } = this.props;
        const { selectedItem } = this.state;
        return (
            <div>
                <CButton
                    onClick={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    color="primary"
                    className="mr-1">
                    Add Featured Image
                </CButton>
                <CModal
                    show={this.state.modal}
                    toggle={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    className="media-modal">
                    <CModalHeader
                        toggle={() => {
                            this.setState({ modal: !this.state.modal });
                        }}>
                        Add Featured Image
                    </CModalHeader>
                    <CModalBody>
                        <CNav variant="tabs">
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '1'}
                                    onClick={() => {
                                        this.setState({ activeTab: '1' });
                                    }}>
                                    Upload Files
                                </CNavLink>
                            </CNavItem>
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '2'}
                                    onClick={() => {
                                        this.setState({ activeTab: '2' });
                                    }}>
                                    Media Library
                                </CNavLink>
                            </CNavItem>
                        </CNav>
                        <CTabContent activeTab={this.state.activeTab}>
                            <CTabPane tabId="1">
                                <DropZoneUploader onUploadSuccess={this.handleUploadSucess} />
                                <CFormGroup row hidden>
                                    <CCol md="3">
                                        <CLabel className="custom-file" htmlFor="custom-file-input">
                                            Custom file input
                                        </CLabel>
                                    </CCol>
                                    <CCol xs="12" md="9">
                                        <CLabel className="custom-file">
                                            <CInput
                                                className="custom-file"
                                                type="file"
                                                id="custom-file-input"
                                                name="file-input"
                                            />
                                        </CLabel>
                                    </CCol>
                                </CFormGroup>
                            </CTabPane>
                            <CTabPane tabId="2">
                                <CRow>
                                    <CCol sm="10">
                                        {items && (
                                            <div className="row media-scroll-gallery">
                                                {items.map((item, index) => {
                                                    let active = false;
                                                    if (selectedItem && selectedItem.id)
                                                        active = selectedItem.id === item.id;
                                                    return (
                                                        <div
                                                            key={index}
                                                            className="gallery-item"
                                                            onClick={() =>
                                                                this.handleImageClick(item)
                                                            }>
                                                            <div>
                                                                <div
                                                                    style={{
                                                                        backgroundImage: `url(${item.file})`,
                                                                        backgroundPosition:
                                                                            'center',
                                                                        backgroundSize: 'cover',
                                                                        height: '100px'
                                                                    }}
                                                                    className={
                                                                        active === true
                                                                            ? 'item-active'
                                                                            : ''
                                                                    }></div>
                                                            </div>
                                                        </div>
                                                    );
                                                })}
                                            </div>
                                        )}
                                    </CCol>
                                    <CCol sm="2">
                                        <MediaSidebar
                                            showCrop={false}
                                            image={this.state.activeItem}
                                            onSaveImageDetail={this.props.onSaveImageDetail}
                                            onDeleteImage={this.props.onDeleteImage}
                                        />
                                    </CCol>
                                </CRow>
                                <CRow className="media-modal-action">
                                    <CButton
                                        color="primary"
                                        className="mr-1 mt-3"
                                        onClick={this.handleSaveSelectedItem}>
                                        Add Featured Image
                                    </CButton>
                                </CRow>
                            </CTabPane>
                        </CTabContent>
                    </CModalBody>
                </CModal>
            </div>
        );
    }
}

function mapState(state) {
    const { media } = state;
    return {
        activeImage: media.activeItem
    };
}

export default connect(mapState, null)(CreateFeaturedImage);
