import React, { Component } from 'react';
import { Route, Switch, BrowserRouter } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import 'font-awesome/css/font-awesome.min.css';
import './App.scss';

// Components
import PulseLoader from './components/common/Loaders/PulseLoader';
import AdminNavbar from './containers/GuestLayout/AdminNavbar';
import TopAlert from './containers/GuestLayout/TopAlert';

// Actions
import { alertActions } from './_actions';
import { textAction } from './_actions';
import { connect } from 'react-redux';

// Wrapper
import { GoogleReCaptchaProvider } from 'react-google-recaptcha-v3';

// Containers
import { AdminLayout } from './containers';
import { GuestLayout } from './containers';
import { configureAnchors } from 'react-scrollable-anchor';
// import WithTracker from './components/WithTracker';
// import WithPixel from './components/WithPixel';

// Pages
const Login = React.lazy(() => import('./views/AdminPages/Auth/Login'));
const PasswordReset = React.lazy(() =>
    import('./views/AdminPages/Auth/PasswordReset/PasswordReset')
);
const PasswordResetDone = React.lazy(() =>
    import('./views/AdminPages/Auth/PasswordResetDone/PasswordResetDone')
);
const PasswordResetConfirm = React.lazy(() =>
    import('./views/AdminPages/Auth/PasswordResetConfirm/PasswordResetConfirm')
);
const Page404 = React.lazy(() => import('./views/Pages/Page404'));
const Page500 = React.lazy(() => import('./views/Pages/Page500'));

const reCaptchaKey = process.env.REACT_APP_RECAPTCHA_KEY;

class App extends Component {
    UNSAFE_componentWillMount() {
        // react-scrollable-anchor offset
        configureAnchors({ offset: -200, scrollDuration: 400 });
        this.props.getAllTexts();
    }

    render() {
        const { alert, authentication, clearAlerts } = this.props;

        return (
            <GoogleReCaptchaProvider reCaptchaKey={reCaptchaKey}>
                {alert.message && <TopAlert alert={alert} onClear={clearAlerts} />}
                <BrowserRouter>
                    {authentication.user && <AdminNavbar />}
                    <React.Suspense fallback={<PulseLoader />}>
                        <Switch>
                            <Route exact path="/admin/login" name="Login Page" component={Login} />
                            <Route
                                exact
                                path="/admin/reset_password"
                                name="Reset Password"
                                component={PasswordReset}
                            />
                            <Route
                                exact
                                path="/admin/reset_password_done"
                                name="Reset Password Done"
                                component={PasswordResetDone}
                            />
                            <Route
                                exact
                                path="/admin/reset/:uid/:token/"
                                name="Reset Password Confirm"
                                component={PasswordResetConfirm}
                            />
                            <Route path="/404" name="Page 404" component={Page404} />
                            <Route path="/500" name="Page 500" component={Page500} />
                            <Route path="/admin" name="Admin Page" component={AdminLayout} />
                            <Route path="/" name="GuestPage" component={GuestLayout} />
                            <Route path="" name="Page 404" component={Page404} />
                        </Switch>
                    </React.Suspense>
                </BrowserRouter>
            </GoogleReCaptchaProvider>
        );
    }
}

function mapStateToProps(state) {
    const { alert, authentication, texts } = state;
    return { alert, authentication, texts };
}

const actionCreators = {
    clearAlerts: alertActions.clear,
    getAllTexts: textAction.getAllTexts
};

export default connect(mapStateToProps, actionCreators)(App);
