import config from '../_config/config';
import { authHeader } from '../_helpers';
import { userService } from './user.service';

const apiUrl = `${config.apiEndpoint}`;

export const mediaService = {
    getAll,
    create,
    getById,
    update,
    updateBulk,
    delete: _delete
};

function getAll(propertyId) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`${apiUrl}/media/?property_id=${propertyId}`, requestOptions).then(handleResponse);
}

function create(media) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(media)
    };

    return fetch(`${apiUrl}/media/create`, requestOptions).then(handleResponse);
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${apiUrl}/media/${id}`, requestOptions).then(handleResponse);
}

function update(media) {
    const requestOptions = {
        method: 'PATCH',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(media)
    };

    return fetch(`${apiUrl}/media/${media.id}`, requestOptions).then(handleResponse);
}

function updateBulk(items) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(items)
    };

    return fetch(`${apiUrl}/media/update`, requestOptions).then(handleResponse);
}

function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${apiUrl}/media/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then((text) => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                userService.logout();
                window.location.reload(true);
            }
            const error = data || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}
