import { mediaConstants, propertyConstants } from '../_constants';

const initialState = {
    activeItem: '',
    items: [],
    uploadedItems: [],
    loading: false
};

export function media(state = initialState, action) {
    switch (action.type) {
        case mediaConstants.MEDIA_UPLOADED: {
            let { uploadedItems } = state;
            uploadedItems.push(action.media);
            return {
                ...state,
                uploadedItems: uploadedItems
            };
        }

        case mediaConstants.GET_ALL_REQUEST:
            return {
                ...state,
                loading: true
            };
        case mediaConstants.GET_ALL_SUCCESS:
            return {
                ...state,
                items: action.items,
                loading: false
            };
        case mediaConstants.GET_ALL_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case mediaConstants.GET_MEDIA_REQUEST:
            return {
                ...state,
                loading: true
            };
        case mediaConstants.GET_MEDIA_SUCCESS:
            return {
                ...state,
                activeItem: action.media,
                loading: false
            };
        case mediaConstants.GET_MEDIA_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case mediaConstants.CREATE_MEDIA_REQUEST:
            return {
                ...state,
                loading: true
            };
        case mediaConstants.CREATE_MEDIA_SUCCESS:
            return {
                ...state,
                activeItem: action.media,
                loading: false
            };
        case mediaConstants.CREATE_MEDIA_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case mediaConstants.UPDATE_MEDIA_REQUEST:
            return {
                ...state,
                loading: true
            };
        case mediaConstants.UPDATE_MEDIA_SUCCESS:
            return {
                ...state,
                activeItem: action.media,
                loading: false
            };
        case mediaConstants.UPDATE_MEDIA_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case mediaConstants.DELETE_MEDIA_REQUEST:
            return {
                ...state,
                items: state.items.map((media) =>
                    media.id === action.id ? { ...media, deleting: true } : media
                )
            };
        case mediaConstants.DELETE_MEDIA_SUCCESS:
            return {
                ...state,
                items: state.items.filter((media) => media.id !== action.id)
            };
        case mediaConstants.DELETE_MEDIA_FAILURE:
            return {
                ...state,
                items: state.items.map((media) => {
                    if (media.id === action.id) {
                        // make copy of user without 'deleting:true' property
                        const { deleting, ...mediaCopy } = media;
                        return { ...mediaCopy };
                    }

                    return media;
                }),
                error: action.error
            };
        case propertyConstants.CREATE_PROPERTY_SUCCESS:
            return {
                ...state,
                uploadedItems: []
            };
        default:
            return state;
    }
}
