import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
    CForm,
    CCol,
    CContainer,
    CFormGroup,
    CInput,
    CInputGroup,
    CLabel,
    CRow,
    CButton,
    CFormFeedback,
    CModal,
    CModalBody
} from '@coreui/react';
import { alertActions, userActions } from '../../../_actions';
import * as Yup from 'yup';
import { Field, Formik } from 'formik';
import AddressAutoComplete from '../../../components/guest/AddressAutoComplete';
import PhoneInput from 'react-phone-input-2';
import DatePicker from 'react-datepicker';
import DatePickerInput from '../../../components/guest/DatePickerInput';
import _ from 'lodash';
import { userService } from '../../../_services';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInfoCircle, faTimesCircle } from '@fortawesome/free-solid-svg-icons';

function EmailConfirmModal({ show, onToggle }) {
    return (
        <CModal className="email-confirm-modal" show={show} toggle={onToggle}>
            <CModalBody>
                <div className="d-flex">
                    <FontAwesomeIcon icon={faInfoCircle} />
                    <div>
                        Your rental application form has been submitted! <br /> We will process it
                        and get back to you ASAP!
                    </div>
                </div>
                {/*<FontAwesomeIcon icon={faTimesCircle} onClick={onToggle}/>*/}
            </CModalBody>
        </CModal>
    );
}

export default function Application() {
    const [submitted, setSubmitted] = useState(false);
    const [show, setShow] = useState(false);

    const dispatch = useDispatch();
    const loading = useSelector((state) => state.users.loading);

    const handleSubmit = (fields) => {
        dispatch(userActions.sendEmailRequest());
        userService.sendApplicationEmail(fields).then(
            (data) => {
                dispatch(userActions.sendEmailSuccess());
                setShow(true);
                // dispatch(
                //     alertActions.success(
                //         'Your rental application form has been submitted! <br/> We will process it and get back to you ASAP!'
                //     )
                // );
                setSubmitted(true);
            },
            (error) => {
                dispatch(userActions.sendEmailFailure());
                dispatch(
                    alertActions.error('Sorry, there was an error while submitting your email')
                );
            }
        );
    };

    return (
        <div className="rental-application-block">
            <EmailConfirmModal show={show} onToggle={() => setShow(!show)} />
            <CContainer>
                <h2>Rental Application Form</h2>
                <Formik
                    initialValues={{
                        name: '',
                        address: '',
                        phonenumber: '1',
                        email: '',
                        startDate: '',
                        endDate: '',
                        people: '',
                        pets: '',
                        contact: '',
                        employer: '',
                        occupation: '',
                        salary: '',
                        reason: '',
                        extradetail: '',
                        lordname: '',
                        lordaddress: '',
                        lordphonenumber: '',
                        formerlordname: '',
                        formerlordaddress: '',
                        formerlordoccupancy: '',
                        formerlordphonenumber: ''
                    }}
                    validationSchema={Yup.object().shape({
                        name: Yup.string().required('Name is required!'),
                        address: Yup.string().required('Address is required!'),
                        phonenumber: Yup.string().required('Phone number is required!'),
                        email: Yup.string().email('Email is invalid').required('Email is required'),
                        startDate: Yup.string().required('Start date is required!'),
                        endDate: Yup.string().required('End date is required!'),
                        contact: Yup.string().required('Emergency contact is required!'),
                        occupation: Yup.string().required('Occupation is required!'),
                        reason: Yup.string().required('Reason is required!'),
                        salary: Yup.string().required('Salary is required!'),
                        extradetail: Yup.string(),
                        lordname: Yup.string().required('Name is required!'),
                        lordphonenumber: Yup.string().required('Phone number is required!'),
                        lordaddress: Yup.string().required('Address is required!'),
                        formerlordname: Yup.string().required('Name is required!'),
                        formerlordoccupancy: Yup.string().required('Occupancy is required!'),
                        formerlordphonenumber: Yup.string().required('Phone number is required!'),
                        formerlordaddress: Yup.string().required('Address is required!')
                    })}
                    isSubmitting={loading}
                    onSubmit={handleSubmit}>
                    {(props) => {
                        const {
                            values,
                            errors,
                            touched,
                            handleChange,
                            setFieldValue,
                            handleBlur,
                            handleSubmit,
                            isValid,
                            handleReset,
                            setTouched
                        } = props;

                        return (
                            <CForm noValidate name="ApplicationForm" onSubmit={handleSubmit}>
                                <CRow>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup>
                                            <CLabel htmlFor="name" className="required">
                                                Name:
                                            </CLabel>
                                            <CInput
                                                type="text"
                                                name="name"
                                                id="name"
                                                invalid={touched.name && !!errors.name}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.name}
                                            />
                                            <CFormFeedback>{errors.name}</CFormFeedback>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline className="address-input">
                                            <CLabel htmlFor="address" className="required">
                                                Permanent address:
                                            </CLabel>
                                            <CInputGroup>
                                                <AddressAutoComplete
                                                    name="address"
                                                    invalid={touched.address && !!errors.address}
                                                    address={values.address}
                                                    onChange={(value) => {
                                                        setTouched({ ...touched, address: true });
                                                        setFieldValue('address', value);
                                                    }}
                                                    onBlur={() =>
                                                        setTouched({ ...touched, address: true })
                                                    }
                                                    onPlaceChanged={(place) => {
                                                        setFieldValue(
                                                            'address',
                                                            place.formatted_address
                                                        );
                                                    }}
                                                    onReset={() => setFieldValue('address', '')}
                                                />
                                                <CFormFeedback
                                                    className={
                                                        touched.address && !!errors.address
                                                            ? 'd-block'
                                                            : ''
                                                    }>
                                                    {errors.address}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="phonenumber" className="required">
                                                Phone number:
                                            </CLabel>
                                            <CInputGroup>
                                                <PhoneInput
                                                    country={'us'}
                                                    value={values.phonenumber}
                                                    placeholder={`   Your Phone Number`}
                                                    onChange={(
                                                        value,
                                                        country,
                                                        e,
                                                        formattedValue
                                                    ) => {
                                                        setTouched({
                                                            ...touched,
                                                            phonenumber: true
                                                        });
                                                        setFieldValue(
                                                            'phonenumber',
                                                            formattedValue
                                                        );
                                                    }}
                                                    onBlur={() =>
                                                        setTouched({
                                                            ...touched,
                                                            phonenumber: true
                                                        })
                                                    }
                                                    inputStyle={{ width: '100%', height: '35px' }}
                                                    isValid={(inputNumber, country, countries) => {
                                                        console.log(
                                                            '__isvalid__',
                                                            inputNumber,
                                                            country,
                                                            countries
                                                        );
                                                        return countries.some((country) => {
                                                            return (
                                                                _.startsWith(
                                                                    inputNumber,
                                                                    country.dialCode
                                                                ) ||
                                                                _.startsWith(
                                                                    country.dialCode,
                                                                    inputNumber
                                                                )
                                                            );
                                                        });
                                                    }}
                                                />
                                                <CFormFeedback
                                                    className={
                                                        touched.phonenumber && !!errors.phonenumber
                                                            ? 'd-block'
                                                            : ''
                                                    }>
                                                    {errors.phonenumber}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="email" className="required">
                                                Email address:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    required
                                                    type="text"
                                                    id="email"
                                                    name="email"
                                                    value={values.email}
                                                    invalid={touched.email && !!errors.email}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.email}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="dates" className="required">
                                                Dates of occupancy (Start Date - End Date):
                                            </CLabel>
                                            <CRow>
                                                <CCol sm={12} md={6}>
                                                    <DatePicker
                                                        name="startDate"
                                                        selected={values.startDate}
                                                        value={values.startDate}
                                                        minDate={new Date()}
                                                        className="form-control custom-form-control "
                                                        dateFormat="MM/dd/yyyy"
                                                        placeholderText="Start Date"
                                                        customInput={<DatePickerInput />}
                                                        onBlur={(e) => {
                                                            setTouched({
                                                                ...touched,
                                                                startDate: true
                                                            });
                                                        }}
                                                        onChange={(value, formattedValue) => {
                                                            if (!touched.startDate) {
                                                                setTouched({
                                                                    ...touched,
                                                                    startDate: true
                                                                });
                                                            }
                                                            if (values.startDate < values.endDate) {
                                                                setFieldValue('endDate', '');
                                                            }
                                                            setFieldValue('startDate', value);
                                                        }}
                                                    />
                                                    <CFormFeedback
                                                        className={
                                                            touched.startDate && !!errors.startDate
                                                                ? 'd-block'
                                                                : ''
                                                        }>
                                                        {errors.startDate}
                                                    </CFormFeedback>
                                                </CCol>
                                                <CCol sm={12} md={6}>
                                                    <DatePicker
                                                        selected={values.endDate}
                                                        value={values.endDate}
                                                        minDate={
                                                            values.startDate === ''
                                                                ? new Date()
                                                                : values.startDate
                                                        }
                                                        className="form-control custom-form-control "
                                                        dateFormat="MM/dd/yyyy"
                                                        placeholderText="End Date"
                                                        customInput={<DatePickerInput />}
                                                        onBlur={(e) => {
                                                            setTouched({
                                                                ...touched,
                                                                endDate: true
                                                            });
                                                        }}
                                                        onChange={(value, formattedValue) => {
                                                            if (!touched.endDate) {
                                                                setTouched({
                                                                    ...touched,
                                                                    endDate: true
                                                                });
                                                            }
                                                            setFieldValue('endDate', value);
                                                        }}
                                                    />
                                                    <CFormFeedback
                                                        className={
                                                            touched.endDate && !!errors.endDate
                                                                ? 'd-block'
                                                                : ''
                                                        }>
                                                        {errors.endDate}
                                                    </CFormFeedback>
                                                </CCol>
                                            </CRow>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="people" className="required">
                                                How many adults, and how many children:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    type="text"
                                                    id="people"
                                                    name="people"
                                                    placeholder={'Example: 2 adults, 1 child'}
                                                    value={values.people}
                                                    invalid={touched.people && !!errors.people}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.people}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="pets" className="required">
                                                Pets - if yes, please list type and breed:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    type="text"
                                                    id="pets"
                                                    name="pets"
                                                    value={values.pets}
                                                    invalid={touched.pets && !!errors.pets}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.pets}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="name" className="required">
                                                Contact in case of an emergency:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    type="text"
                                                    id="contact"
                                                    name="contact"
                                                    value={values.contact}
                                                    invalid={touched.contact && !!errors.contact}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.contact}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="name" className="required">
                                                Employer? (if student, please list name of
                                                university):
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    type="text"
                                                    id="employer"
                                                    name="employer"
                                                    value={values.employer}
                                                    invalid={touched.employer && !!errors.employer}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.employer}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="name" className="required">
                                                Occupation:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    type="text"
                                                    id="occupation"
                                                    name="occupation"
                                                    value={values.occupation}
                                                    invalid={
                                                        touched.occupation && !!errors.occupation
                                                    }
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.occupation}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="salary" className="required">
                                                Salary (Please state currency):
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    required
                                                    type="text"
                                                    id="salary"
                                                    name="salary"
                                                    placeholder={'Example: 50,000 USD per year'}
                                                    value={values.salary}
                                                    invalid={touched.salary && !!errors.salary}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                            </CInputGroup>
                                            <CFormFeedback>{errors.salary}</CFormFeedback>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="reason" className="required">
                                                Reason for your visit:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    required
                                                    type="text"
                                                    id="reason"
                                                    name="reason"
                                                    value={values.reason}
                                                    invalid={touched.reason && !!errors.reason}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                            </CInputGroup>
                                            <CFormFeedback>{errors.reason}</CFormFeedback>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={6}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="extradetail" className="required">
                                                Anything else we should know of::
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    required
                                                    type="text"
                                                    id="extradetail"
                                                    name="extradetail"
                                                    placeholder="Please write N/A if Nothing else"
                                                    value={values.extradetail}
                                                    invalid={
                                                        touched.extradetail && !!errors.extradetail
                                                    }
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.extradetail}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12}>
                                        <h5 className="d-block">Present Landlord</h5>
                                    </CCol>
                                    <CCol sm={12} lg={4}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="lordname" className="required">
                                                Name:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    type="text"
                                                    id="lordname"
                                                    name="lordname"
                                                    required
                                                    value={values.lordname}
                                                    invalid={touched.lordname && !!errors.lordname}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>{errors.lordname}</CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={4}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="lordAddress" className="required">
                                                Complete Address:
                                            </CLabel>
                                            <CInputGroup>
                                                <AddressAutoComplete
                                                    name="lordaddress"
                                                    invalid={
                                                        touched.lordaddress && !!errors.lordaddress
                                                    }
                                                    address={values.lordaddress}
                                                    onChange={(value) => {
                                                        setTouched({
                                                            ...touched,
                                                            lordaddress: true
                                                        });
                                                        setFieldValue('lordaddress', value);
                                                    }}
                                                    onBlur={() =>
                                                        setTouched({
                                                            ...touched,
                                                            lordaddress: true
                                                        })
                                                    }
                                                    onPlaceChanged={(place) => {
                                                        setFieldValue(
                                                            'lordaddress',
                                                            place.formatted_address
                                                        );
                                                    }}
                                                    onReset={() => setFieldValue('lordaddress', '')}
                                                />
                                                <CFormFeedback
                                                    className={
                                                        touched.lordaddress && !!errors.lordaddress
                                                            ? 'd-block'
                                                            : ''
                                                    }>
                                                    {errors.lordaddress}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={4}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="lordphonenumber" className="required">
                                                Phone Number:
                                            </CLabel>
                                            <CInputGroup>
                                                <PhoneInput
                                                    country={'us'}
                                                    value={values.lordphonenumber}
                                                    placeholder={``}
                                                    onChange={(
                                                        value,
                                                        country,
                                                        e,
                                                        formattedValue
                                                    ) => {
                                                        setTouched({
                                                            ...touched,
                                                            lordphonenumber: true
                                                        });
                                                        setFieldValue(
                                                            'lordphonenumber',
                                                            formattedValue
                                                        );
                                                    }}
                                                    onBlur={() =>
                                                        setTouched({
                                                            ...touched,
                                                            lordphonenumber: true
                                                        })
                                                    }
                                                    inputStyle={{ width: '100%', height: '35px' }}
                                                    isValid={(inputNumber, country, countries) => {
                                                        return countries.some((country) => {
                                                            return (
                                                                _.startsWith(
                                                                    inputNumber,
                                                                    country.dialCode
                                                                ) ||
                                                                _.startsWith(
                                                                    country.dialCode,
                                                                    inputNumber
                                                                )
                                                            );
                                                        });
                                                    }}
                                                />
                                                <CFormFeedback
                                                    className={
                                                        touched.formerlordphonenumber &&
                                                        !!errors.formerlordphonenumber
                                                            ? 'd-block'
                                                            : ''
                                                    }>
                                                    {errors.formerlordphonenumber}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12}>
                                        <h5 className="d-block">Former Landlord</h5>
                                    </CCol>
                                    <CCol sm={12} lg={4}>
                                        <CFormGroup inline>
                                            <CLabel htmlFor="formerlordname" className="required">
                                                Name:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    type="text"
                                                    id="formerlordname"
                                                    name="formerlordname"
                                                    value={values.formerlordname}
                                                    required
                                                    invalid={
                                                        touched.formerlordname &&
                                                        !!errors.formerlordname
                                                    }
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>
                                                    {errors.formerlordname}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={4}>
                                        <CFormGroup inline>
                                            <CLabel
                                                htmlFor="formerlordoccupancy"
                                                className="required">
                                                Occupancy:
                                            </CLabel>
                                            <CInputGroup>
                                                <CInput
                                                    required
                                                    type="text"
                                                    id="formerlordoccupancy"
                                                    name="formerlordoccupancy"
                                                    placeholder={
                                                        'Example: April 1, 2018 - August 31, 2019'
                                                    }
                                                    value={values.formerlordoccupancy}
                                                    invalid={
                                                        touched.formerlordoccupancy &&
                                                        !!errors.formerlordoccupancy
                                                    }
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                />
                                                <CFormFeedback>
                                                    {errors.formerlordoccupancy}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={4}>
                                        <CFormGroup inline>
                                            <CLabel
                                                htmlFor="formerLordAddress"
                                                className="required">
                                                Complete Address:
                                            </CLabel>
                                            <CInputGroup>
                                                <AddressAutoComplete
                                                    name="formerlordaddress"
                                                    invalid={
                                                        touched.formerlordaddress &&
                                                        !!errors.formerlordaddress
                                                    }
                                                    address={values.formerlordaddress}
                                                    onChange={(value) => {
                                                        setTouched({
                                                            ...touched,
                                                            formerlordaddress: true
                                                        });
                                                        setFieldValue('formerlordaddress', value);
                                                    }}
                                                    onBlur={() =>
                                                        setTouched({
                                                            ...touched,
                                                            formerlordaddress: true
                                                        })
                                                    }
                                                    onPlaceChanged={(place) => {
                                                        setFieldValue(
                                                            'formerlordaddress',
                                                            place.formatted_address
                                                        );
                                                    }}
                                                    onReset={() =>
                                                        setFieldValue('formerlordaddress', '')
                                                    }
                                                />
                                                <CFormFeedback
                                                    className={
                                                        touched.formerlordaddress &&
                                                        !!errors.formerlordaddress
                                                            ? 'd-block'
                                                            : ''
                                                    }>
                                                    {errors.formerlordaddress}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} lg={4}>
                                        <CFormGroup inline>
                                            <CLabel
                                                htmlFor="formerLordPhoneNumber"
                                                className="required">
                                                Phone Number:
                                            </CLabel>
                                            <CInputGroup>
                                                <PhoneInput
                                                    country={'us'}
                                                    value={values.formerlordphonenumber}
                                                    placeholder={``}
                                                    onChange={(
                                                        value,
                                                        country,
                                                        e,
                                                        formattedValue
                                                    ) => {
                                                        setTouched({
                                                            ...touched,
                                                            formerlordphonenumber: true
                                                        });
                                                        setFieldValue(
                                                            'formerlordphonenumber',
                                                            formattedValue
                                                        );
                                                    }}
                                                    onBlur={() =>
                                                        setTouched({
                                                            ...touched,
                                                            formerlordphonenumber: true
                                                        })
                                                    }
                                                    inputStyle={{ width: '100%', height: '35px' }}
                                                    isValid={(inputNumber, country, countries) => {
                                                        return countries.some((country) => {
                                                            return (
                                                                _.startsWith(
                                                                    inputNumber,
                                                                    country.dialCode
                                                                ) ||
                                                                _.startsWith(
                                                                    country.dialCode,
                                                                    inputNumber
                                                                )
                                                            );
                                                        });
                                                    }}
                                                />
                                                <CFormFeedback
                                                    className={
                                                        touched.formerlordphonenumber &&
                                                        !!errors.formerlordphonenumber
                                                            ? 'd-block'
                                                            : ''
                                                    }>
                                                    {errors.formerlordphonenumber}
                                                </CFormFeedback>
                                            </CInputGroup>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol sm={12} className="d-flex justify-content-center mt-5">
                                        <CButton
                                            color="dark"
                                            type="submit"
                                            className="px-5 mr-5"
                                            disabled={loading || submitted}>
                                            Submit
                                        </CButton>
                                        <CButton
                                            type="reset"
                                            color="light"
                                            className="px-5"
                                            onClick={handleReset}>
                                            Reset
                                        </CButton>
                                    </CCol>
                                </CRow>
                            </CForm>
                        );
                    }}
                </Formik>
            </CContainer>
        </div>
    );
}
