import React from 'react';
import DatePicker from 'react-datepicker';
import DatePickerInput from './DatePickerInput';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import EditableBlock from '../common/EditableBlock';
import { Link } from 'react-router-dom';

export default function MainSearchForm(props) {
    const {
        checkinDate,
        checkoutDate,
        adultsCount,
        childrenCount,
        handleCheckInChange,
        handleCheckOutChange,
        handleAdultsCountChange,
        handleChildrenCountChange,
        handleBrowseButtonClick,
        handleSearchButtonClick
    } = props;
    return (
        <div className="main-search-box">
            <EditableBlock lg id={93} />
            <div className="row">
                <div className="col-xs-12 col-md-3 col-lg-2 p-1">
                    <DatePicker
                        selected={checkinDate}
                        value={checkinDate}
                        minDate={new Date()}
                        className="form-control custom-form-control "
                        dateFormat="MM/dd/yyyy"
                        placeholderText="Check In"
                        customInput={<DatePickerInput />}
                        onChange={handleCheckInChange}
                    />
                </div>
                <div className="col-xs-12 col-md-3 col-lg-2 p-1">
                    <DatePicker
                        selected={checkoutDate}
                        value={checkoutDate}
                        minDate={checkinDate === '' ? new Date() : checkinDate}
                        className="form-control custom-form-control "
                        dateFormat="MM/dd/yyyy"
                        placeholderText="Check Out"
                        customInput={<DatePickerInput />}
                        onChange={handleCheckOutChange}
                    />
                </div>
                <div className="col-xs-12 col-md-3 col-lg-2 p-1">
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text bg-white">
                                <FontAwesomeIcon icon={faUser} />
                                {/*<i className="fa fa-user"/>*/}
                            </span>
                        </div>
                        <input
                            type="text"
                            className="form-control custom-form-control "
                            placeholder="Adults"
                            value={adultsCount}
                            onChange={handleAdultsCountChange}
                            aria-label="adults"
                            aria-describedby="adults"
                        />
                    </div>
                </div>
                <div className="col-xs-12 col-md-3 col-lg-2 p-1">
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text bg-white">
                                <FontAwesomeIcon icon={faUser} />
                                {/*<i className="fa fa-user"/>*/}
                            </span>
                        </div>
                        <input
                            type="text"
                            className="form-control custom-form-control "
                            placeholder="Children"
                            value={childrenCount}
                            onChange={handleChildrenCountChange}
                            aria-label="Children"
                            aria-describedby="children"
                        />
                    </div>
                </div>
                <div className="col-xs-12 col-md-12 col-lg-4 p-1">
                    <div className="d-flex justify-content-around">
                        <button
                            type="button"
                            className="btn btn-outline-primary user-btn user-btn-outline mt-0"
                            onClick={handleSearchButtonClick}>
                            Search
                        </button>
                        <button
                            type="button"
                            className="btn btn-outline-primary user-btn user-btn-outline mb-3"
                            onClick={handleBrowseButtonClick}>
                            Browse all rentals
                        </button>
                    </div>
                </div>
                <div className="col-12 text-center">
                    <p>
                        Please <Link to={'contact'}>Contact us</Link> for best rates for long-term
                        stays of 28 nights or more
                    </p>
                </div>
            </div>
        </div>
    );
}
