import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faDoorOpen,
    faUserFriends,
    faParking,
    faBath,
    faCouch,
    faPaw
} from '@fortawesome/free-solid-svg-icons';

class PropertyCard extends React.Component {
    render() {
        const {
            id,
            title,
            slug,
            category,
            price,
            minPrice,
            maxPrice,
            minMonthPrice,
            maxMonthPrice,
            bedroomCount,
            minSleeps,
            sleeps,
            bathroomCount,
            rentalParking,
            sharedBathroom,
            furnished,
            petsConsidered,
            image
        } = this.props;

        return (
            <div className="col-xl-4 col-md-6 col-sm-12 d-flex mb-4">
                <div className="property-card">
                    <div
                        style={{ backgroundImage: image && image.file ? `url(${image.file})` : `` }}
                        className="property-card-image-wrapper">
                        <div className="card-title-wrapper">
                            <h4>{title}</h4>
                        </div>
                    </div>
                    <div className="property-card-content">
                        <div className="features-wrapper">
                            <ul>
                                {category === 'Private Rooms' ? (
                                    <li>
                                        <FontAwesomeIcon icon={faDoorOpen} /> Private Room
                                    </li>
                                ) : category === 'Studios' ? (
                                    <li>
                                        <FontAwesomeIcon icon={faDoorOpen} /> Studio
                                    </li>
                                ) : bedroomCount && bedroomCount !== 0 ? (
                                    <li>
                                        <FontAwesomeIcon icon={faDoorOpen} /> {bedroomCount} Bedroom
                                    </li>
                                ) : (
                                    ''
                                )}
                                {sharedBathroom && sharedBathroom === true ? (
                                    <li>
                                        <FontAwesomeIcon icon={faBath} />
                                        Shared Bathroom
                                    </li>
                                ) : (
                                    ''
                                )}
                                {bathroomCount &&
                                bathroomCount !== 0 &&
                                sharedBathroom === false ? (
                                    <li>
                                        <FontAwesomeIcon icon={faBath} /> {bathroomCount} Bathroom
                                    </li>
                                ) : (
                                    ''
                                )}
                                {sleeps && sleeps !== 0 ? (
                                    <li>
                                        <FontAwesomeIcon icon={faUserFriends} /> Sleeps {sleeps}
                                    </li>
                                ) : (
                                    ''
                                )}
                                {rentalParking === true && (
                                    <li>
                                        <FontAwesomeIcon icon={faParking} /> Rental Parking
                                    </li>
                                )}
                                {furnished === true && (
                                    <li>
                                        <FontAwesomeIcon icon={faCouch} /> Furnished
                                    </li>
                                )}
                                {petsConsidered && (
                                    <li>
                                        <FontAwesomeIcon icon={faPaw} /> Pets considered
                                    </li>
                                )}
                            </ul>
                        </div>
                        <div className="price-wrapper">
                            <h4>
                                <span>Starting at</span> ${minPrice}/night,
                            </h4>
                            <h4>${minMonthPrice}/month</h4>
                        </div>
                        <div className="property-buttons-wrapper">
                            <Link
                                to={`/quick-book/${slug}`}
                                className="btn btn-outline-primary user-btn user-btn-outline">
                                Quick book
                            </Link>
                            <Link
                                to={`/property/${slug}`}
                                className="btn btn-outline-primary user-btn user-btn-outline">
                                View Property
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default withRouter(PropertyCard);
