import React from 'react';
import { CCard, CCardBody, CCardHeader, CCollapse, CLink } from '@coreui/react';
import ReactQuill from 'react-quill';
import CIcon from '@coreui/icons-react/es/CIcon';

export default function TextEditor({ show, toggleCard, title, text, handleChange }) {
    const modules = {
        toolbar: [
            ['bold', 'italic', 'underline', 'strike'], // toggled buttons
            ['blockquote', 'code-block'],
            [{ header: 1 }, { header: 2 }], // custom button values
            [{ list: 'ordered' }, { list: 'bullet' }],
            [{ script: 'sub' }, { script: 'super' }], // superscript/subscript
            [{ indent: '-1' }, { indent: '+1' }], // outdent/indent
            [{ direction: 'rtl' }], // text direction
            [{ size: ['small', false, 'large', 'huge'] }], // custom dropdown
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ color: [] }, { background: [] }], // dropdown with defaults from theme
            [{ font: [] }],
            [{ align: [] }],
            ['link', 'image'],
            ['clean'] // remove formatting button
        ]
    };

    return (
        <CCard custom accentColor="primary">
            <CCardHeader>
                {title.charAt(0).toUpperCase() + title.slice(1)}
                <div className="card-header-actions">
                    <CLink className="card-header-action" onClick={(e) => toggleCard(e, title)}>
                        <CIcon name={show ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                    </CLink>
                </div>
            </CCardHeader>
            <CCollapse custom show={show}>
                <CCardBody>
                    <ReactQuill
                        value={text || ''}
                        modules={modules}
                        onChange={(value) => handleChange(title, value)}
                    />
                </CCardBody>
            </CCollapse>
        </CCard>
    );
}
