import React from 'react';
import { CCard, CCardBody, CCardHeader, CCollapse, CLink, CSwitch } from '@coreui/react';
import CIcon from '@coreui/icons-react/es/CIcon';

export default function AmenitySelection({
    show,
    toggleCard,
    amenities,
    selectedAmenities,
    onAmenitySelect
}) {
    return (
        <CCard custom accentColor="primary">
            <CCardHeader>
                Amenities
                <div className="card-header-actions">
                    <CLink
                        className="card-header-action"
                        onClick={(e) => toggleCard(e, 'amenitySelection')}>
                        <CIcon name={show ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                    </CLink>
                </div>
            </CCardHeader>
            <CCollapse custom show={show}>
                <CCardBody>
                    <div className="amenity-selection">
                        {amenities &&
                            amenities.length > 0 &&
                            amenities.map((item, index) => {
                                item.checked = !!selectedAmenities.find(
                                    (obj) => obj.id === item.id
                                );
                                return (
                                    <div key={index} className="switch-wrapper">
                                        <CSwitch
                                            className={'mx-1'}
                                            size={'sm'}
                                            shape={'pill'}
                                            color={'info'}
                                            variant={'opposite'}
                                            labelOn={'\u2713'}
                                            labelOff={'\u2715'}
                                            name={item.name}
                                            value={item.id.toString()}
                                            checked={item.checked}
                                            onChange={() => onAmenitySelect(item)}
                                        />
                                        {item.name}
                                    </div>
                                );
                            })}
                    </div>
                </CCardBody>
            </CCollapse>
        </CCard>
    );
}
