import React, { Component } from 'react';
import {
    CInput,
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CModal,
    CModalBody,
    CModalHeader,
    CRow,
    CCol,
    CFormGroup,
    CFormText,
    CLabel,
    CInputGroup,
    CInputGroupAddon,
    CInputGroupText
} from '@coreui/react';
import { TextMask, InputAdapter } from 'react-text-mask-hoc';
import { CIcon } from '@coreui/icons-react';
import BigCalendar from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import { propertyAction } from '../../../_actions/property.actions';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

const localizer = BigCalendar.momentLocalizer(moment); // or globalizeLocalizer
const currDate = new Date();
const currYear = currDate.getFullYear();
const currMonth = currDate.getMonth();

function CalendarModal(props) {
    const {
        show,
        price,
        defaultPrice,
        minPrice,
        maxPrice,
        toggleShow,
        startDate,
        endDate,
        onPriceChange,
        onSave,
        onSetDefault,
        onCancel
    } = props;
    return (
        <CModal show={show} toggle={toggleShow} className="pricing-modal">
            <CModalHeader toggle={toggleShow}>Set Price for selected days</CModalHeader>
            <CModalBody>
                <CFormGroup>
                    <CLabel>From</CLabel>
                    <CInputGroup>
                        <CInputGroupAddon addonType="prepend">
                            <CInputGroupText>
                                <CIcon name="cil-calendar" />
                            </CInputGroupText>
                        </CInputGroupAddon>
                        <TextMask
                            mask={[/\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/]}
                            Component={InputAdapter}
                            className="form-control"
                            value={startDate.toLocaleString()}
                        />
                    </CInputGroup>
                </CFormGroup>
                <CFormGroup>
                    <CLabel>To</CLabel>
                    <CInputGroup>
                        <CInputGroupAddon addonType="prepend">
                            <CInputGroupText>
                                <CIcon name="cil-calendar" />
                            </CInputGroupText>
                        </CInputGroupAddon>
                        <TextMask
                            mask={[/\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/]}
                            Component={InputAdapter}
                            className="form-control"
                            value={endDate.toLocaleString()}
                        />
                    </CInputGroup>
                </CFormGroup>
                <CFormGroup>
                    <CLabel htmlFor="priceItem">
                        Price <span className="required">*</span>
                    </CLabel>
                    <CInputGroup>
                        <CInputGroupAddon addonType="prepend">
                            <CInputGroupText>$</CInputGroupText>
                        </CInputGroupAddon>
                        <CInput
                            type="number"
                            id="priceItem"
                            name="priceItem"
                            value={price !== '' ? price : price}
                            required
                            onChange={onPriceChange}
                        />
                    </CInputGroup>
                </CFormGroup>
                {/*<CInput type="range" min={minPrice} max={maxPrice} name="price" value={this.state.price}*/}
                {/*onChange={(e) => this.setState({price: e.target.value})}/>*/}
                <div>
                    <CButton color="success" className="m-3" onClick={onSave}>
                        Save
                    </CButton>
                    <CButton color="primary" className="m-3" onClick={onSetDefault}>
                        Set default
                    </CButton>
                    <CButton color="light" className="m-3" onClick={onCancel}>
                        Cancel
                    </CButton>
                </div>
            </CModalBody>
        </CModal>
    );
}

class PropertyCalendar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            propertyId: '',
            price: '',
            pricingItems: [],
            showModal: false,
            startDate: '',
            endDate: ''
        };
    }

    componentDidMount() {
        this.props.getPriceItems(this.props.match.params.id);
        this.setState({ propertyId: this.props.match.params.id });
    }

    componentDidUpdate(prevProps, prevState, snapShot) {
        if (
            this.props.property.pricingItems &&
            this.props.property.pricingItems !== prevProps.property.pricingItems
        ) {
            let eventsData = [];
            this.props.property.pricingItems.map((item, index) => {
                eventsData.push({
                    price: item.price,
                    allDay: true,
                    start: item.start_date,
                    end: item.end_date
                });
            });
            this.setState({ pricingItems: eventsData });
        }
    }

    handleToggleModal = () => {
        this.setState({ showModal: !this.state.showModal });
    };

    handleSlotSelection = ({ start, end, action }) => {
        this.setState({ showModal: true });
        this.setState({
            startDate: this.convertDate(start),
            endDate: this.convertDate(end)
        });
    };

    convertDate = (date) => {
        const offset = date.getTimezoneOffset();
        console.log('offset', offset);
        const newDate = new Date(date.getTime() - offset * 60 * 1000);
        return newDate.toISOString().split('T')[0];
    };

    handlePriceChange = (e) => {
        this.setState({ price: e.target.value });
    };

    handleSave = () => {
        this.props.createPriceItem({
            property: this.state.propertyId,
            price: this.state.price,
            start_date: this.state.startDate,
            end_date: this.state.endDate
        });
        this.props.getPriceItems(this.state.propertyId);
        this.setState({
            showModal: false
        });
    };

    handleSetDefault = () => {
        this.props.createPriceItem({
            property: this.state.propertyId,
            price: this.props.price,
            start_date: this.state.startDate,
            end_date: this.state.endDate
        });
        this.props.getPriceItems(this.state.propertyId);
        this.setState({
            showModal: false
        });
    };

    handleCancel = () => {
        this.setState({
            showModal: false,
            startDate: '',
            endDate: ''
        });
    };

    render() {
        const { onInputChange } = this.props;
        return (
            <div className="animated">
                <CalendarModal
                    show={this.state.showModal}
                    price={this.state.price}
                    defaultPrice={this.props.price}
                    minPrice={this.props.minPrice}
                    maxPrice={this.props.maxPrice}
                    startDate={this.state.startDate}
                    endDate={this.state.endDate}
                    toggleShow={this.handleToggleModal}
                    onPriceChange={this.handlePriceChange}
                    onSetDefault={this.handleSetDefault}
                    onSave={this.handleSave}
                    onCancel={this.handleCancel}
                />
                <CCard custom>
                    <CCardHeader>
                        <CIcon name="cil-calendar" /> Pricing Calendar
                    </CCardHeader>
                    <CCardBody>
                        <CRow className="my-4">
                            <CCol sm={6} lg={4}>
                                <CFormGroup>
                                    <CLabel htmlFor="price">
                                        Default Price <span className="required">*</span>
                                    </CLabel>
                                    <CInputGroup>
                                        <CInputGroupAddon addonType="prepend">
                                            <CInputGroupText>$</CInputGroupText>
                                        </CInputGroupAddon>
                                        <CInput
                                            type="number"
                                            id="price"
                                            name="price"
                                            value={this.props.price}
                                            required
                                            onChange={onInputChange}
                                        />
                                    </CInputGroup>
                                </CFormGroup>
                            </CCol>
                            <CCol sm={6} lg={4}>
                                <CFormGroup>
                                    <CLabel htmlFor="minPrice">
                                        Starting nightly Price <span className="required">*</span>
                                    </CLabel>
                                    <CInputGroup>
                                        <CInputGroupAddon addonType="prepend">
                                            <CInputGroupText>$</CInputGroupText>
                                        </CInputGroupAddon>
                                        <CInput
                                            type="number"
                                            id="minPrice"
                                            name="minPrice"
                                            value={this.props.minPrice}
                                            onChange={onInputChange}
                                        />
                                    </CInputGroup>
                                </CFormGroup>
                            </CCol>
                            <CCol sm={6} lg={4}>
                                <CFormGroup>
                                    <CLabel htmlFor="minPrice">
                                        Starting Monthly Price <span className="required">*</span>
                                    </CLabel>
                                    <CInputGroup>
                                        <CInputGroupAddon addonType="prepend">
                                            <CInputGroupText>$</CInputGroupText>
                                        </CInputGroupAddon>
                                        <CInput
                                            type="number"
                                            id="minMonthPrice"
                                            name="minMonthPrice"
                                            value={this.props.minMonthPrice}
                                            onChange={onInputChange}
                                        />
                                    </CInputGroup>
                                </CFormGroup>
                            </CCol>
                        </CRow>
                        <div className="calendar-container">
                            {this.state.pricingItems && (
                                <BigCalendar
                                    selectable={true}
                                    events={[]}
                                    views={['month']}
                                    step={30}
                                    components={{
                                        month: {
                                            dateHeader: ({ date, label }) => {
                                                let pricingItem = this.state.pricingItems.find(
                                                    (item) =>
                                                        moment(date).isBetween(
                                                            moment(item.start),
                                                            moment(item.end),
                                                            null,
                                                            '[]'
                                                        )
                                                );
                                                return (
                                                    <div className="calendar-date-cell">
                                                        <h4>{moment(date).date()}</h4>
                                                        <h6>
                                                            $
                                                            {pricingItem !== undefined
                                                                ? pricingItem.price
                                                                : this.props.price}
                                                        </h6>
                                                    </div>
                                                );
                                            }
                                        }
                                    }}
                                    defaultDate={new Date(currYear, currMonth, 1)}
                                    defaultView="month"
                                    toolbar={true}
                                    localizer={localizer}
                                    onSelectSlot={this.handleSlotSelection}
                                />
                            )}
                        </div>
                    </CCardBody>
                </CCard>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    property: state.properties.activeItem
});

const actionCreators = {
    createPriceItem: propertyAction.createPriceItem,
    getPriceItems: propertyAction.getPriceItems
};

export default connect(mapStateToProps, actionCreators)(withRouter(PropertyCalendar));
