import React from 'react';
import { CModal, CModalBody, CModalHeader } from '@coreui/react';
import Iframe from 'react-iframe';
import CircleLoader from '../../common/Loaders/CircleLoader';

export default function TourModal({ show, toggle, tour360 }) {
    return (
        <CModal show={show} toggle={toggle} className="tour360-modal">
            <CModalHeader toggle={toggle} />
            <CModalBody>
                {tour360 ? (
                    <Iframe
                        loading="lazy"
                        url={`${tour360}`}
                        width="100%"
                        height="500px"
                        id="360tour"
                        className="360tour-iframe"
                        display="initial"
                        position="relative"
                    />
                ) : (
                    <CircleLoader />
                )}
            </CModalBody>
        </CModal>
    );
}
