import React from 'react';
import { withRouter } from 'react-router-dom';

import { Link } from 'react-router-dom';
import {
    CModal,
    CModalBody,
    CModalHeader,
    CNav,
    CNavItem,
    CNavLink,
    CRow,
    CTabContent,
    CTabPane
} from '@coreui/react';

import BookingForm from '../../../components/guest/BookingForm';
import PropertyCard from '../../../components/guest/PropertyCard';
import LocationMap from '../../../components/guest/LocationMap';
import PropertyGalleryModal from '../../../components/guest/property/PropertyGalleryModal';
import TourModal from '../../../components/guest/property/TourModal';
import TextLoader from '../../../components/common/Loaders/TextLoader';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';
import SocialShareButtons from '../../../components/guest/SocialShareButtons';

//redux
import { alertActions } from '../../../_actions/alert.actions';
import { bookingActions } from '../../../_actions/booking.actions';
import { propertyAction } from '../../../_actions/property.actions';
import { connect } from 'react-redux';

//icons
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faDoorOpen,
    faUserFriends,
    faParking,
    faBath,
    faCouch,
    faBed,
    faPaw
} from '@fortawesome/free-solid-svg-icons';
import WithPixel from '../../../components/WithPixel';
import WithGATracker from '../../../components/WithGATracker';
import ReactGA from 'react-ga';

function imagesLoaded(parentNode) {
    const imgElements = [...parentNode.querySelectorAll('img')];
    for (let i = 0; i < imgElements.length; i += 1) {
        const img = imgElements[i];
        if (!img.complete) {
            return false;
        }
    }
    console.log('imgElements.length', imgElements.length);
    return true;
}

class PropertyDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeTabKey: 'overview',
            imagesLoaded: true,
            showGallery: false,
            showIframe: false,
            showShare: false
        };
    }

    componentDidMount() {
        this.props.addBookingInfo({
            checkinDate: '',
            checkoutDate: '',
            adultsCount: '',
            childrenCount: ''
        });
        ReactGA.event({
            category: 'engagement',
            action: 'view_item'
        });
        this.props.getPropertyDetail(this.props.match.params.slug);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.slug !== prevProps.match.params.slug) {
            this.props.getPropertyDetail(this.props.match.params.slug);
        }
    }

    handleBookClick = () => {
        const { booking } = this.props;
        if (booking.checkinDate === '') {
            this.props.alertError('Please select date');
            return;
        }
        if (booking.adultsCount === '') {
            this.props.alertError('Please insert number of people');
            return;
        }
        this.props.addBookingInfo({
            ...booking
        });
        this.props.history.push(`/checkout/${this.props.match.params.slug}`, {
            from: this.props.match.params.slug
        });
    };

    render() {
        const { property } = this.props;

        return (
            <>
                <section
                    className="property-detail-banner-block"
                    style={{
                        backgroundImage:
                            property.featured_img && this.state.imagesLoaded === true
                                ? `url(${property.featured_img.file})`
                                : ``
                    }}>
                    <div className="container property-banner-buttons-wrapper">
                        <button
                            type="button"
                            className="btn btn-outline-primary user-btn user-btn-outline mr-4 mb-4"
                            onClick={() => {
                                this.setState({ showIframe: true });
                            }}>
                            <i className="icon360-2" />
                        </button>
                        <button
                            type="button"
                            className="btn btn-outline-primary user-btn user-btn-outline  mb-4"
                            onClick={() => {
                                this.setState({ showGallery: true });
                            }}>
                            View All Photos
                        </button>
                    </div>
                </section>
                <TourModal
                    show={this.state.showIframe}
                    tour360={this.props.property.tour360}
                    toggle={() => this.setState({ showIframe: !this.state.showIframe })}
                />
                <PropertyGalleryModal
                    show={this.state.showGallery}
                    images={this.props.property.gallery_imgs}
                    toggle={() => this.setState({ showGallery: !this.state.showGallery })}
                />
                <section className="property-detail-content-block">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 col-md-12 col-lg-7 mb-4">
                                <div className="w-100">
                                    {property.name && this.state.imagesLoaded === true ? (
                                        <div>
                                            <h3 className="text-blue mb-3">{property.name}</h3>
                                        </div>
                                    ) : (
                                        <CircleLoader />
                                    )}
                                </div>
                                <div className="w-100">
                                    <div className="property-feature-icons-wrapper">
                                        {property.furnished && (
                                            <span>
                                                <FontAwesomeIcon icon={faCouch} /> Furnished
                                            </span>
                                        )}
                                        {property.sleeps && property.sleeps !== 0 ? (
                                            <span>
                                                <FontAwesomeIcon icon={faUserFriends} />
                                                Sleeps {property.sleeps}
                                            </span>
                                        ) : (
                                            ''
                                        )}
                                        {property.category === 'Private Rooms' ? (
                                            <span>
                                                <FontAwesomeIcon icon={faDoorOpen} /> Private Room
                                            </span>
                                        ) : property.category === 'Studios' ? (
                                            <span>
                                                <FontAwesomeIcon icon={faDoorOpen} /> Studio
                                            </span>
                                        ) : property.countBed && property.countBed !== 0 ? (
                                            <span>
                                                <FontAwesomeIcon icon={faDoorOpen} />{' '}
                                                {property.countBed} Bedroom
                                            </span>
                                        ) : (
                                            ''
                                        )}
                                        {property.bathroom_count &&
                                        property.bathroom_count !== 0 &&
                                        property.shared_bathroom === false ? (
                                            <span>
                                                <FontAwesomeIcon icon={faBath} />{' '}
                                                {property.bathroom_count} Bathroom
                                            </span>
                                        ) : (
                                            ''
                                        )}
                                        {property.shared_bathroom &&
                                        property.shared_bathroom === true ? (
                                            <span>
                                                <FontAwesomeIcon icon={faBath} /> Shared Bathroom
                                            </span>
                                        ) : (
                                            ''
                                        )}
                                        {property.rental_parking && (
                                            <span>
                                                <FontAwesomeIcon icon={faParking} /> Rental Parking
                                            </span>
                                        )}
                                        {property.pets_considered && (
                                            <span>
                                                <FontAwesomeIcon icon={faPaw} /> Pets considered
                                            </span>
                                        )}
                                    </div>
                                    <CNav variant="tabs" className="property-detail-tab-buttons">
                                        {[
                                            'overview',
                                            'amenity',
                                            'location',
                                            'neighbourhood',
                                            'transit'
                                        ].map((item, index) => (
                                            <CNavItem key={index} custom>
                                                <CNavLink
                                                    active={this.state.activeTabKey === item}
                                                    className="btn btn-outline-primary user-btn user-btn-outline"
                                                    onClick={() =>
                                                        this.setState({ activeTabKey: item })
                                                    }>
                                                    {item.toUpperCase()}
                                                </CNavLink>
                                            </CNavItem>
                                        ))}
                                    </CNav>
                                    <CTabContent activeTab={this.state.activeTabKey}>
                                        <CTabPane tabId="overview">
                                            {property.description &&
                                            this.state.imagesLoaded === true ? (
                                                <div
                                                    dangerouslySetInnerHTML={{
                                                        __html: property.description
                                                    }}
                                                />
                                            ) : (
                                                <CircleLoader />
                                            )}
                                            <div className="row">
                                                {property.property_rooms &&
                                                    property.property_rooms.map((item, index) => (
                                                        <div
                                                            key={index}
                                                            className="col-12 col-sm-4 col-lg-4 col-xl-4 mb-4">
                                                            <div className="property-room-container position-relative d-block mx-auto">
                                                                <div className="property-room">
                                                                    <div className="property-room-details">
                                                                        <FontAwesomeIcon
                                                                            icon={faBed}
                                                                        />
                                                                        <p>{item.name}</p>
                                                                        <p>{item.bed_type}</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))}
                                            </div>
                                        </CTabPane>
                                        <CTabPane tabId="amenity">
                                            <h4>Amenities</h4>
                                            <div className="container text-center">
                                                <div className="row">
                                                    {property.amenities &&
                                                        property.amenities.map((item, index) => (
                                                            <div
                                                                key={index}
                                                                className="col-6 col-md-3">
                                                                <div className="amenity-box">
                                                                    {item.image && item.image ? (
                                                                        <div
                                                                            style={{
                                                                                backgroundImage: `url(${item.image.file})`,
                                                                                backgroundPosition:
                                                                                    'center',
                                                                                backgroundSize:
                                                                                    'cover',
                                                                                paddingBottom:
                                                                                    '100%',
                                                                                boxShadow:
                                                                                    '0 6px 12px 0 rgba(0, 0, 0, 0.15)'
                                                                            }}
                                                                        />
                                                                    ) : (
                                                                        <div
                                                                            style={{
                                                                                paddingBottom:
                                                                                    '100%',
                                                                                backgroundColor:
                                                                                    '#ebedef'
                                                                            }}
                                                                        />
                                                                    )}
                                                                    <p>{item.name}</p>
                                                                </div>
                                                            </div>
                                                        ))}
                                                </div>
                                            </div>
                                        </CTabPane>
                                        <CTabPane tabId="location">
                                            <h4>Location</h4>
                                            {property.lat && (
                                                <LocationMap
                                                    lat={property.lat}
                                                    lng={property.lng}
                                                />
                                            )}
                                        </CTabPane>
                                        <CTabPane tabId="neighbourhood">
                                            {property.neighbourhood && (
                                                <div
                                                    dangerouslySetInnerHTML={{
                                                        __html: property.neighbourhood
                                                    }}
                                                />
                                            )}
                                        </CTabPane>
                                        <CTabPane tabId="transit">
                                            {property.transit && (
                                                <div
                                                    dangerouslySetInnerHTML={{
                                                        __html: property.transit
                                                    }}
                                                />
                                            )}
                                        </CTabPane>
                                    </CTabContent>
                                </div>
                            </div>
                            <div className="col-12 col-md-12 col-lg-5">
                                <div className="booking-information">
                                    {this.state.showShare ? (
                                        <div className="row justify-content-center mb-3">
                                            <SocialShareButtons
                                                url={window.location.href}
                                                text="Check out this property"
                                            />
                                        </div>
                                    ) : (
                                        ''
                                    )}
                                    <div className="booking-form-header">
                                        {property.price ? (
                                            <div>
                                                <h4>
                                                    Starting at <br />
                                                    <span>
                                                        ${property.min_price}/night, $
                                                        {property.min_month_price}/month
                                                    </span>
                                                </h4>
                                            </div>
                                        ) : (
                                            <TextLoader width={'100px'} />
                                        )}
                                        <div>
                                            <button
                                                type="button"
                                                className="btn btn-outline-secondary user-btn"
                                                onClick={() =>
                                                    this.setState({
                                                        showShare: !this.state.showShare
                                                    })
                                                }>
                                                <i className="fa fa-external-link" /> Share
                                            </button>
                                        </div>
                                    </div>
                                    <BookingForm />
                                    <button
                                        type="button"
                                        className="btn btn-outline-primary user-btn user-btn-outline d-block mx-auto text-center mb-3 book-btn"
                                        onClick={this.handleBookClick}>
                                        Book Now
                                    </button>
                                    <p className="text-center">
                                        For booking assistance please{' '}
                                        <u>
                                            <Link to="/Contact">contact us</Link>
                                        </u>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="container mt-5">
                        <h3 className="text-blue">Similar Accommodations </h3>
                        <div className="row mt-5">
                            {property.similar_properties ? (
                                property.similar_properties.map((property, index) => (
                                    <PropertyCard
                                        key={index}
                                        id={property.id}
                                        address={property.address}
                                        title={property.name}
                                        slug={property.slug}
                                        price={property.price}
                                        minPrice={property.min_price}
                                        maxPrice={property.max_price}
                                        category={property.category}
                                        bedroomCount={property.bedroom_count}
                                        bathroomCount={property.bathroom_count}
                                        sharedBathroom={property.shared_bathroom}
                                        minSleeps={property.min_sleeps}
                                        sleeps={property.sleeps}
                                        rentalParking={property.rental_parking}
                                        furnished={property.furnished}
                                        petsConsidered={property.pets_considered}
                                        image={property.featured_img}
                                    />
                                ))
                            ) : (
                                <CircleLoader />
                            )}
                        </div>
                    </div>
                </section>
            </>
        );
    }
}

const mapStateToProps = (state) => {
    const { properties, booking } = state;
    return {
        property: properties.activeItem,
        loading: properties.loading,
        booking
    };
};

const actionCreators = {
    getPropertyDetail: propertyAction.getPropertyDetail,
    addBookingInfo: bookingActions.addBookingInfo,
    alertError: alertActions.error
};

export default withRouter(
    connect(mapStateToProps, actionCreators)(WithPixel(WithGATracker(PropertyDetail)))
);
