import React, { Component } from 'react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CForm,
    CFormFeedback,
    CFormGroup,
    CInput,
    CLabel,
    CRow
} from '@coreui/react';

import { Formik } from 'formik';
import * as Yup from 'yup';
import { categoryAction as categoryActions } from '../../../_actions/category.actions';
import { connect } from 'react-redux';

class CategoryCreate extends Component {
    handleSubmit = (fields) => {
        const category = {
            ...fields
        };
        this.props.createCategory(category, this.props.history);
    };

    render() {
        const { loading } = this.props;

        return (
            <div className="animated fadeIn">
                <CRow>
                    <CCol lg={6} sm={12} className="mx-auto">
                        <CCard custom>
                            <CCardHeader>
                                <h4>Create category</h4>
                            </CCardHeader>
                            <CCardBody>
                                <Formik
                                    enableReinitialize
                                    initialValues={{ name: '' }}
                                    validate={{
                                        name: Yup.string()
                                            .min(2, `name has to be at least 2 characters`)
                                            .required('name is required')
                                    }}
                                    onSubmit={(fields) => this.handleSubmit(fields)}>
                                    {({
                                        values,
                                        errors,
                                        touched,
                                        status,
                                        dirty,
                                        handleChange,
                                        handleBlur,
                                        handleSubmit,
                                        isValid,
                                        handleReset
                                    }) => (
                                        <CForm
                                            onSubmit={handleSubmit}
                                            noValidate
                                            name="categoryForm">
                                            <CFormGroup>
                                                <CLabel for="name">Category Name</CLabel>
                                                <CInput
                                                    type="text"
                                                    name="name"
                                                    id="name"
                                                    valid={!errors.name}
                                                    invalid={touched.name && !!errors.name}
                                                    autoFocus={true}
                                                    required
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    value={values.name}
                                                />
                                                <CFormFeedback>{errors.name}</CFormFeedback>
                                            </CFormGroup>
                                            <CRow>
                                                <CCol xs="12" sm="6" className="text-center">
                                                    <CButton
                                                        type="submit"
                                                        color="success"
                                                        className="mr-1"
                                                        disabled={!isValid}>
                                                        {loading ? 'Saving' : 'Save'}
                                                    </CButton>
                                                </CCol>
                                                <CCol xs="12" sm="6" className="text-center">
                                                    <CButton
                                                        type="reset"
                                                        color="danger"
                                                        className="mr-1"
                                                        onClick={handleReset}>
                                                        Reset
                                                    </CButton>
                                                </CCol>
                                            </CRow>
                                        </CForm>
                                    )}
                                </Formik>
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    loading: state.categories.loading,
    category: state.categories.activeItem
});

const actionCreators = {
    createCategory: categoryActions.createCategory
};

export default connect(mapStateToProps, actionCreators)(CategoryCreate);
