import { propertyConstants } from '../_constants/property.constants';
import { propertyService } from '../_services';
import { alertActions } from './alert.actions';

export const propertyAction = {
    getListing,
    getFilteredListing,
    getLocationListing,
    getPropertyList,
    createProperty,
    getPropertyDetail,
    getProperty,
    getLatestProperty,
    updateProperty,
    deleteProperty,
    getPriceItems,
    createPriceItem
};

function getListing() {
    return (dispatch) => {
        dispatch(request());
        propertyService.getListing().then(
            (items) => {
                dispatch(success(items));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: propertyConstants.GET_ALL_REQUEST };
    }

    function success(items) {
        return { type: propertyConstants.GET_ALL_SUCCESS, items };
    }

    function failure(error) {
        return { type: propertyConstants.GET_ALL_FAILURE, error };
    }
}

