import {
    CButton,
    CCard,
    CCardBody,
    CCol,
    CContainer,
    CForm,
    CFormFeedback,
    CFormGroup,
    CInput,
    CRow
} from '@coreui/react';
import React, { Component } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { userActions } from '../../../_actions';
import connect from 'react-redux/es/connect/connect';

const validationSchema = function (values) {
    return Yup.object().shape({
        first_name: Yup.string(),
        last_name: Yup.string(),
        username: Yup.string()
            .min(5, `Username has to be at least 5 characters`)
            .required('Username is required'),
        email: Yup.string().email('Invalid email address').required('Email is required!'),
        password: Yup.string()
    });
};

const validate = (getValidationSchema) => {
    return (values) => {
        const validationSchema = getValidationSchema(values);
        try {
            validationSchema.validateSync(values, { abortEarly: false });
            return {};
        } catch (error) {
            return getErrorsFromValidationError(error);
        }
    };
};

const getErrorsFromValidationError = (validationError) => {
    const FIRST_ERROR = 0;
    return validationError.inner.reduce((errors, error) => {
        return {
            ...errors,
            [error.path]: error.errors[FIRST_ERROR]
        };
    }, {});
};

const initialValues = {
    first_name: '',
    last_name: '',
    username: '',
    email: '',
    password: ''
};

class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: initialValues,
            loading: false
        };
        this.touchAll = this.touchAll.bind(this);
    }

    componentDidMount() {
        console.log('this.props', this.props);
        this.props.getUser(this.props.account.id);
    }

    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        console.log('UNSAFE_componentWillReceiveProps', nextProps);
        this.setState({ user: nextProps.user });
        console.log(nextProps);
    }

    findFirstError(formName, hasError) {
        const form = document.forms[formName];
        for (let i = 0; i < form.length; i++) {
            if (hasError(form[i].name)) {
                form[i].focus();
                break;
            }
        }
    }

    validateForm(errors) {
        this.findFirstError('userForm', (fieldName) => {
            console.log(fieldName);
            return Boolean(errors[fieldName]);
        });
    }

    touchAll(setTouched, errors) {
        setTouched({
            first_name: true,
            last_name: true,
            username: true,
            email: true,
            password: true
        });
        this.validateForm(errors);
    }

    handleSubmit = (fields) => {
        console.log(fields);
        delete fields.status;
        delete fields.role;
        this.props.updateUser(fields);
    };

    render() {
        const { loading, user } = this.props;

        return (
            <div className="c-app c-default-layout flex-row align-items-center">
                <CContainer>
                    <CRow className="justify-content-center">
                        <CCol md="9" lg="7" xl="6">
                            <CCard className="mx-4">
                                <CCardBody className="p-4">
                                    <h3 className="text-center">Profile</h3>
                                    {user && (
                                        <Formik
                                            initialValues={user}
                                            validate={validate(validationSchema)}
                                            onSubmit={(fields) => this.handleSubmit(fields)}
                                            render={({
                                                values,
                                                errors,
                                                touched,
                                                dirty,
                                                handleChange,
                                                handleBlur,
                                                handleSubmit,
                                                isValid,
                                                handleReset
                                            }) => (
                                                <CRow>
                                                    <CCol lg="12">
                                                        <CForm
                                                            onSubmit={handleSubmit}
                                                            noValidate
                                                            name="userForm">
                                                            <CFormGroup>
                                                                {/*<CLabel for="first_name">First Name</CLabel>*/}
                                                                <CInput
                                                                    type="text"
                                                                    name="first_name"
                                                                    id="first_name"
                                                                    placeholder="First Name"
                                                                    autoComplete="given-name"
                                                                    valid={!errors.first_name}
                                                                    invalid={
                                                                        touched.first_name &&
                                                                        !!errors.first_name
                                                                    }
                                                                    autoFocus={true}
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.first_name}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.first_name}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CFormGroup>
                                                                {/*<CLabel for="last_name">Last Name</CLabel>*/}
                                                                <CInput
                                                                    type="text"
                                                                    name="last_name"
                                                                    id="last_name"
                                                                    placeholder="Last Name"
                                                                    autoComplete="family-name"
                                                                    valid={!errors.last_name}
                                                                    invalid={
                                                                        touched.last_name &&
                                                                        !!errors.last_name
                                                                    }
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.last_name}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.last_name}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CFormGroup>
                                                                {/*<CLabel for="username">User Name</CLabel>*/}
                                                                <CInput
                                                                    type="text"
                                                                    name="username"
                                                                    id="username"
                                                                    placeholder="User Name"
                                                                    autoComplete="username"
                                                                    valid={!errors.username}
                                                                    invalid={
                                                                        touched.username &&
                                                                        !!errors.username
                                                                    }
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.username}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.username}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CFormGroup>
                                                                {/*<CLabel for="email">Email</CLabel>*/}
                                                                <CInput
                                                                    type="email"
                                                                    name="email"
                                                                    id="email"
                                                                    placeholder="Email"
                                                                    autoComplete="email"
                                                                    valid={!errors.email}
                                                                    invalid={
                                                                        touched.email &&
                                                                        !!errors.email
                                                                    }
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.email}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.email}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CFormGroup>
                                                                <CInput
                                                                    type="password"
                                                                    name="password"
                                                                    id="password"
                                                                    placeholder="New password"
                                                                    valid={!errors.password}
                                                                    invalid={
                                                                        touched.password &&
                                                                        !!errors.password
                                                                    }
                                                                    required
                                                                    onBlur={handleBlur}
                                                                    value={values.password}
                                                                    onChange={handleChange}
                                                                />
                                                            </CFormGroup>
                                                            <CRow>
                                                                <CCol
                                                                    xs="12"
                                                                    sm="6"
                                                                    className="text-center">
                                                                    <CButton
                                                                        type="submit"
                                                                        color="success"
                                                                        className="mr-1"
                                                                        disabled={
                                                                            loading || !isValid
                                                                        }>
                                                                        {loading
                                                                            ? 'Wait...'
                                                                            : 'Save'}
                                                                    </CButton>
                                                                </CCol>
                                                                <CCol
                                                                    xs="12"
                                                                    sm="6"
                                                                    className="text-center">
                                                                    <CButton
                                                                        type="reset"
                                                                        color="danger"
                                                                        className="mr-1"
                                                                        onClick={handleReset}>
                                                                        Reset
                                                                    </CButton>
                                                                </CCol>
                                                            </CRow>
                                                        </CForm>
                                                    </CCol>
                                                </CRow>
                                            )}
                                        />
                                    )}
                                </CCardBody>
                            </CCard>
                        </CCol>
                    </CRow>
                </CContainer>
            </div>
        );
    }
}

function mapStateToProps(state) {
    const { authentication, users } = state;
    return {
        account: authentication.user,
        user: users.activeItem
    };
}

const actionCreators = {
    getUser: userActions.getUser,
    updateUser: userActions.updateUser
};

export default connect(mapStateToProps, actionCreators)(Profile);
