import { alertActions } from './alert.actions';
import { categoryConstants } from '../_constants';
import { categoryService } from '../_services/category.service';

export const categoryAction = {
    getAllCategory,
    getCategoryListing,
    createCategory,
    getCategory,
    updateCategory,
    deleteCategory
};

function getAllCategory() {
    return (dispatch) => {
        dispatch(request());
        categoryService.getAll().then(
            (items) => {
                dispatch(success(items));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: categoryConstants.GET_CATEGORIES_REQUEST };
    }

    function success(items) {
        return { type: categoryConstants.GET_CATEGORIES_SUCCESS, items };
    }

    function failure(error) {
        return { type: categoryConstants.GET_CATEGORIES_FAILURE, error };
    }
}

function getCategoryListing() {
    return (dispatch) => {
        dispatch(request());
        categoryService.getListing().then(
            (items) => {
                dispatch(success(items));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: categoryConstants.GET_CATEGORY_ITEMS_REQUEST };
    }

    function success(items) {
        return { type: categoryConstants.GET_CATEGORY_ITEMS_SUCCESS, items };
    }

    function failure(error) {
        return { type: categoryConstants.GET_CATEGORY_ITEMS_FAILURE, error };
    }
}

function createCategory(category, history) {
    return (dispatch) => {
        dispatch(request());
        categoryService.create(category).then(
            (res) => {
                dispatch(success(res));
                dispatch(alertActions.success('A Category has been created successfully'));
                history.push('/admin/categories');
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: categoryConstants.CREATE_CATEGORY_REQUEST };
    }

    function success(Category) {
        return { type: categoryConstants.CREATE_CATEGORY_SUCCESS, Category };
    }

    function failure(error) {
        return { type: categoryConstants.CREATE_CATEGORY_FAILURE, error };
    }
}

function getCategory(id) {
    return (dispatch) => {
        dispatch(request());
        categoryService.getById(id).then(
            (Category) => dispatch(success(Category)),
            (error) => dispatch(failure(error.toString()))
        );
    };

    function request() {
        return { type: categoryConstants.GET_CATEGORY_REQUEST };
    }

    function success(category) {
        return { type: categoryConstants.GET_CATEGORY_SUCCESS, category };
    }

    function failure(error) {
        return { type: categoryConstants.GET_CATEGORY_FAILURE, error };
    }
}

function updateCategory(category, history) {
    return (dispatch) => {
        dispatch(request());
        categoryService.update(category).then(
            (category) => {
                dispatch(success(category));
                dispatch(alertActions.success('A Category has been updated successfully!'));
                history.push('/admin/categories');
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );

        function request() {
            return { type: categoryConstants.UPDATE_CATEGORY_REQUEST };
        }

        function success(Category) {
            return { type: categoryConstants.UPDATE_CATEGORY_SUCCESS, Category };
        }

        function failure(error) {
            return { type: categoryConstants.UPDATE_CATEGORY_FAILURE, error };
        }
    };
}

function deleteCategory(id) {
    return (dispatch) => {
        dispatch(request(id));
        categoryService.delete(id).then(
            (res) => {
                dispatch(success(id));
                dispatch(alertActions.success('A Category has been deleted successfully'));
                // history.push('/admin/categories')
            },
            (error) => dispatch(failure(id, error.toString()))
        );
    };

    function request(id) {
        return { type: categoryConstants.DELETE_CATEGORY_REQUEST, id };
    }

    function success(id) {
        return { type: categoryConstants.DELETE_CATEGORY_SUCCESS, id };
    }

    function failure(id, error) {
        return { type: categoryConstants.DELETE_CATEGORY_FAILURE, id, error };
    }
}
