import React from 'react';
import { Link } from 'react-router-dom';

// property
import ContactForm from '../../../components/guest/ContactForm';
import { LazyLoadImage } from 'react-lazy-load-image-component';

// icons
import imgBoston from '../../../assets/locations-boston.jpg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInstagram, faTwitter, faFacebookSquare } from '@fortawesome/free-brands-svg-icons';
import Subscription from '../../../components/guest/apartments/Subscription';
import HeaderBanner from '../../../containers/GuestLayout/HeaderBanner';
import EditableBlock from '../../../components/common/EditableBlock';
import WithPixel from '../../../components/WithPixel';
import WithGATracker from '../../../components/WithGATracker';
import EditableTitle from '../../../components/common/EditableTitle';
import EditableText from '../../../components/common/EditableText';
import EditButton from '../../../components/common/EditButton';
import SocialLinks from '../../../components/guest/SocialLinks';

class About extends React.Component {
    render() {
        return (
            <div>
                <HeaderBanner className="main-background-2" />
                <div className="container my-5">
                    <div id="boston" className="row mb-5">
                        <div className="col-lg">
                            <EditableBlock id={44} lg />
                            <Link to="/locations?location=boston">
                                <button className="btn btn-outline-primary user-btn user-btn-outline">
                                    CHECK OUT BOSTON
                                </button>
                            </Link>
                        </div>
                        <div className="col-lg image-box">
                            <LazyLoadImage alt="boston" src={imgBoston} className="image-box" />
                        </div>
                    </div>
                </div>
                <section className="bg-lightblue pb-1">
                    <div className="container text-center">
                        <h2 className="text-blue">
                            <EditableTitle id={95} />
                        </h2>
                        <EditableText id={95} />
                        <EditButton id={95} />
                    </div>
                </section>
                <section className="bg-lightblue">
                    <div className="container">
                        <div className="row ">
                            {/*<h2><EditableTitle id={45}/></h2>*/}
                            {/*<EditableText id={45}/>*/}
                            {/*<EditButton id={45}/>*/}
                            <h2 className="text-blue">Our Team</h2>
                            <p className="mb-5">
                                Hello! We are small local team with diverse backgrounds, and
                                lifetime residents of this amazing city so we have inside
                                information and suggestions for your planned visit, short or long.
                                Meet also the latest addition to our team, Cisco the bike rider, and
                                follow his personal{' '}
                                <Link
                                    target="_blank"
                                    to={'//www.instagram.com/where.is.cisco/?hl=en'}
                                    className="instagram-link">
                                    Instagram page
                                </Link>
                            </p>
                        </div>
                    </div>
                </section>
                <section>
                    <div className="container my-5">
                        <div className="row">
                            <div className="col-lg">
                                {/*<EditableBlock id={46} lg/>*/}
                                <h2 className="text-blue">Contact us</h2>
                                <p>
                                    We are available to answer questions and provide suggestions or
                                    connect you with some of our additional personalized services.
                                    Please contact us using the form or the contact details below:
                                </p>
                                <p>(617)-855-9576 </p>
                                <p>info@starsofboston.com </p>
                                <p>
                                    1583 Beacon Street
                                    <br />
                                    Brookline, MA 02446
                                    <br />
                                    USA
                                </p>
                                <SocialLinks />
                            </div>
                            <div className="col-lg">
                                <ContactForm />
                            </div>
                        </div>
                    </div>
                </section>
                <Subscription />
            </div>
        );
    }
}

export default WithPixel(WithGATracker(About));
