import { bookingConstants } from '../_constants';
import { bookingService } from '../_services';
import { alertActions } from './alert.actions';
import ReactPixel from 'react-facebook-pixel';

export const bookingActions = {
    addBookingInfo,
    getBookingQuote,
    addBookingPayment
};

function addBookingInfo(data) {
    return { type: bookingConstants.Add_BOOKING_INFOMATION, data };
}

function getBookingQuote({ checkinDate, checkoutDate, bookervilleID }) {
    return (dispatch) => {
        dispatch(request());
        bookingService.getBookingQuote({ checkinDate, checkoutDate, bookervilleID }).then(
            (data) => {
                dispatch(success(data));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: bookingConstants.Add_BOOKING_PAYMENT_REQUEST };
    }

    function success(data) {
        return { type: bookingConstants.Add_BOOKING_PAYMENT_SUCCESS, data: data };
    }

    function failure(error) {
        return { type: bookingConstants.Add_BOOKING_PAYMENT_FAILURE, data: error };
    }
}

function addBookingPayment(booking, history) {
    return (dispatch) => {
        dispatch(request());
        bookingService.addBookingPayment(booking).then(
            (data) => {
                dispatch(success(data));
                ReactPixel.track('Purchase', { value: booking.total, currency: 'USD' });
                history.push('/checkout-success');
            },
            (errors) => {
                dispatch(failure(errors.toString()));
                let errorMessage = '';
                errors.map((value) => {
                    errorMessage = errorMessage + value + '</br>';
                });

                dispatch(alertActions.error(errorMessage));
            }
        );
    };

    function request() {
        return { type: bookingConstants.Add_BOOKING_PAYMENT_REQUEST, booking: booking };
    }

    function success(data) {
        return { type: bookingConstants.Add_BOOKING_PAYMENT_SUCCESS, data: data };
    }

    function failure(error) {
        return { type: bookingConstants.Add_BOOKING_PAYMENT_FAILURE, data: error };
    }
}
