import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { alertActions, textAction } from '../../_actions';
import { connect } from 'react-redux';

class EditButton extends Component {
    render() {
        const { id, authentication, texts } = this.props;
        return (
            <>
                {authentication.user && texts && texts.length > 0 && (
                    <div className="text-center">
                        <Link to={`/admin/text/${texts[id].id}`} className="edit-btn">
                            Edit
                        </Link>
                    </div>
                )}
            </>
        );
    }
}

const mapStateToProps = (state) => {
    const { authentication, texts } = state;
    return {
        authentication: authentication,
        texts: texts.items
    };
};

const actionCreators = {
    alertError: alertActions.error,
    selectText: textAction.selectText,
    getText: textAction.getText
};

export default connect(mapStateToProps, actionCreators)(EditButton);
