import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CRow,
    CTable,
    CModal,
    CModalHeader,
    CModalFooter,
    CLink,
    CPagination,
    CPaginationItem,
    CInput,
    CContainer
} from '@coreui/react';
import {
    useTable,
    useFilters,
    useSortBy,
    usePagination,
    useBlockLayout,
    useGlobalFilter
} from 'react-table';

import { CIcon } from '@coreui/icons-react';
import { connect } from 'react-redux';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';
// import { faSortDown, faSortUp } from '@fortawesome/free-solid-svg-icons'
import matchSorter from 'match-sorter';
import { amenityAction } from '../../../_actions/amenity.actions';

function DefaultColumnFilter({ column: { filterValue, preFilteredRows, setFilter } }) {
    const count = preFilteredRows.length;

    return (
        <CInput
            value={filterValue || ''}
            onChange={(e) => {
                setFilter(e.target.value || undefined);
            }}
            placeholder={`Search ${count} records...`}
        />
    );
}

function fuzzyTextFilterFn(rows, id, filterValue) {
    return matchSorter(rows, filterValue, { keys: [(row) => row.values[id]] });
}

function DataTable({ columns, data }) {
    const filterTypes = React.useMemo(
        () => ({
            fuzzyText: fuzzyTextFilterFn,
            text: (rows, id, filterValue) => {
                return rows.filter((row) => {
                    const rowValue = row.values[id];
                    return rowValue !== undefined
                        ? String(rowValue)
                              .toLowerCase()
                              .startsWith(String(filterValue).toLowerCase())
                        : true;
                });
            }
        }),
        []
    );

    const defaultColumn = React.useMemo(
        () => ({
            Filter: DefaultColumnFilter
        }),
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        prepareRow,
        page,
        canPreviousPage,
        canNextPage,
        pageCount,
        gotoPage,
        nextPage,
        previousPage,
        setPageSize,
        state: { pageIndex, pageSize }
    } = useTable(
        {
            columns,
            data,
            defaultColumn,
            filterTypes,
            initialState: { pageIndex: 0, pageSize: 5 }
        },
        useBlockLayout,
        useFilters,
        useGlobalFilter,
        useSortBy,
        usePagination
    );

    return (
        <>
            <div className="d-flex justify-content-end mb-3">
                <CInput
                    type="select"
                    className="page-size-selection"
                    value={pageSize}
                    onChange={(e) => {
                        setPageSize(Number(e.target.value));
                    }}>
                    {[5, 10, 20, 30, 40, 50].map((pageSize) => (
                        <option key={pageSize} value={pageSize}>
                            Show {pageSize}
                        </option>
                    ))}
                </CInput>
            </div>
            <div className="d-flex justify-content-center mb-3">
                <CPagination className="pagination-wrapper" custom>
                    <CPaginationItem onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                        First
                    </CPaginationItem>
                    <CPaginationItem onClick={() => previousPage()} disabled={!canPreviousPage}>
                        Previous
                    </CPaginationItem>
                    {[...Array(pageCount)].map((_, i) => (
                        <CPaginationItem key={i} onClick={() => gotoPage(i)}>
                            {i + 1}
                        </CPaginationItem>
                    ))}
                    <CPaginationItem onClick={() => nextPage()} disabled={!canNextPage}>
                        Next
                    </CPaginationItem>
                    <CPaginationItem
                        onClick={() => gotoPage(pageCount - 1)}
                        disabled={!canNextPage}>
                        Last
                    </CPaginationItem>
                </CPagination>
            </div>
            <CRow className="justify-content-center">
                <CCol sm={10}>
                    <CTable custom responsive hover {...getTableProps()}>
                        <thead>
                            {headerGroups.map((headerGroup, index) => (
                                <tr key={index} {...headerGroup.getHeaderGroupProps()}>
                                    {headerGroup.headers.map((column) => {
                                        if (column.id === 'name') {
                                            return (
                                                <th {...column.getHeaderProps()}>
                                                    {column.render('Header')}
                                                    <div>
                                                        {column.canFilter
                                                            ? column.render('Filter')
                                                            : null}
                                                    </div>
                                                </th>
                                            );
                                        } else if (column.id === 'action') {
                                            return (
                                                <th
                                                    {...column.getHeaderProps()}
                                                    className="text-center">
                                                    {column.render('Header')}
                                                </th>
                                            );
                                        } else {
                                            return (
                                                <th {...column.getHeaderProps()}>
                                                    {column.render('Header')}
                                                </th>
                                            );
                                        }
                                    })}
                                </tr>
                            ))}
                        </thead>
                        <tbody {...getTableBodyProps()}>
                            {page.map((row, index) => {
                                prepareRow(row);
                                return (
                                    <tr key={index} {...row.getRowProps()}>
                                        {row.cells.map((cell, i) => {
                                            return (
                                                <td key={i} {...cell.getCellProps()}>
                                                    {cell.render('Cell')}
                                                </td>
                                            );
                                        })}
                                    </tr>
                                );
                            })}
                        </tbody>
                    </CTable>
                </CCol>
            </CRow>

            <CPagination className="pagination-wrapper mb-lg-5 mt-lg-5" custom>
                <CPaginationItem onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                    First
                </CPaginationItem>
                <CPaginationItem onClick={() => previousPage()} disabled={!canPreviousPage}>
                    Previous
                </CPaginationItem>
                {[...Array(pageCount)].map((_, i) => (
                    <CPaginationItem key={i} onClick={() => gotoPage(i)}>
                        {i + 1}
                    </CPaginationItem>
                ))}
                <CPaginationItem onClick={() => nextPage()} disabled={!canNextPage}>
                    Next
                </CPaginationItem>
                <CPaginationItem onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                    Last
                </CPaginationItem>
            </CPagination>
        </>
    );
}

class Amenities extends Component {
    state = {
        selectedAmenityId: '',
        showModal: false
    };

    componentDidMount() {
        this.props.getAmenityListing();
    }

    requestDeleteAmenity = (id) => {
        this.toggleModal();
        this.setState({ selectedAmenityId: id });
    };

    confirmDelete = () => {
        this.toggleModal();
        this.props.deleteAmenity(this.state.selectedAmenityId);
    };

    toggleModal = () => {
        this.setState((prevState) => ({
            showModal: !prevState.showModal
        }));
    };

    render() {
        const { items, loading } = this.props.amenities;

        const columns = [
            {
                Header: 'Name',
                accessor: 'name',
                width: 300,
                Cell: ({ row: { original } }) => (
                    <Link to={`/admin/amenities/${original.id}`}>{original.name}</Link>
                )
            },
            {
                Header: 'Image',
                accessor: 'image',
                width: 300,
                Cell: ({ row: { original } }) =>
                    original.image && (
                        <div className="table-img-thumbnail">
                            <img alt={original.name} src={original.image.file} />
                        </div>
                    )
            },
            {
                Header: 'Action',
                accessor: 'action',
                width: 400,
                Cell: ({ row: { original } }) => (
                    <CRow>
                        <CCol xl={6} className="text-center">
                            <Link to={`/admin/amenities/${original.id}`}>
                                <CButton color="dark" className="m-2">
                                    Edit
                                </CButton>
                            </Link>
                        </CCol>
                        <CCol xl={6} className="text-center">
                            <CButton
                                color="danger"
                                className="m-2"
                                onClick={() => this.requestDeleteAmenity(original.id)}>
                                {original.deleting ? 'Deleting...' : 'Delete'}
                            </CButton>
                        </CCol>
                    </CRow>
                )
            }
        ];

        return (
            <div className="animated fadeIn">
                <CModal show={this.state.showModal} toggle={this.toggleModal}>
                    <CModalHeader>Are you sure?</CModalHeader>
                    <CModalFooter>
                        <CButton color="warning" onClick={this.confirmDelete}>
                            Delete
                        </CButton>
                        <CButton color="secondary" onClick={this.toggleModal}>
                            Cancel
                        </CButton>
                    </CModalFooter>
                </CModal>
                <CContainer>
                    <CRow className="justify-content-center">
                        <CCol>
                            <CCard custom>
                                <CCardHeader>
                                    <h4>Amenities</h4>
                                </CCardHeader>
                                <CCardBody>
                                    <CRow>
                                        <CCol sm={12} className="justify-content-end">
                                            <CLink to="/admin/amenities/new">
                                                <CButton color="primary" className="mr-lg-5">
                                                    <CIcon
                                                        name="cil-plus"
                                                        className="c-d-dark-none"
                                                        alt="add"
                                                    />
                                                    Add new
                                                </CButton>
                                            </CLink>
                                        </CCol>
                                    </CRow>
                                    {loading === true ? (
                                        <CircleLoader />
                                    ) : items && items.length > 0 ? (
                                        <DataTable columns={columns} data={items} />
                                    ) : (
                                        <div>No amenities found</div>
                                    )}
                                </CCardBody>
                            </CCard>
                        </CCol>
                    </CRow>
                </CContainer>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    amenities: state.amenities
});

const actionCreators = {
    getAmenityListing: amenityAction.getAmenityListing,
    deleteAmenity: amenityAction.deleteAmenity
};

export default connect(mapStateToProps, actionCreators)(Amenities);
