import React from 'react';
import { CModal, CModalBody } from '@coreui/react';
import PropertyGallery from './PropertyGallery';

export default function PropertyGalleryModal({ show, images, toggle }) {
    return (
        <CModal className="property-browse-modal" show={show} toggle={toggle}>
            <CModalBody className="property-browse-modal">
                {images && <PropertyGallery images={images} />}
            </CModalBody>
        </CModal>
    );
}
