import { amenityConstants } from '../_constants';

export function amenities(state = {}, action) {
    switch (action.type) {
        case amenityConstants.GET_ALL_REQUEST:
            return {
                ...state,
                loading: true
            };
        case amenityConstants.GET_ALL_SUCCESS:
            return {
                ...state,
                items: action.items,
                loading: false
            };
        case amenityConstants.GET_ALL_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case amenityConstants.GET_AMENITY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case amenityConstants.GET_AMENITY_SUCCESS:
            return {
                ...state,
                activeItem: action.amenity,
                loading: false
            };
        case amenityConstants.GET_AMENITY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case amenityConstants.CREATE_AMENITY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case amenityConstants.CREATE_AMENITY_SUCCESS:
            return {
                ...state,
                activeItem: action.amenity,
                loading: false
            };
        case amenityConstants.CREATE_AMENITY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case amenityConstants.UPDATE_AMENITY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case amenityConstants.UPDATE_AMENITY_SUCCESS:
            return {
                ...state,
                activeItem: action.amenity,
                loading: false
            };
        case amenityConstants.UPDATE_AMENITY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case amenityConstants.DELETE_AMENITY_REQUEST:
            return {
                ...state,
                items: state.items.map((amenity) =>
                    amenity.id === action.id ? { ...amenity, deleting: true } : amenity
                )
            };
        case amenityConstants.DELETE_AMENITY_SUCCESS:
            return {
                ...state,
                items: state.items.filter((amenity) => amenity.id !== action.id)
            };
        case amenityConstants.DELETE_AMENITY_FAILURE:
            return {
                ...state,
                items: state.items.map((amenity) => {
                    if (amenity.id === action.id) {
                        // make copy of user without 'deleting:true' property
                        const { deleting, ...amenityCopy } = amenity;
                        return { ...amenityCopy };
                    }

                    return amenity;
                }),
                error: action.error
            };
        default:
            return state;
    }
}
