import React from 'react';
import {
    CRow,
    CCol,
    CButton,
    CModal,
    CModalHeader,
    CModalBody,
    CTabPane,
    CTabContent,
    CNav,
    CNavItem,
    CNavLink,
    CInput,
    CFormGroup,
    CLabel
} from '@coreui/react';
import MediaSidebar from './property/MediaSidebar';
import DropZoneUploader from './property/DropZoneUploader';
import InfiniteScroll from 'react-infinite-scroll-component';
import config from '../../_config/config';

import CircleLoader from '../common/Loaders/CircleLoader';
import { authHeader } from '../../_helpers';
import { userActions } from '../../_actions';
import { connect } from 'react-redux';

class AmenityMediaManager extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
            activeTab: '2',
            items: [],
            activeItem: '',
            selectedItem: [],
            hasMore: true,
            nextHref: null
        };
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.modal !== this.state.modal && this.state.modal === true) {
            this.setState({
                items: [],
                selectedItem: this.props.selectedItem
            });
            this.fetchInitialData();
        }

        if (
            this.state.modal === true &&
            prevState.activeTab !== this.state.activeTab &&
            this.state.activeTab === '2'
        ) {
            this.setState({
                items: [],
                selectedItem: this.props.selectedItem
            });
            this.fetchInitialData();
        }
        // update list after new file is created or updated
        if (this.state.modal === true && this.props.activeItem !== prevProps.activeItem) {
            this.setState({
                items: [],
                selectedItem: this.props.selectedItem
            });
            this.fetchInitialData();
        }
    }

    fetchInitialData = () => {
        fetch(`${config.apiEndpoint}/media/list`, { headers: authHeader() })
            .then((res) => {
                if (res.status === 401) {
                    userActions.logout();
                }
                return res.json();
            })
            .then(
                (res) => {
                    this.setState({
                        items: res.results,
                        nextHref: res.next
                    });
                },
                (error) => {}
            );
    };

    fetchItems = () => {
        fetch(`${this.state.nextHref}`, { headers: authHeader() })
            .then((res) => {
                if (res.status === 401) {
                    this.props.logout();
                }
                return res.json();
            })
            .then(
                (res) => {
                    if (res.next) {
                        this.setState({
                            items: this.state.items.concat(res.results),
                            nextHref: res.next
                        });
                    } else {
                        this.setState({
                            items: this.state.items.concat(res.results),
                            hasMore: false
                        });
                    }
                },
                (error) => {
                    console.log('error', error);
                }
            );
    };

    toggleModal = () => {
        this.setState({ modal: !this.state.modal });
    };

    toggleTab = (tabIndex) => {
        this.setState({ activeTab: tabIndex });
    };

    handleImageClick = (image) => {
        this.setState({
            activeItem: image,
            selectedItem: image
        });
    };

    handleSaveSelectedItem = () => {
        this.toggleModal();
        this.props.onSelectItem(this.state.selectedItem);
    };

    handleChangeImageDetail = (e) => {
        const { activeItem } = this.state;
        activeItem[e.target.name] = e.target.value;
        this.setState({ activeItem: activeItem });
    };

    handleSaveImageDetail = (image) => {
        const { items } = this.state;
        items.find((item) => {
            if (item.id === image.id) {
                item.title = image.title;
                item.caption = image.caption;
            }
        });
        this.setState({ items: items });
    };

    handleDeleteImage = (id) => {
        this.setState({ activeItem: '' });
        let { items } = this.state;
        this.setState({ items: items.filter((item) => item.id !== id) });
    };

    handleUploadSuccess = (res) => {
        console.log(res);
    };

    render() {
        const { openButtonText } = this.props;
        const { selectedItem } = this.state;
        return (
            <div>
                <CButton onClick={this.toggleModal} color="primary" className="mr-1 my-3">
                    {openButtonText ? openButtonText : 'media manager'}
                </CButton>
                <CModal show={this.state.modal} toggle={this.toggleModal} className="media-modal">
                    <CModalHeader toggle={this.toggleModal}>Add Media</CModalHeader>
                    <CModalBody>
                        <CNav variant="tabs">
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '1'}
                                    onClick={() => {
                                        this.toggleTab('1');
                                    }}>
                                    Upload Files
                                </CNavLink>
                            </CNavItem>
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '2'}
                                    onClick={() => {
                                        this.toggleTab('2');
                                    }}>
                                    Media Library
                                </CNavLink>
                            </CNavItem>
                        </CNav>
                        <CTabContent activeTab={this.state.activeTab}>
                            <CTabPane tabId="1">
                                <DropZoneUploader onUploadSuccess={this.handleUploadSuccess} />
                                <CFormGroup row hidden>
                                    <CCol md="3">
                                        <CLabel className="custom-file" htmlFor="custom-file-input">
                                            Custom file input
                                        </CLabel>
                                    </CCol>
                                    <CCol xs="12" md="9">
                                        <CLabel className="custom-file">
                                            <CInput
                                                className="custom-file"
                                                type="file"
                                                id="custom-file-input"
                                                name="file-input"
                                            />
                                        </CLabel>
                                    </CCol>
                                </CFormGroup>
                            </CTabPane>
                            <CTabPane tabId="2">
                                <CRow>
                                    <CCol sm="10">
                                        {this.state.items && (
                                            <InfiniteScroll
                                                className="row infinite-scroll-wrapper"
                                                dataLength={this.state.items.length}
                                                next={this.fetchItems}
                                                hasMore={this.state.hasMore}
                                                loader={<CircleLoader />}
                                                height={500}>
                                                {this.state.items.map((item, index) => {
                                                    let active = item.id === selectedItem.id;
                                                    return (
                                                        <div
                                                            key={index}
                                                            className="gallery-item"
                                                            onClick={() =>
                                                                this.handleImageClick(item)
                                                            }>
                                                            <div>
                                                                <div
                                                                    style={{
                                                                        backgroundImage: `url(${item.file})`,
                                                                        backgroundPosition:
                                                                            'center',
                                                                        backgroundSize: 'cover',
                                                                        height: '100px'
                                                                    }}
                                                                    className={
                                                                        active === true
                                                                            ? 'item-active'
                                                                            : ''
                                                                    }></div>
                                                            </div>
                                                        </div>
                                                    );
                                                })}
                                            </InfiniteScroll>
                                        )}
                                    </CCol>
                                    <CCol sm={2}>
                                        <MediaSidebar
                                            image={this.state.activeItem}
                                            onChangeImageDetail={this.handleChangeImageDetail}
                                            onSaveImageDetail={this.handleSaveImageDetail}
                                            onDeleteImage={this.handleDeleteImage}
                                        />
                                    </CCol>
                                </CRow>
                                <CRow className="media-modal-action">
                                    <CButton
                                        color="primary"
                                        className="mr-1 mt-lg-5"
                                        onClick={this.handleSaveSelectedItem}>
                                        Set image
                                    </CButton>
                                </CRow>
                            </CTabPane>
                        </CTabContent>
                    </CModalBody>
                </CModal>
            </div>
        );
    }
}

function mapState(state) {
    const { media } = state;
    return {
        activeItem: media.activeItem
    };
}

const actionCreators = {
    logout: userActions.logout
};

export default connect(mapState, actionCreators)(AmenityMediaManager);
