import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ScrollableAnchor from 'react-scrollable-anchor';
import BostonImage from '../../../assets/location_boston.jpg';
import BrooklineImage from '../../../assets/location_brookline.jpg';
import CambridgeImage from '../../../assets/location_cambridge.jpg';
import QuincyImage from '../../../assets/location_quincy.jpg';

export default class ExploreLocations extends Component {
    state = {
        locations: [
            {
                title: 'Boston',
                img: BostonImage
            },
            {
                title: 'Brookline',
                img: BrooklineImage
            },
            {
                title: 'Cambridge',
                img: CambridgeImage
            },
            {
                title: 'Quincy',
                img: QuincyImage
            }
        ]
    };

    render() {
        return (
            <ScrollableAnchor id={'explore-locations'}>
                <section className="explore-locations-block">
                    <h2>Explore Our Locations </h2>
                    <div className="container">
                        <div className="row">
                            {this.state.locations &&
                                this.state.locations.map((location, index) => {
                                    const bgStyle = {
                                        backgroundImage: `url(${location.img})`,
                                        backgroundSize: `cover`,
                                        backgroundPosition: `center`
                                    };
                                    return (
                                        <div key={index} className="col-md-6 col-lg-3">
                                            <div className="location-card my-3 mx-auto">
                                                <div
                                                    className="bg-box"
                                                    style={{
                                                        backgroundImage: `url(${location.img})`
                                                    }}
                                                />
                                                <Link
                                                    to={`/locations#${location.title.toLowerCase()}`}>
                                                    {location.title}
                                                </Link>
                                            </div>
                                        </div>
                                    );
                                })}
                        </div>
                    </div>
                </section>
            </ScrollableAnchor>
        );
    }
}
