import { categoryConstants } from '../_constants';

export function categories(state = {}, action) {
    switch (action.type) {
        case categoryConstants.GET_CATEGORIES_REQUEST:
            return {
                ...state,
                loading: true
            };
        case categoryConstants.GET_CATEGORIES_SUCCESS:
            return {
                ...state,
                items: action.items,
                loading: false
            };
        case categoryConstants.GET_CATEGORIES_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case categoryConstants.GET_CATEGORY_ITEMS_REQUEST:
            return {
                ...state,
                loading: true
            };
        case categoryConstants.GET_CATEGORY_ITEMS_SUCCESS:
            return {
                ...state,
                items: action.items,
                loading: false
            };
        case categoryConstants.GET_CATEGORY_ITEMS_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case categoryConstants.GET_CATEGORY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case categoryConstants.GET_CATEGORY_SUCCESS:
            return {
                ...state,
                activeItem: action.category,
                loading: false
            };
        case categoryConstants.GET_CATEGORY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case categoryConstants.CREATE_CATEGORY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case categoryConstants.CREATE_CATEGORY_SUCCESS:
            return {
                ...state,
                activeItem: action.category,
                loading: false
            };
        case categoryConstants.CREATE_CATEGORY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case categoryConstants.UPDATE_CATEGORY_REQUEST:
            return {
                ...state,
                loading: true
            };
        case categoryConstants.UPDATE_CATEGORY_SUCCESS:
            return {
                ...state,
                activeItem: action.category,
                loading: false
            };
        case categoryConstants.UPDATE_CATEGORY_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case categoryConstants.DELETE_CATEGORY_REQUEST:
            return {
                ...state,
                items: state.items.map((category) =>
                    category.id === action.id ? { ...category, deleting: true } : category
                )
            };
        case categoryConstants.DELETE_CATEGORY_SUCCESS:
            return {
                ...state,
                items: state.items.filter((category) => category.id !== action.id)
            };
        case categoryConstants.DELETE_CATEGORY_FAILURE:
            return {
                ...state,
                items: state.items.map((category) => {
                    if (category.id === action.id) {
                        // make copy of user without 'deleting:true' property
                        const { deleting, ...categoryCopy } = category;
                        return { ...categoryCopy };
                    }

                    return category;
                }),
                error: action.error
            };
        default:
            return state;
    }
}
