import React from 'react';
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';
import { CButton, CCol, CInput, CModal, CModalBody, CRow } from '@coreui/react';
import { mediaAction } from '../../../_actions/media.actions';
import { connect } from 'react-redux';

class MediaSidebar extends React.Component {
    state = {
        image: {},
        modal: false,
        crop: {
            unit: '%',
            width: 80,
            height: 40
            // aspect: 16 / 9,
        },
        croppedImage: null
    };

    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        if (this.state.image !== nextProps.image) {
            this.setState({ image: nextProps.image });
        }
    }

    handleChangeImageDetail = (e) => {
        const { image } = this.state;
        image[e.target.name] = e.target.value;
        this.setState({ image });
    };

    handleSaveImageDetail = (image) => {
        this.props.updateMedia({
            id: image.id,
            title: image.title,
            caption: image.caption
        });
        this.props.onSaveImageDetail(image);
    };

    handleSaveCroppedImage = (image) => {
        this.setState({ modal: !this.state.modal });
        if (this.state.croppedImage) {
            this.props.createMedia({
                id: image.id,
                title: image.title,
                imageData: this.state.croppedImage,
                caption: image.caption
            });
        }
    };

    handleDeleteImage = (id) => {
        this.props.deleteMedia(id);
        this.setState({ image: null });
        this.props.onDeleteImage(id);
    };

    // onSelectFile = e => {
    //     if (e.target.files && e.target.files.length > 0) {
    //         const reader = new FileReader()
    //         reader.addEventListener('load', () =>
    //             this.setState({src: reader.result})
    //         )
    //         reader.readAsDataURL(e.target.files[0])
    //     }
    // }

    onImageLoaded = (image) => {
        this.imageRef = image;
    };

    onCropComplete = (crop) => {
        this.makeClientCrop(crop);
    };

    onCropChange = (crop, percentCrop) => {
        this.setState({ crop });
    };

    async makeClientCrop(crop) {
        if (this.imageRef && crop.width && crop.height) {
            const croppedImage = await MediaSidebar.getCroppedImg(this.imageRef, crop, 'crop.jpeg');
            this.setState({ croppedImage });
        }
    }

    static getCroppedImg(image, crop, fileName) {
        const canvas = document.createElement('canvas');
        const scaleX = image.naturalWidth / image.width;
        const scaleY = image.naturalHeight / image.height;
        canvas.width = Math.ceil(crop.width * scaleX);
        canvas.height = Math.ceil(crop.height * scaleY);
        const ctx = canvas.getContext('2d');
        ctx.drawImage(
            image,
            crop.x * scaleX,
            crop.y * scaleY,
            crop.width * scaleX,
            crop.height * scaleY,
            0,
            0,
            crop.width * scaleX,
            crop.height * scaleY
        );

        return canvas.toDataURL('image/jpeg');

        // return new Promise((resolve, reject) => {
        //     canvas.toBlob(blob => {
        //         if (!blob) {
        //             //reject(new Error('Canvas is empty'))
        //             console.error('Canvas is empty')
        //             return
        //         }
        //         blob.name = fileName
        //         window.URL.revokeObjectURL(this.fileUrl)
        //         this.fileUrl = window.URL.createObjectURL(blob)
        //         resolve(this.fileUrl)
        //     }, 'image/jpeg')
        // })
    }

    render() {
        const { image, crop, croppedImage } = this.state;
        return (
            <div className="media-sidebar">
                {image !== null ? (
                    <div>
                        <div className="media-sidebar-thumbnail-wrapper">
                            <img
                                className="media-sidebar-thumbnail"
                                src={image.file}
                                alt={image.title}
                            />
                        </div>
                        {this.props.showCrop === true && image.file && (
                            <CButton
                                size="sm"
                                onClick={() => {
                                    this.setState({ modal: !this.state.modal });
                                }}
                                color="primary"
                                className="mr-1">
                                Crop image
                            </CButton>
                        )}

                        <CModal
                            show={this.state.modal}
                            toggle={() => {
                                this.setState({ modal: !this.state.modal });
                            }}
                            className="media-modal">
                            <CModalBody className="crop-modal">
                                <CRow>
                                    <CCol sm={8}>
                                        {image && (
                                            <ReactCrop
                                                className="d-block mx-auto"
                                                src={image.file}
                                                crossorigin="*"
                                                crop={crop}
                                                ruleOfThirds
                                                onImageLoaded={this.onImageLoaded}
                                                onComplete={this.onCropComplete}
                                                onChange={this.onCropChange}
                                            />
                                        )}
                                    </CCol>
                                    <CCol sm={4}>
                                        {croppedImage && (
                                            <img
                                                alt="Crop"
                                                style={{ width: '100%' }}
                                                crossOrigin="*"
                                                src={croppedImage}
                                            />
                                        )}
                                    </CCol>
                                </CRow>
                                <div>
                                    <CButton
                                        color="light"
                                        className="crop-modal-action mt-3"
                                        onClick={() => {
                                            this.setState({ modal: !this.state.modal });
                                        }}>
                                        Cancel
                                    </CButton>
                                    <CButton
                                        color="primary"
                                        className="crop-modal-action mr-5 mt-3"
                                        onClick={() => this.handleSaveCroppedImage(image)}>
                                        Save
                                    </CButton>
                                </div>
                            </CModalBody>
                        </CModal>

                        <CInput
                            type="text"
                            name="title"
                            className="mt-2 mb-2"
                            placeholder="name"
                            value={image.title ? image.title : ''}
                            onChange={this.handleChangeImageDetail}
                        />
                        <CInput
                            type="text"
                            name="caption"
                            className="mt-2 mb-2"
                            placeholder="caption"
                            value={image.caption ? image.caption : ''}
                            onChange={this.handleChangeImageDetail}
                        />
                        <CRow>
                            <CCol sm="6">
                                <CButton
                                    color="primary"
                                    className="mr-1 mt-3"
                                    onClick={() => this.handleSaveImageDetail(image)}>
                                    Save
                                </CButton>
                            </CCol>
                            <CCol sm="6">
                                <CButton
                                    color="warning"
                                    className="mr-1 mt-3"
                                    onClick={() => this.handleDeleteImage(image.id)}>
                                    Delete
                                </CButton>
                            </CCol>
                        </CRow>
                    </div>
                ) : (
                    ''
                )}
            </div>
        );
    }
}

const actionCreators = {
    createMedia: mediaAction.createMedia,
    updateMedia: mediaAction.updateMedia,
    deleteMedia: mediaAction.deleteMedia
};

export default connect(null, actionCreators)(MediaSidebar);
