import config from '../_config/config';
import { authHeader } from '../_helpers';
import { userService } from './user.service';

const apiUrl = `${config.apiEndpoint}/accommodation`;

export const amenityService = {
    getAll,
    getListing,
    create,
    getById,
    update,
    delete: _delete
};

function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`${apiUrl}/amenity/`, requestOptions).then(handleResponse);
}

function getListing() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`${apiUrl}/amenity/listing/`, requestOptions).then(handleResponse);
}

function create(amenity) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(amenity)
    };

    return fetch(`${apiUrl}/amenity/`, requestOptions).then(handleResponse);
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${apiUrl}/amenity/${id}/`, requestOptions).then(handleResponse);
}

function update(amenity) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(amenity)
    };

    return fetch(`${apiUrl}/amenity/${amenity.id}/`, requestOptions).then(handleResponse);
}

function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${apiUrl}/amenity/${id}/`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then((text) => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                userService.logout();
                window.location.reload(true);
            }
            const error = data || response.statusText;
            return Promise.reject(error);
        }
        return data;
    });
}
