import React from 'react';
import { mediaAction } from '../../../_actions';
import { connect } from 'react-redux';
import {
    CButton,
    CCol,
    CFormGroup,
    CInput,
    CLabel,
    CModal,
    CModalBody,
    CModalHeader,
    CNav,
    CNavItem,
    CNavLink,
    CRow,
    CTabContent,
    CTabPane
} from '@coreui/react';
import DropZoneUploader from './DropZoneUploader';
import MediaSidebar from './MediaSidebar';

class EditFeaturedImage extends React.Component {
    state = {
        modal: false,
        activeTab: '2',
        propertyId: '',
        items: [],
        selectedItem: '',
        activeItem: '',
        loading: false
    };

    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.property && nextProps.property.id) {
            this.setState({
                propertyId: nextProps.property.id,
                selectedItem: nextProps.property.featured_img,
                items: nextProps.images
            });
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.modal !== this.state.modal && this.state.modal === true) {
            this.props.getAllMedia(this.state.propertyId);
        }
        if (
            this.state.modal === true &&
            prevState.activeTab !== this.state.activeTab &&
            this.state.activeTab === '2'
        ) {
            this.props.getAllMedia(this.state.propertyId);
        }
        // update list after creating cropped image
        if (this.state.modal === true && prevProps.activeImage !== this.props.activeImage) {
            this.props.getAllMedia(this.state.propertyId);
        }
    }

    handleImageClick = (image) => {
        this.setState({
            activeItem: image,
            selectedItem: image
        });
    };

    handleSaveSelectedItem = () => {
        this.setState({ modal: !this.state.modal });
        this.props.onSave(this.state.selectedItem);
    };

    handleChangeImageDetail = (e) => {
        const { activeItem } = this.state;
        activeItem[e.target.name] = e.target.value;
        this.setState({ activeItem: activeItem });
    };

    handleSaveImageDetail = (image) => {
        const { items } = this.state;
        items.forEach((item) => {
            if (item.id === image.id) {
                item.title = image.title;
                item.caption = image.caption;
            }
        });
        this.setState({ items: items });
    };

    handleDeleteImage = (id) => {
        this.setState({ activeItem: '' });
        const { items } = this.state;
        items.filter((item) => item.id !== id);
        this.setState({ items: items });
    };

    render() {
        return (
            <div>
                <CButton
                    onClick={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    color="primary"
                    className="mr-1">
                    Edit Featured Image
                </CButton>
                <CModal
                    show={this.state.modal}
                    toggle={() => {
                        this.setState({ modal: !this.state.modal });
                    }}
                    className="media-modal">
                    <CModalHeader
                        toggle={() => {
                            this.setState({ modal: !this.state.modal });
                        }}>
                        Featured image
                    </CModalHeader>
                    <CModalBody>
                        <CNav variant="tabs">
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '1'}
                                    onClick={() => {
                                        this.setState({ activeTab: '1' });
                                    }}>
                                    Upload Files
                                </CNavLink>
                            </CNavItem>
                            <CNavItem custom>
                                <CNavLink
                                    active={this.state.activeTab === '2'}
                                    onClick={() => {
                                        this.setState({ activeTab: '2' });
                                    }}>
                                    Media Library
                                </CNavLink>
                            </CNavItem>
                        </CNav>
                        <CTabContent activeTab={this.state.activeTab}>
                            <CTabPane tabId="1">
                                <DropZoneUploader propertyId={this.state.propertyId} />
                                <CFormGroup row hidden>
                                    <CCol md="3">
                                        <CLabel className="custom-file" htmlFor="custom-file-input">
                                            Custom file input
                                        </CLabel>
                                    </CCol>
                                    <CCol xs="12" md="9">
                                        <CLabel className="custom-file">
                                            <CInput
                                                className="custom-file"
                                                type="file"
                                                id="custom-file-input"
                                                name="file-input"
                                            />
                                        </CLabel>
                                    </CCol>
                                </CFormGroup>
                            </CTabPane>
                            <CTabPane tabId="2">
                                <CRow>
                                    <CCol sm="10">
                                        {this.state.items && (
                                            <div className="row media-scroll-gallery">
                                                {this.state.items.map((item, index) => {
                                                    let active = false;
                                                    if (this.state.selectedItem !== null) {
                                                        active =
                                                            this.state.selectedItem.id === item.id;
                                                    }
                                                    return (
                                                        <div
                                                            key={index}
                                                            className="gallery-item"
                                                            onClick={() =>
                                                                this.handleImageClick(item)
                                                            }>
                                                            <div>
                                                                <div
                                                                    style={{
                                                                        backgroundImage: `url(${item.file})`,
                                                                        backgroundPosition:
                                                                            'center',
                                                                        backgroundSize: 'cover',
                                                                        height: '100px'
                                                                    }}
                                                                    className={
                                                                        active === true
                                                                            ? 'item-active'
                                                                            : ''
                                                                    }></div>
                                                            </div>
                                                        </div>
                                                    );
                                                })}
                                            </div>
                                        )}
                                    </CCol>
                                    <CCol sm="2">
                                        <MediaSidebar
                                            showCrop={true}
                                            image={this.state.activeItem}
                                            onChangeImageDetail={this.handleChangeImageDetail}
                                            onSaveImageDetail={this.handleSaveImageDetail}
                                            onDeleteImage={this.handleDeleteImage}
                                        />
                                    </CCol>
                                </CRow>
                                <CRow className="media-modal-action">
                                    <CButton
                                        color="primary"
                                        className="mr-1 mt-3"
                                        onClick={this.handleSaveSelectedItem}>
                                        Save Featured Image
                                    </CButton>
                                </CRow>
                            </CTabPane>
                        </CTabContent>
                    </CModalBody>
                </CModal>
            </div>
        );
    }
}

function mapState(state) {
    const { properties, media } = state;
    return {
        property: properties.activeItem,
        images: media.items,
        activeImage: media.activeItem
    };
}

const actionCreators = {
    getAllMedia: mediaAction.getAllMedia,
    updateMedia: mediaAction.updateMedia,
    updateBulkMedia: mediaAction.updateBulkMedia,
    deleteMedia: mediaAction.deleteMedia,
    addUploadedMedia: mediaAction.addUploadedMedia
};

export default connect(mapState, actionCreators)(EditFeaturedImage);
