import React from 'react';
import { withRouter } from 'react-router-dom';

// components
import ContactInfoForm from '../../../components/guest/ContactInfoForm';
import BookingCheckForm from '../../../components/guest/BookingCheckForm';
import PropertySimpleGallery from '../../../components/guest/property/PropertySimpleGallery';
import TourModal from '../../../components/guest/property/TourModal';
import PropertyGalleryModal from '../../../components/guest/property/PropertyGalleryModal';

// redux
import { propertyAction } from '../../../_actions/property.actions';
import { alertActions, bookingActions } from '../../../_actions';
import { connect } from 'react-redux';
import WithPixel from '../../../components/WithPixel';
import WithGATracker from '../../../components/WithGATracker';
import ReactGA from 'react-ga';

class CheckOutStart extends React.Component {
    state = {
        showGallery: false,
        showIframe: false,
        disableBookingForm: false
    };

    componentDidMount() {
        ReactGA.event({
            category: 'ecommerce',
            action: 'begin_checkout'
        });
        if (
            this.props.history.location.state &&
            this.props.history.location.state.from === this.props.match.params.slug
        ) {
            const { booking } = this.props;
            const fields = [
                'checkinDate',
                'checkoutDate',
                'adultsCount',
                'childrenCount',
                'propertyFee',
                'cleaningFee',
                'refundableAmount',
                'transactionFee',
                'tax',
                'total'
            ];
            if (fields.some((field) => booking[field] === '')) {
                this.props.getPropertyDetail(this.props.match.params.slug);
            } else {
                this.setState({ disableBookingForm: true });
            }
        } else {
            this.props.getPropertyDetail(this.props.match.params.slug);
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.slug !== prevProps.match.params.slug) {
            this.props.getPropertyDetail(this.props.match.params.slug);
        }
    }

    handleSubmit = (fields) => {
        const { booking } = this.props;
        // validation
        if (booking.checkinDate === '' || booking.checkoutDate === '') {
            this.props.alertError('Please select date');
            return;
        }
        if (booking.adultsCount === '' && booking.childrenCount === '') {
            this.props.alertError('Please insert number of people');
            return;
        }

        this.props.addBookingInfo({
            guest: {
                firstName: fields.firstName,
                lastName: fields.lastName,
                email: fields.email,
                phoneNumber: fields.phoneNumber
            },
            bookervilleID: this.props.property.bookerville_id
        });
        this.props.history.push('/checkout-review');
    };

    render() {
        return (
            <>
                <section className="main-background-2"></section>
                <section>
                    <div className="container">
                        <div className="row ">
                            <div className="col-12 col-sm-12 col-xl-7">
                                <div className="d-flex justify-content-between mb-4">
                                    <h3 className="text-blue">Begin your booking</h3>
                                    <p>Step 1 of 3</p>
                                </div>
                                <h5 className="text-darkgray text-blue">Contact Information</h5>
                                <ContactInfoForm
                                    handleSubmit={(fields) => this.handleSubmit(fields)}
                                />
                            </div>
                            <div className="col-12 col-sm-12 col-xl-5">
                                <TourModal
                                    show={this.state.showIframe}
                                    tour360={this.props.property.tour360}
                                    toggle={() =>
                                        this.setState({ showIframe: !this.state.showIframe })
                                    }
                                />
                                <PropertyGalleryModal
                                    show={this.state.showGallery}
                                    images={this.props.property.gallery_imgs}
                                    toggle={() =>
                                        this.setState({ showGallery: !this.state.showGallery })
                                    }
                                />
                                <div className="booking-information">
                                    <div className="position-relative d-flex justify-content-center position-relative mb-4">
                                        <PropertySimpleGallery
                                            images={this.props.property.gallery_imgs}
                                        />
                                        <div className="booking-information-buttons-wrapper">
                                            <button
                                                type="button"
                                                className="btn btn-secondary custom-btn-secondary user-btn user-btn-outline"
                                                onClick={() => {
                                                    this.setState({ showIframe: true });
                                                }}>
                                                <i className="icon360" />
                                            </button>
                                            <button
                                                type="button"
                                                className="btn btn-secondary custom-btn-secondary user-btn user-btn-outline"
                                                onClick={() => {
                                                    this.setState({ showGallery: true });
                                                }}>
                                                Browse Photos
                                            </button>
                                        </div>
                                    </div>
                                    <BookingCheckForm
                                        disableBookingForm={this.state.disableBookingForm}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </>
        );
    }
}

const mapStateToProps = (state) => {
    const { properties, booking } = state;
    return {
        property: properties.activeItem,
        booking: booking
    };
};

const actionCreators = {
    getPropertyDetail: propertyAction.getPropertyDetail,
    addBookingInfo: bookingActions.addBookingInfo,
    alertError: alertActions.error
};

export default connect(
    mapStateToProps,
    actionCreators
)(withRouter(WithPixel(WithGATracker(CheckOutStart))));
