// Define a default UI for filtering
function DefaultColumnFilter({ column: { filterValue, preFilteredRows, setFilter } }) {
    const count = preFilteredRows.length;

    return (
        <div className="mt-3 mb-3">
            <CInput
                value={filterValue || ''}
                onChange={(e) => {
                    setFilter(e.target.value || undefined);
                }}
                placeholder={`Search ${count} records...`}
            />
        </div>
    );
}

function fuzzyTextFilterFn(rows, id, filterValue) {
    return matchSorter(rows, filterValue, { keys: [(row) => row.values[id]] });
}

function PropertyDataTable({ columns, data }) {
    const filterTypes = React.useMemo(
        () => ({
            fuzzyText: fuzzyTextFilterFn,
            text: (rows, id, filterValue) => {
                return rows.filter((row) => {
                    const rowValue = row.values[id];
                    return rowValue !== undefined
                        ? String(rowValue)
                              .toLowerCase()
                              .startsWith(String(filterValue).toLowerCase())
                        : true;
                });
            }
        }),
        []
    );

    const defaultColumn = React.useMemo(
        () => ({
            // Let's set up our default Filter UI
            Filter: DefaultColumnFilter
        }),
        []
    );

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        prepareRow,
        page,
        canPreviousPage,
        canNextPage,
        pageCount,
        gotoPage,
        nextPage,
        previousPage,
        setPageSize,
        state: { pageIndex, pageSize }
    } = useTable(
        {
            columns,
            data,
            defaultColumn, // Be sure to pass the defaultColumn option
            filterTypes,
            initialState: { pageIndex: 0, pageSize: 5 }
        },
        useBlockLayout,
        useFilters, // useFilters!
        useGlobalFilter, // useGlobalFilter!
        useSortBy,
        usePagination
    );

    return (
        <>
            <CRow className="mb-5">
                <CCol sm={2} className="ml-auto justify-content-end">
                    <CInput
                        type="select"
                        className="page-size-selection"
                        value={pageSize}
                        onChange={(e) => {
                            setPageSize(Number(e.target.value));
                        }}>
                        {[5, 10, 20, 30, 40, 50].map((pageSize, index) => (
                            <option key={index} value={pageSize}>
                                Show {pageSize}
                            </option>
                        ))}
                    </CInput>
                </CCol>
                <CCol sm={12}>
                    <CPagination className="pagination-wrapper" custom>
                        <CPaginationItem onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                            First
                        </CPaginationItem>
                        <CPaginationItem onClick={() => previousPage()} disabled={!canPreviousPage}>
                            Previous
                        </CPaginationItem>
                        {[...Array(pageCount)].map((_, i) => (
                            <CPaginationItem key={i} onClick={() => gotoPage(i)}>
                                {i + 1}
                            </CPaginationItem>
                        ))}
                        {/* {
                            canPreviousPage ?
                                <CPaginationItem onClick={() => gotoPage(pageIndex - 1)}>
                                    {pageIndex}
                                </CPaginationItem> : ''
                        }
                        <CPaginationItem onClick={() => gotoPage(pageIndex)}>
                            {pageIndex + 1}
                        </CPaginationItem>
                        {
                            canNextPage ?
                                <CPaginationItem onClick={() => gotoPage(pageIndex + 1)}>
                                    {pageIndex + 2}
                                </CPaginationItem> : ''
                        } */}
                        <CPaginationItem onClick={() => nextPage()} disabled={!canNextPage}>
                            Next
                        </CPaginationItem>
                        <CPaginationItem
                            onClick={() => gotoPage(pageCount - 1)}
                            disabled={!canNextPage}>
                            Last
                        </CPaginationItem>
                    </CPagination>
                </CCol>
            </CRow>
            <CTable custom responsive hover {...getTableProps()}>
                <thead>
                    {headerGroups.map((headerGroup, index) => (
                        <tr key={index} {...headerGroup.getHeaderGroupProps()}>
                            {headerGroup.headers.map((column) => {
                                if (column.id === 'action') {
                                    return (
                                        <th {...column.getHeaderProps()} className="text-center">
                                            {column.render('Header')}
                                        </th>
                                    );
                                } else {
                                    // return (
                                    //     <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                                    //         {column.render('Header')}
                                    //         <span className="ml-3" >
                                    //             {column.isSorted
                                    //                 ? column.isSortedDesc
                                    //                     ? <FontAwesomeIcon icon={faSortUp}/>
                                    //                     : <FontAwesomeIcon icon={faSortDown}/>
                                    //                 : <FontAwesomeIcon icon={faSort}/>}
                                    //           </span>
                                    //         <div>{column.canFilter ? column.render('Filter') : null}</div>
                                    //     </th>
                                    // )
                                    return (
                                        <th {...column.getHeaderProps()}>
                                            {column.render('Header')}
                                            <div>
                                                {column.canFilter ? column.render('Filter') : null}
                                            </div>
                                        </th>
                                    );
                                }
                            })}
                        </tr>
                    ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                    {page.map((row, index) => {
                        prepareRow(row);
                        return (
                            <tr key={index} {...row.getRowProps()}>
                                {row.cells.map((cell, i) => {
                                    return (
                                        <td key={i} {...cell.getCellProps()}>
                                            {cell.render('Cell')}
                                        </td>
                                    );
                                })}
                            </tr>
                        );
                    })}
                </tbody>
            </CTable>
            <CPagination className="pagination-wrapper" custom>
                <CPaginationItem onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                    First
                </CPaginationItem>
                <CPaginationItem onClick={() => previousPage()} disabled={!canPreviousPage}>
                    Previous
                </CPaginationItem>
                {[...Array(pageCount)].map((_, i) => (
                    <CPaginationItem key={i} onClick={() => gotoPage(i)}>
                        {i + 1}
                    </CPaginationItem>
                ))}
                <CPaginationItem onClick={() => nextPage()} disabled={!canNextPage}>
                    Next
                </CPaginationItem>
                <CPaginationItem onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                    Last
                </CPaginationItem>
            </CPagination>
        </>
    );
}

class Properties extends Component {
    state = {
        selectedPropertyId: '',
        showModal: false
    };

    componentDidMount() {
        this.props.getPropertyList();
    }

    requestDeleteProperty = (id) => {
        this.toggleModal();
        this.setState({ selectedPropertyId: id });
    };

    confirmDelete = () => {
        this.toggleModal();
        this.props.deleteProperty(this.state.selectedPropertyId);
    };

    toggleModal = () => {
        this.setState((prevState) => ({
            showModal: !prevState.showModal
        }));
    };

    render() {
        const { items, loading } = this.props.properties;

        const columns = [
            {
                Header: 'Title',
                accessor: 'name',
                width: 400,
                sortType: 'basic',
                Cell: ({ row: { original } }) => (
                    <Link to={`/admin/properties/${original.id}`}>{original.name}</Link>
                )
            },
            {
                Header: 'Address',
                accessor: 'address',
                width: 300,
                sortType: 'basic'
            },
            {
                Header: 'BookervilleID',
                accessor: 'bookervilleID',
                width: 200,
                sortType: 'basic'
            },
            {
                Header: 'Updated date',
                accessor: 'updated_date',
                width: 300,
                sortType: 'basic'
            },
            {
                Header: 'Action',
                accessor: 'action',
                minWidth: 300,
                Cell: ({ row: { original } }) => (
                    <CRow>
                        <CCol lg={6} className="text-center">
                            <Link to={`/admin/properties/${original.slug}`}>
                                <CButton color="dark" className="m-2">
                                    Edit
                                </CButton>
                            </Link>
                        </CCol>
                        <CCol lg={6} className="text-center">
                            <CButton
                                color="danger"
                                className="m-2"
                                onClick={() => this.requestDeleteProperty(original.id)}>
                                {original.deleting ? 'Deleting...' : 'Delete'}
                            </CButton>
                        </CCol>
                    </CRow>
                )
            }
        ];

        return (
            <div className="animated fadeIn">
                <CModal show={this.state.showModal} toggle={this.toggleModal}>
                    <CModalHeader>Are you sure?</CModalHeader>
                    <CModalFooter>
                        <CButton color="warning" onClick={this.confirmDelete}>
                            Delete
                        </CButton>
                        <CButton color="secondary" onClick={this.toggleModal}>
                            Cancel
                        </CButton>
                    </CModalFooter>
                </CModal>
                <CRow>
                    <CCol xl={12}>
                        <CCard custom>
                            <CCardHeader>
                                <h4>Properties</h4>
                            </CCardHeader>
                            <CCardBody>
                                <CRow>
                                    <CCol sm={12} className=" justify-content-end">
                                        <CLink to="/admin/properties/new">
                                            <CButton color="primary" className="mr-lg-5">
                                                <CIcon
                                                    name="cil-plus"
                                                    className="c-d-dark-none"
                                                    alt="add"
                                                />
                                                Add new
                                            </CButton>
                                        </CLink>
                                    </CCol>
                                </CRow>
                                {loading === true ? (
                                    <CircleLoader />
                                ) : (
                                    <PropertyDataTable columns={columns} data={items} />
                                )}
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

function mapState(state) {
    const { properties } = state;
    return { properties };
}

const actionCreators = {
    getPropertyList: propertyAction.getPropertyList,
    deleteProperty: propertyAction.deleteProperty
};

export default connect(mapState, actionCreators)(Properties);
