import 'font-awesome/css/font-awesome.min.css';
import 'react-datepicker/dist/react-datepicker.css';

import React from 'react';
import DatePicker from 'react-datepicker';
import DatePickerInput from './DatePickerInput';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAsterisk, faUser } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';

export default function PropertySearchForm(props) {
    const {
        checkinDate,
        checkoutDate,
        adultsCount,
        childrenCount,
        handleCheckInChange,
        handleCheckOutChange,
        handleAdultsCountChange,
        handleChildrenCountChange,
        handleBrowseButtonClick,
        handleSearchButtonClick
    } = props;
    return (
        <div className="container">
            <div className="property-search-box">
                <div className="row">
                    <div className="col-md-4 col-md-4 d-flex flex-column">
                        <div>
                            <DatePicker
                                selected={checkinDate}
                                value={checkinDate}
                                minDate={new Date()}
                                className="form-control custom-form-control "
                                dateFormat="MM/dd/yyyy"
                                placeholderText="Check In"
                                customInput={<DatePickerInput />}
                                onChange={handleCheckInChange}
                            />
                        </div>
                        <div>
                            <DatePicker
                                selected={checkoutDate}
                                value={checkoutDate}
                                minDate={checkinDate === '' ? new Date() : checkinDate}
                                className="form-control custom-form-control "
                                dateFormat="MM/dd/yyyy"
                                placeholderText="Check Out"
                                customInput={<DatePickerInput />}
                                onChange={handleCheckOutChange}
                            />
                        </div>
                    </div>
                    <div className="col-md-4 d-flex flex-column">
                        <div className="input-group mb-3">
                            <div className=" input-group-append">
                                <span className="input-group-text bg-white">
                                    <FontAwesomeIcon icon={faUser} />
                                </span>
                            </div>
                            <input
                                type="text"
                                className="form-control custom-form-control"
                                placeholder="Adults"
                                value={adultsCount}
                                onChange={handleAdultsCountChange}
                                aria-label="adults"
                                aria-describedby="adults"
                            />
                        </div>
                        <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                <span className="input-group-text bg-white">
                                    <FontAwesomeIcon icon={faUser} />
                                </span>
                            </div>
                            <input
                                type="text"
                                className="form-control custom-form-control"
                                placeholder="Children"
                                value={childrenCount}
                                onChange={handleChildrenCountChange}
                                aria-label="Children"
                                aria-describedby="children"
                            />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="d-flex flex-column btn-wrapper">
                            <button
                                type="button"
                                className="btn btn-block btn-outline-primary user-btn user-btn-outline w-100 mt-0 mb-3"
                                onClick={handleBrowseButtonClick}>
                                Browse all rentals
                            </button>
                            <button
                                type="button"
                                className="btn btn-block btn-outline-primary user-btn user-btn-outline w-100 mt-0 mb-3"
                                onClick={handleSearchButtonClick}>
                                Search
                            </button>
                        </div>
                    </div>
                    <div className="col-12 d-block text-center">
                        <p>
                            <FontAwesomeIcon icon={faAsterisk} /> Please{' '}
                            <Link to={'/contact'}>contact us</Link> for the best available price if
                            you want to book for a month or more.
                        </p>
                        <p>
                            <FontAwesomeIcon icon={faAsterisk} /> Our pricing is seasonal and
                            subject to change.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}
