import { textConstants } from '../_constants';

const initialState = { items: [], activeItem: null, loading: false };

export function texts(state = initialState, action) {
    switch (action.type) {
        case textConstants.GET_ALL_REQUEST:
            return {
                ...state,
                loading: true
            };
        case textConstants.GET_ALL_SUCCESS:
            return {
                ...state,
                items: action.items,
                loading: false
            };
        case textConstants.GET_ALL_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case textConstants.GET_TEXT_REQUEST:
            return {
                ...state,
                loading: true
            };
        case textConstants.GET_TEXT_SUCCESS:
            return {
                ...state,
                activeItem: action.text,
                loading: false
            };
        case textConstants.GET_TEXT_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case textConstants.CREATE_TEXT_REQUEST:
            return {
                ...state,
                loading: true
            };
        case textConstants.CREATE_TEXT_SUCCESS:
            return {
                ...state,
                activeItem: action.text,
                loading: false
            };
        case textConstants.CREATE_TEXT_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case textConstants.UPDATE_TEXT_REQUEST:
            return {
                ...state,
                loading: true
            };
        case textConstants.UPDATE_TEXT_SUCCESS:
            return {
                ...state,
                activeItem: action.text,
                loading: false
            };
        case textConstants.UPDATE_TEXT_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.error
            };
        case textConstants.DELETE_TEXT_REQUEST:
            return {
                ...state,
                items: state.items.map((content) =>
                    content.id === action.id ? { ...content, deleting: true } : content
                )
            };
        case textConstants.DELETE_TEXT_SUCCESS:
            return {
                ...state,
                items: state.items.filter((content) => content.id !== action.id)
            };
        case textConstants.DELETE_TEXT_FAILURE:
            return {
                ...state,
                items: state.items.map((content) => {
                    if (content.id === action.id) {
                        const { deleting, ...contentCopy } = content;
                        return { ...contentCopy };
                    }

                    return content;
                }),
                error: action.error
            };
        default:
            return state;
    }
}
