import React from 'react';
import Home from '../views/GuestPages/Home/Home';
import Locations from '../views/GuestPages/Locations/Locations';
import Medical from '../views/GuestPages/Medical-Corporate/Medical';
import PropertyListing from '../views/GuestPages/Property/PropertyListing';
import CheckOutStart from '../views/GuestPages/CheckOut/CheckOutStart';
import CheckOutReview from '../views/GuestPages/CheckOut/CheckOutReview';
import CheckOutPayment from '../views/GuestPages/CheckOut/CheckOutPayment';
import About from '../views/GuestPages/About/About';
import ComingSoon from '../views/GuestPages/Others/ComingSoon';
import ContactUs from '../views/GuestPages/ContactUs/ContactUs';
import PrivacyPolicy from '../views/GuestPages/Others/PrivacyPolicy';
import CancellationPolicy from '../views/GuestPages/Others/CancellationPolicy';
import Terms from '../views/GuestPages/Others/Terms';
import PropertyDetail from '../views/GuestPages/Property/PropertyDetail';
import CheckOutSuccess from '../views/GuestPages/CheckOut/CheckOutSuccess';
import CookiePolicy from '../views/GuestPages/Others/CookiePolicy';
import Application from './../views/GuestPages/Application/Application';

const guestRoutes = [
    { path: '/', exact: true, name: Home, component: Home },
    { path: '/locations', exact: true, name: 'Locations', component: Locations },
    { path: '/medical', exact: true, name: 'Medical', component: Medical },
    { path: '/property', exact: true, name: 'PropertyListing', component: PropertyListing },
    { path: '/property/:slug', exact: true, name: 'Property Details', component: PropertyDetail },
    { path: '/checkout/:slug', exact: true, name: 'CheckOut Start', component: CheckOutStart },
    { path: '/quick-book/:slug', exact: true, name: 'CheckOut Start', component: CheckOutStart },
    { path: '/checkout-review', exact: true, name: 'CheckOut Review', component: CheckOutReview },
    {
        path: '/checkout-payment',
        exact: true,
        name: 'CheckOut Payment',
        component: CheckOutPayment
    },
    {
        path: '/checkout-success',
        exact: true,
        name: 'CheckOut Payment',
        component: CheckOutSuccess
    },
    { path: '/about', exact: true, name: 'About', component: About },
    { path: '/services', exact: true, name: 'Services', component: ComingSoon },
    { path: '/blog', exact: true, name: 'Blog', component: ComingSoon },
    { path: '/coming-soon', exact: true, name: 'Blog', component: ComingSoon },
    { path: '/terms-and-conditions', exact: true, name: 'Terms', component: Terms },
    { path: '/privacy-policy', exact: true, name: 'PrivacyPolicy', component: PrivacyPolicy },
    { path: '/cookie-policy', exact: true, name: 'PrivacyPolicy', component: CookiePolicy },
    {
        path: '/cancellation-policy',
        exact: true,
        name: 'CancellationPolicy',
        component: CancellationPolicy
    },
    { path: '/contact', exact: true, name: 'ContactUs', component: ContactUs },
    { path: '/form', exact: true, name: 'Application', component: Application }
];

export default guestRoutes;
