import config from '../_config/config';
import { userService } from './user.service';

export const bookingService = {
    getBookingQuote,
    addBookingPayment
};

function addBookingPayment(booking) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(booking)
    };

    return fetch(`${config.apiEndpoint}/accommodation/booking/add_payment`, requestOptions).then(
        handleResponse
    );
}

function getBookingQuote({ checkin_date, checkout_date, bookervilleID }) {
    const requestOptions = {
        method: 'POSt',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ checkin_date, checkout_date, bookervilleID })
    };

    return fetch(`${config.apiEndpoint}/accommodation/booking/get_quote`, requestOptions).then(
        handleResponse
    );
}

function handleResponse(response) {
    return response.text().then((text) => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            console.log('handleResponse', response, data);

            if (response.status === 401) {
                userService.logout();
                window.location.reload(true);
            }

            const error = getObjectValues(data) || response.statusText;
            return Promise.reject(error);
        }
        return data;
    });
}

function getObjectValues(obj) {
    return obj && typeof obj === 'object' ? Object.values(obj).map(getObjectValues).flat() : [obj];
}
