function getPasswordResetToken(email) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email })
    };

    return fetch(`${appConfig.apiEndpoint}/auth/password/reset/`, requestOptions).then(
        handleResponse
    );
}

function confirmPasswordChange(data) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
    };

    return fetch(`${appConfig.apiEndpoint}/auth/password/reset/confirm/`, requestOptions).then(
        handleResponse
    );
}

function sendContactEmail(content) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(content)
    };
    return fetch(`${appConfig.apiEndpoint}/users/contact_email`, requestOptions).then(
        handleResponse
    );
}

function sendApplicationEmail(content) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(content)
    };
    return fetch(`${appConfig.apiEndpoint}/users/apply_email`, requestOptions).then(handleResponse);
}

function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };
    return fetch(`${appConfig.apiEndpoint}/users/`, requestOptions).then(handleResponse);
}

function create(user) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(user)
    };

    return fetch(`${appConfig.apiEndpoint}/users/create`, requestOptions).then(handleResponse);
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${appConfig.apiEndpoint}/users/${id}`, requestOptions).then(handleResponse);
}

function register(user) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(user)
    };

    return fetch(`${appConfig.apiEndpoint}/auth/signup`, requestOptions).then(handleResponse);
}

function update(user) {
    const requestOptions = {
        method: 'PATCH',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(user)
    };

    return fetch(`${appConfig.apiEndpoint}/users/${user.id}`, requestOptions).then(handleResponse);
}

function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${appConfig.apiEndpoint}/users/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then((text) => {
        const data = text && JSON.parse(text);
        console.log('+++++++response data ++++++', data);
        if (!response.ok) {
            if (response.status === 401) {
                logout();
                window.location.reload(true);
                localStorage.removeItem('access_token');
                localStorage.removeItem('refresh_token');
            }
            console.log('++++++data++++++', data);
            const error = data || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}
