import React from 'react';
import { CButton, CCard, CCardBody, CCardHeader, CCollapse, CLink } from '@coreui/react';
import CIcon from '@coreui/icons-react/es/CIcon';
import Room from './Room';

export default function BedTypeSelection({
    show,
    toggleCard,
    rooms,
    onRoomChange,
    onAddRoom,
    onRemoveRoom
}) {
    return (
        <CCard custom accentColor="primary" id="bedTypeSelection">
            <CCardHeader>
                Property Bed Type Selection
                <div className="card-header-actions">
                    <CLink
                        className="card-header-action"
                        onClick={(e) => toggleCard(e, 'bedTypeSelection')}>
                        <CIcon name={show ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                    </CLink>
                </div>
            </CCardHeader>
            <CCollapse custom show={show}>
                <CCardBody>
                    {rooms &&
                        rooms.map((room, index) => {
                            if (room.name === '' && room.bed_type === '') return;
                            return (
                                <Room
                                    key={index}
                                    room={room}
                                    onRoomChange={onRoomChange}
                                    onRemoveRoom={onRemoveRoom}
                                />
                            );
                        })}
                    <CButton type="submit" color="info" className="mr-1" onClick={onAddRoom}>
                        Add Bedtype
                    </CButton>
                </CCardBody>
            </CCollapse>
        </CCard>
    );
}
