import React from 'react';
import queryString from 'query-string';
import moment from 'moment';

// property
import PropertySearchForm from '../../../components/guest/PropertySearchForm';
import PropertyCard from '../../../components/guest/PropertyCard';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';
import { Tab, Nav, Container, Row } from 'react-bootstrap';

// redux
import { alertActions } from '../../../_actions/alert.actions';
import { propertyAction } from '../../../_actions/property.actions';
import { categoryAction } from '../../../_actions';
import { connect } from 'react-redux';
import HeaderBanner from '../../../containers/GuestLayout/HeaderBanner';
import WithPixel from '../../../components/WithPixel';
import WithGATracker from '../../../components/WithGATracker';
import ReactGA from 'react-ga';

class PropertyListing extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            queryString: '',
            activeCategory: 'All',
            categories: [],
            checkinDate: '',
            checkoutDate: '',
            adultsCount: '',
            childrenCount: ''
        };
    }

    componentDidMount() {
        this.props.getCategoryListing();
        ReactGA.event({
            category: 'engagement',
            action: 'view_item_list'
        });
        let currentQS = queryString.parse(this.props.location.search);
        if (currentQS.checkin_date !== undefined) {
            this.setState({
                checkinDate: new Date(currentQS.checkin_date),
                checkoutDate: new Date(currentQS.checkout_date),
                adultsCount: currentQS.adults,
                childrenCount: currentQS.children
            });
            this.props.getFilteredListing(currentQS);
        } else if (currentQS.category !== undefined) {
            this.setState({ activeCategory: currentQS.category });
            this.props.getFilteredListing(currentQS);
        } else {
            this.props.getListing();
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        let currentQS = queryString.parse(this.props.location.search);
        let oldQS = queryString.parse(prevProps.location.search);
        let areSameObjects = (a, b) => {
            if (Object.keys(a).length !== Object.keys(b).length) return false;
            for (let key in a) {
                if (a[key] !== b[key]) return false;
            }
            return true;
        };
        if (this.props.location.pathname === '/property' && !areSameObjects(currentQS, oldQS)) {
            this.props.getFilteredListing(currentQS);
        }
        if (this.props.categories !== prevProps.categories) {
            if (this.props.categories !== undefined && this.props.categories.items !== undefined) {
                let categories = [
                    'All',
                    'Private Rooms',
                    'Studios',
                    ...this.props.categories.items
                ];
                this.setState({
                    categories: categories.filter((item, i, ar) => ar.indexOf(item) === i)
                });
            }
        }
    }

    handleFilterClick = (key) => {
        this.setState({ activeCategory: key });
        let queryString = '';
        if (key !== 'All') {
            queryString += `category=${key}`;
        }

        if (this.state.checkinDate !== '') {
            const formattedCheckinDate = moment(this.state.checkinDate).format('YYYY-MM-DD');
            const formattedCheckoutDate = moment(this.state.checkoutDate).format('YYYY-MM-DD');
            queryString += `&checkin_date=${formattedCheckinDate}&checkout_date=${formattedCheckoutDate}&adults=${this.state.adultsCount}&children=${this.state.childrenCount}`;
        }
        this.props.history.push(`/property?${queryString}`);
    };

    handleCheckInChange = (value) => {
        this.setState({
            checkinDate: value
        });
        if (this.state.checkinDate < this.state.checkoutDate) {
            this.setState({ checkoutDate: '' });
        }
    };

    handleCheckOutChange = (value) => {
        this.setState({
            checkoutDate: value
        });
    };

    handleAdultsCountChange = (e) => {
        const reg = /^[0-9\b]+$/;
        if (e.target.value === '' || reg.test(e.target.value)) {
            this.setState({ adultsCount: e.target.value });
        }
    };

    handleChildrenCountChange = (e) => {
        const reg = /^[0-9\b]+$/;
        if (e.target.value === '' || reg.test(e.target.value)) {
            this.setState({ childrenCount: e.target.value });
        }
    };

    handleBrowseButtonClick = (e) => {
        this.props.location.search = {};
        this.setState({
            checkinDate: '',
            checkoutDate: '',
            adultsCount: '',
            childrenCount: ''
        });
        this.props.history.push(`/property`);
        // window.location.reload()
        this.setState({ activeCategory: 'All' });
        this.props.getListing();
    };

    handleSearchButtonClick = (e) => {
        if (this.state.checkinDate === '' && this.state.checkoutDate === '') {
            this.props.alertError('Please choose date ');
            return;
        }
        if (this.state.adultsCount === '' && this.state.childrenCount === '') {
            this.props.alertError('Please insert number of people ');
            return;
        }
        const formattedCheckinDate = moment(this.state.checkinDate).format('YYYY-MM-DD');
        const formattedCheckoutDate = moment(this.state.checkoutDate).format('YYYY-MM-DD');
        let queryString = `checkin_date=${formattedCheckinDate}&checkout_date=${formattedCheckoutDate}&adults=${this.state.adultsCount}&children=${this.state.childrenCount}`;

        ReactGA.event({
            category: 'engagement',
            action: 'search',
            label: 'search rentals'
        });
        ReactGA.event({
            category: 'engagement',
            action: 'view_search_results',
            label: 'view rentals search results'
        });
        this.props.history.push(`/property?${queryString}`);
    };

    render() {
        const { properties } = this.props;

        return (
            <>
                <HeaderBanner className={'main-background-3'} />
                <PropertySearchForm
                    checkinDate={this.state.checkinDate}
                    checkoutDate={this.state.checkoutDate}
                    adultsCount={this.state.adultsCount}
                    childrenCount={this.state.childrenCount}
                    handleCheckInChange={this.handleCheckInChange}
                    handleCheckOutChange={this.handleCheckOutChange}
                    handleAdultsCountChange={this.handleAdultsCountChange}
                    handleChildrenCountChange={this.handleChildrenCountChange}
                    handleBrowseButtonClick={this.handleBrowseButtonClick}
                    handleSearchButtonClick={this.handleSearchButtonClick}
                />
                <section className="property-listing-block">
                    <Container>
                        <Tab.Container
                            id="property-tab"
                            className="row justify-content-between"
                            activeKey={this.state.activeCategory}
                            onSelect={(key) => this.handleFilterClick(key)}>
                            <Row>
                                {this.state.categories.map((category, index) => (
                                    <Nav key={index} className="col-lg col-md-4 px-1 mb-3">
                                        <div
                                            className={
                                                this.state.activeCategory === category
                                                    ? 'property-filter bg-yellow'
                                                    : 'property-filter'
                                            }>
                                            <Nav.Link
                                                eventKey={category}
                                                className="text-blue w-100">
                                                {category}
                                            </Nav.Link>
                                        </div>
                                    </Nav>
                                ))}
                            </Row>
                            <Row>
                                <Tab.Content>
                                    {this.state.categories.map((cat, index) => {
                                        if (cat === 'All') {
                                            return (
                                                <Tab.Pane key={index} eventKey={cat} title={cat}>
                                                    {properties.loading === true ? (
                                                        <CircleLoader />
                                                    ) : properties &&
                                                      properties.items &&
                                                      properties.items.length > 0 ? (
                                                        this.state.categories &&
                                                        this.state.categories.map(
                                                            (category, index) => {
                                                                let filteredProperties = properties.items.filter(
                                                                    (property) =>
                                                                        property.category ===
                                                                        category
                                                                );
                                                                if (filteredProperties.length > 0) {
                                                                    return (
                                                                        <div
                                                                            key={index}
                                                                            className="container">
                                                                            <h2 className="text-blue">
                                                                                {category
                                                                                    .charAt(0)
                                                                                    .toUpperCase() +
                                                                                    category.slice(
                                                                                        1
                                                                                    )}
                                                                            </h2>
                                                                            <hr className="property-category-border" />
                                                                            <div className="row">
                                                                                {filteredProperties &&
                                                                                    filteredProperties.length !==
                                                                                        0 &&
                                                                                    filteredProperties.map(
                                                                                        (
                                                                                            property,
                                                                                            idx
                                                                                        ) => (
                                                                                            <PropertyCard
                                                                                                key={
                                                                                                    index
                                                                                                }
                                                                                                id={
                                                                                                    property.id
                                                                                                }
                                                                                                address={
                                                                                                    property.address
                                                                                                }
                                                                                                title={
                                                                                                    property.name
                                                                                                }
                                                                                                slug={
                                                                                                    property.slug
                                                                                                }
                                                                                                price={
                                                                                                    property.price
                                                                                                }
                                                                                                minPrice={
                                                                                                    property.min_price
                                                                                                }
                                                                                                maxPrice={
                                                                                                    property.max_price
                                                                                                }
                                                                                                minMonthPrice={
                                                                                                    property.min_month_price
                                                                                                }
                                                                                                maxMonthPrice={
                                                                                                    property.max_month_price
                                                                                                }
                                                                                                category={
                                                                                                    property.category
                                                                                                }
                                                                                                bedroomCount={
                                                                                                    property.bedroom_count
                                                                                                }
                                                                                                bathroomCount={
                                                                                                    property.bathroom_count
                                                                                                }
                                                                                                sharedBathroom={
                                                                                                    property.shared_bathroom
                                                                                                }
                                                                                                minSleeps={
                                                                                                    property.min_sleeps
                                                                                                }
                                                                                                sleeps={
                                                                                                    property.sleeps
                                                                                                }
                                                                                                rentalParking={
                                                                                                    property.rental_parking
                                                                                                }
                                                                                                furnished={
                                                                                                    property.furnished
                                                                                                }
                                                                                                petsConsidered={
                                                                                                    property.pets_considered
                                                                                                }
                                                                                                image={
                                                                                                    property.featured_img
                                                                                                }
                                                                                            />
                                                                                        )
                                                                                    )}
                                                                            </div>
                                                                        </div>
                                                                    );
                                                                }
                                                            }
                                                        )
                                                    ) : (
                                                        <div className="col-md">
                                                            <h4>
                                                                Sorry there are no properties
                                                                available for your dates!
                                                            </h4>
                                                        </div>
                                                    )}
                                                </Tab.Pane>
                                            );
                                        } else {
                                            return (
                                                <Tab.Pane key={index} eventKey={cat} title={cat}>
                                                    {properties.loading === true ? (
                                                        <CircleLoader />
                                                    ) : (
                                                        <div className="container">
                                                            <div>
                                                                <h2 className="text-blue">
                                                                    {this.state.activeCategory
                                                                        .charAt(0)
                                                                        .toUpperCase() +
                                                                        this.state.activeCategory.slice(
                                                                            1
                                                                        )}
                                                                </h2>
                                                                <hr className="property-category-border" />
                                                            </div>
                                                            <div className="row">
                                                                {properties &&
                                                                properties.items &&
                                                                properties.items.length > 0 ? (
                                                                    properties.items.map(
                                                                        (property, index) => (
                                                                            <PropertyCard
                                                                                key={index}
                                                                                id={property.id}
                                                                                address={
                                                                                    property.address
                                                                                }
                                                                                title={
                                                                                    property.name
                                                                                }
                                                                                slug={property.slug}
                                                                                price={
                                                                                    property.price
                                                                                }
                                                                                minPrice={
                                                                                    property.min_price
                                                                                }
                                                                                maxPrice={
                                                                                    property.max_price
                                                                                }
                                                                                minMonthPrice={
                                                                                    property.min_month_price
                                                                                }
                                                                                maxMonthPrice={
                                                                                    property.max_month_price
                                                                                }
                                                                                category={
                                                                                    property.category
                                                                                }
                                                                                bedroomCount={
                                                                                    property.bedroom_count
                                                                                }
                                                                                bathroomCount={
                                                                                    property.bathroom_count
                                                                                }
                                                                                sharedBathroom={
                                                                                    property.shared_bathroom
                                                                                }
                                                                                minSleeps={
                                                                                    property.min_sleeps
                                                                                }
                                                                                sleeps={
                                                                                    property.sleeps
                                                                                }
                                                                                rentalParking={
                                                                                    property.rental_parking
                                                                                }
                                                                                furnished={
                                                                                    property.furnished
                                                                                }
                                                                                petsConsidered={
                                                                                    property.pets_considered
                                                                                }
                                                                                image={
                                                                                    property.featured_img
                                                                                }
                                                                            />
                                                                        )
                                                                    )
                                                                ) : (
                                                                    <div className="col-md">
                                                                        <h4>
                                                                            Sorry there are no
                                                                            properties available for
                                                                            your dates!
                                                                        </h4>
                                                                    </div>
                                                                )}
                                                            </div>
                                                        </div>
                                                    )}
                                                </Tab.Pane>
                                            );
                                        }
                                    })}
                                </Tab.Content>
                            </Row>
                        </Tab.Container>
                    </Container>
                </section>
            </>
        );
    }
}

const mapStateToProps = (state) => {
    const { categories, properties } = state;
    return {
        categories,
        properties
    };
};

const mapDispatchToProps = (dispatch) => ({
    getListing: () => dispatch(propertyAction.getListing()),
    getFilteredListing: (terms) => dispatch(propertyAction.getFilteredListing(terms)),
    getCategoryListing: () => dispatch(categoryAction.getCategoryListing()),
    alertError: (msg) => dispatch(alertActions.error(msg))
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(WithPixel(WithGATracker(PropertyListing)));
