import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CRow,
    CTable,
    CModal,
    CModalHeader,
    CModalFooter,
    CLink
} from '@coreui/react';

import { CIcon } from '@coreui/icons-react';
import { categoryAction } from '../../../_actions/index';
import { connect } from 'react-redux';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';

function CategoryRow(props) {
    const { item, index } = props;
    const itemLink = `/admin/categories/${item.id}`;

    return (
        <tr key={item.id.toString()}>
            <td scope="row">
                <Link to={itemLink}>{index}</Link>
            </td>
            <td>
                <Link to={itemLink}>{item.name}</Link>
            </td>
            <td>
                <CRow>
                    <CCol xl={6}>
                        <Link to={itemLink}>
                            <CButton color="dark" className="m-2">
                                Edit
                            </CButton>
                        </Link>
                    </CCol>
                    <CCol xl={6}>
                        <CButton
                            color="danger"
                            className="m-2"
                            onClick={() => props.requestDeleteCategory(item.id)}>
                            {item.deleting ? 'Deleting...' : 'Delete'}
                        </CButton>
                    </CCol>
                </CRow>
            </td>
        </tr>
    );
}

class Categories extends Component {
    state = {
        selectedCategoryId: '',
        showModal: false
    };

    componentDidMount() {
        this.props.getAllCategory();
    }

    requestDeleteCategory = (id) => {
        this.toggleModal();
        this.setState({ selectedCategoryId: id });
    };

    confirmDelete = () => {
        this.toggleModal();
        this.props.deleteCategory(this.state.selectedCategoryId);
    };

    toggleModal = () => {
        this.setState((prevState) => ({
            showModal: !prevState.showModal
        }));
    };

    render() {
        const { categories } = this.props;

        return (
            <div className="animated fadeIn">
                <CModal show={this.state.showModal} toggle={this.toggleModal}>
                    <CModalHeader className="text-center">Are you sure?</CModalHeader>
                    <CModalFooter>
                        <CButton color="warning" onClick={this.confirmDelete}>
                            Delete
                        </CButton>
                        <CButton color="secondary" onClick={this.toggleModal}>
                            Cancel
                        </CButton>
                    </CModalFooter>
                </CModal>
                <CRow>
                    <CCol xl={12}>
                        <CCard custom>
                            <CCardHeader>
                                <h4>Categories</h4>
                            </CCardHeader>
                            <CCardBody>
                                <CLink to="/admin/categories/new">
                                    <CButton
                                        color="primary"
                                        style={{ float: 'right', margin: '0 30px 10px' }}>
                                        <CIcon
                                            name="cil-plus"
                                            className="c-d-dark-none"
                                            alt="add"
                                        />
                                        Add new
                                    </CButton>
                                </CLink>
                                <CTable custom responsive hover>
                                    <thead>
                                        <tr>
                                            <th scope="col">ID</th>
                                            <th scope="col">Title</th>
                                            <th
                                                scope="col"
                                                style={{ width: '20%', textAlign: 'center' }}>
                                                Action
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {categories.items &&
                                            categories.items.map((category, index) => (
                                                <CategoryRow
                                                    key={index}
                                                    index={index + 1}
                                                    item={category}
                                                    requestDeleteCategory={(id) =>
                                                        this.requestDeleteCategory(id)
                                                    }
                                                />
                                            ))}
                                    </tbody>
                                </CTable>
                                {categories.loading && <CircleLoader />}
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

function mapState(state) {
    const { categories } = state;
    return { categories };
}

const actionCreators = {
    getAllCategory: categoryAction.getAllCategory,
    deleteCategory: categoryAction.deleteCategory
};

export default connect(mapState, actionCreators)(Categories);
