import React from 'react';
import { CCard, CCardBody, CCardHeader, CCollapse, CLink, CTable } from '@coreui/react';
import CIcon from '@coreui/icons-react/es/CIcon';

export default function SimilarPropertySelection({
    show,
    toggleCard,
    properties,
    similarProperties,
    onSimilarPropertyChange
}) {
    return (
        <CCard custom accentColor="primary" id="bedTypeSelection">
            <CCardHeader>
                Similar Properties
                <div className="card-header-actions">
                    <CLink
                        className="card-header-action"
                        onClick={(e) => toggleCard(e, 'similarPropertySelection')}>
                        <CIcon name={show ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                    </CLink>
                </div>
            </CCardHeader>
            <CCollapse custom show={show}>
                <CCardBody>
                    <CTable custom responsive hover>
                        <thead>
                            <tr>
                                <th scope="col"></th>
                                <th scope="col">ID</th>
                                <th scope="col">Title</th>
                                <th scope="col">Address</th>
                            </tr>
                        </thead>
                        <tbody>
                            {properties &&
                                properties.map((item, index) => {
                                    item.checked = !!(
                                        similarProperties &&
                                        similarProperties.find((obj) => obj === item.id)
                                    );
                                    return (
                                        <tr key={index}>
                                            <td scope="row">
                                                <input
                                                    type="checkbox"
                                                    checked={item.checked}
                                                    value={item.id}
                                                    onChange={onSimilarPropertyChange}
                                                />
                                            </td>
                                            <td>{item.id}</td>
                                            <td>{item.name}</td>
                                            <td>{item.address}</td>
                                        </tr>
                                    );
                                })}
                        </tbody>
                    </CTable>
                </CCardBody>
            </CCollapse>
        </CCard>
    );
}
