import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CForm,
    CFormFeedback,
    CFormGroup,
    CInput,
    CLabel,
    CRow
} from '@coreui/react';

import { amenityAction } from '../../../_actions/amenity.actions';
import { connect } from 'react-redux';
import * as Yup from 'yup';
import { Formik } from 'formik';
import AmenityMediaManager from '../../../components/admin/AmenityMediaManager';

class AmenityCreate extends Component {
    state = {
        name: '',
        image: ''
    };

    handleSelectImage = (image) => {
        this.setState({ image: image });
    };

    handleSubmit = (fields) => {
        const amenity = {
            ...fields,
            image: this.state.image
        };
        this.props.createAmenity(amenity, this.props.history);
    };

    render() {
        const { loading } = this.props;

        return (
            <div className="animated fadeIn">
                <CRow>
                    <CCol lg={6} sm={12} className="mx-auto">
                        <CCard custom>
                            <CCardHeader>
                                <h4>Create Amenity</h4>
                            </CCardHeader>
                            <CCardBody>
                                <Formik
                                    initialValues={{
                                        name: ''
                                    }}
                                    validationSchema={Yup.object().shape({
                                        name: Yup.string().required('Name is required')
                                    })}
                                    onSubmit={(fields) => this.handleSubmit(fields)}>
                                    {(props) => {
                                        const {
                                            values,
                                            touched,
                                            errors,
                                            handleChange,
                                            handleBlur,
                                            isValid,
                                            handleSubmit,
                                            handleReset
                                        } = props;
                                        return (
                                            <CForm
                                                onSubmit={handleSubmit}
                                                noValidate
                                                name="amenityForm">
                                                <CFormGroup className="mb-3">
                                                    <CLabel for="name">Amenity Name</CLabel>
                                                    <CInput
                                                        type="text"
                                                        name="name"
                                                        id="name"
                                                        valid={!errors.name}
                                                        invalid={touched.name && !!errors.name}
                                                        autoFocus={true}
                                                        required
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.name}
                                                    />
                                                    <CFormFeedback>{errors.name}</CFormFeedback>
                                                </CFormGroup>
                                                {this.state.image && (
                                                    <div
                                                        style={{
                                                            backgroundImage: `url(${this.state.image.file})`,
                                                            backgroundPosition: 'center',
                                                            backgroundSize: 'cover',
                                                            height: '200px',
                                                            width: '50%',
                                                            marginBottom: '10px'
                                                        }}
                                                    />
                                                )}
                                                <AmenityMediaManager
                                                    openButtonText={
                                                        this.state.image
                                                            ? 'Change Image'
                                                            : 'Add Image'
                                                    }
                                                    selectedItem={this.state.image}
                                                    onSelectItem={this.handleSelectImage}
                                                />
                                                <CRow>
                                                    <CCol xs="12" sm="6" className="text-center">
                                                        <CButton
                                                            type="submit"
                                                            color="primary"
                                                            className="mr-1"
                                                            disabled={!isValid || loading}>
                                                            {loading ? 'Saving' : 'Save'}
                                                        </CButton>
                                                    </CCol>
                                                    <CCol xs="12" sm="6" className="text-center">
                                                        <CButton
                                                            type="reset"
                                                            color="danger"
                                                            className="mr-1"
                                                            onClick={handleReset}>
                                                            Reset
                                                        </CButton>
                                                    </CCol>
                                                </CRow>
                                            </CForm>
                                        );
                                    }}
                                </Formik>
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    loading: state.amenities.loading
});

const actionCreators = {
    createAmenity: amenityAction.createAmenity
};

export default connect(mapStateToProps, actionCreators)(withRouter(AmenityCreate));
