import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CRow,
    CTable,
    CModal,
    CModalBody,
    CModalFooter,
    CLink
} from '@coreui/react';

import { CIcon } from '@coreui/icons-react';
import { connect } from 'react-redux';
import { textAction } from '../../../_actions/text.actions';

function TextRow(props) {
    const item = props.item;
    const itemLink = `/admin/text/${item.id}`;

    return (
        <tr>
            <td scope="row">
                <Link to={itemLink}>{item.id}</Link>
            </td>
            <td>{item.title}</td>
            <td>{item.text}</td>
            <td>
                <CRow>
                    <CCol xl={6}>
                        <Link to={itemLink}>
                            <CButton color="dark" className="m-2">
                                <CIcon name="cil-pencil" className="c-d-dark-none" alt="add" />
                                Edit
                            </CButton>
                        </Link>
                    </CCol>
                    <CCol xl={6}>
                        <CButton
                            color="danger"
                            className="m-2"
                            onClick={() => props.requestDeleteText(item.id)}>
                            {item.deleting ? 'Deleting...' : 'Delete'}
                        </CButton>
                    </CCol>
                </CRow>
            </td>
        </tr>
    );
}

class TextList extends Component {
    state = {
        selectedTextId: '',
        showModal: false
    };

    componentDidMount() {
        this.props.getAllText();
    }

    requestDeleteText = (id) => {
        this.toggleModal();
        this.setState({ selectedTextId: id });
    };

    confirmDelete = () => {
        this.toggleModal();
        this.props.deleteText(this.state.selectedTextId);
    };

    toggleModal = () => {
        this.setState((prevState) => ({
            showModal: !prevState.showModal
        }));
    };

    render() {
        const { texts } = this.props;

        return (
            <div className="animated fadeIn">
                <CModal show={this.state.showModal} onClose={this.toggleModal}>
                    <CModalBody>Are you sure?</CModalBody>
                    <CModalFooter>
                        <CButton color="primary" onClick={this.confirmDelete}>
                            Delete
                        </CButton>
                        <CButton color="secondary" onClick={this.toggleModal}>
                            Cancel
                        </CButton>
                    </CModalFooter>
                </CModal>
                <CRow>
                    <CCol xl={12}>
                        <CCard custom>
                            <CCardHeader>texts</CCardHeader>
                            <CCardBody>
                                <CLink to="/admin/texts/new">
                                    <CButton
                                        color="primary"
                                        style={{ float: 'right', margin: '0 30px 10px' }}>
                                        <CIcon
                                            name="cil-plus"
                                            className="c-d-dark-none"
                                            alt="add"
                                        />
                                        Add new
                                    </CButton>
                                </CLink>

                                <CTable custom responsive hover>
                                    <thead>
                                        <tr>
                                            <th scope="col">ID</th>
                                            <th scope="col">Title</th>
                                            <th scope="col">Text</th>
                                            <th
                                                scope="col"
                                                style={{ width: '20%', textAlign: 'center' }}>
                                                Action
                                            </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {texts.items &&
                                            texts.items.map((text, index) => (
                                                <TextRow
                                                    key={index}
                                                    item={text}
                                                    requestDeleteText={(id) =>
                                                        this.requestDeleteText(id)
                                                    }
                                                />
                                            ))}
                                    </tbody>
                                </CTable>

                                {texts.loading && (
                                    <div className="animated fadeIn pt-3 text-center">
                                        <div className="sk-spinner sk-spinner-pulse"></div>
                                    </div>
                                )}
                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

function mapState(state) {
    const { texts } = state;
    return { texts };
}

const actionCreators = {
    getAllText: textAction.getAllTexts,
    getText: textAction.getText
};

export default connect(mapState, actionCreators)(TextList);
