import React from 'react';
import {
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CCollapse,
    CFormGroup,
    CInput,
    CInputGroup,
    CInputGroupAddon,
    CInputGroupText,
    CLabel,
    CLink,
    CRow
} from '@coreui/react';
import CIcon from '@coreui/icons-react/es/CIcon';

export default function PropertySetting({
    show,
    toggleCard,
    setting,
    onInputChange,
    onCheckboxChange
}) {
    return (
        <CCard custom accentColor="primary" id="setting">
            <CCardHeader>
                Setting
                <div className="card-header-actions">
                    <CLink className="card-header-action" onClick={(e) => toggleCard(e, 'setting')}>
                        <CIcon name={show ? 'cil-chevron-bottom' : 'cil-chevron-top'} />
                    </CLink>
                </div>
            </CCardHeader>
            <CCollapse custom show={show}>
                <CCardBody>
                    <CRow>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="price">
                                    Price <span className="required">*</span>
                                </CLabel>
                                <CInputGroup>
                                    <CInputGroupAddon addonType="prepend">
                                        <CInputGroupText>$</CInputGroupText>
                                    </CInputGroupAddon>
                                    <CInput
                                        type="text"
                                        id="price"
                                        name="price"
                                        pattern="[0-9]*"
                                        value={setting.price ? setting.price : ''}
                                        required
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="minPrice">
                                    Starting nightly Price <span className="required">*</span>
                                </CLabel>
                                <CInputGroup>
                                    <CInputGroupAddon addonType="prepend">
                                        <CInputGroupText>$</CInputGroupText>
                                    </CInputGroupAddon>
                                    <CInput
                                        type="text"
                                        id="minPrice"
                                        name="minPrice"
                                        value={setting.minPrice ? setting.minPrice : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="minPrice">
                                    Starting Monthly Price <span className="required">*</span>
                                </CLabel>
                                <CInputGroup>
                                    <CInputGroupAddon addonType="prepend">
                                        <CInputGroupText>$</CInputGroupText>
                                    </CInputGroupAddon>
                                    <CInput
                                        type="text"
                                        id="minMonthPrice"
                                        name="minMonthPrice"
                                        value={setting.minMonthPrice ? setting.minMonthPrice : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="bedroomCount">
                                    Bedroom <span className="required">*</span>
                                </CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="bedroomCount"
                                        name="bedroomCount"
                                        required
                                        value={setting.bedroomCount ? setting.bedroomCount : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="bathroomCount">
                                    Bathroom <span className="required">*</span>
                                </CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="bathroomCount"
                                        name="bathroomCount"
                                        required
                                        value={setting.bathroomCount ? setting.bathroomCount : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CLabel htmlFor="sharedBathroom">
                                Shared Bathroom <span className="required">*</span>
                            </CLabel>
                            <br />
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio1"
                                    name="sharedBathroom"
                                    value={1}
                                    checked={setting.sharedBathroom === true}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" htmlFor="inline-radio1">
                                    Yes
                                </CLabel>
                            </CFormGroup>
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio2"
                                    name="sharedBathroom"
                                    value={0}
                                    checked={setting.sharedBathroom === false}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" check htmlFor="inline-radio2">
                                    No
                                </CLabel>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="sleeps">
                                    Sleeps <span className="required">*</span>
                                </CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="sleeps"
                                        name="sleeps"
                                        required
                                        value={setting.sleeps ? setting.sleeps : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="minSleeps">Minimum number of nights</CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="minSleeps"
                                        name="minSleeps"
                                        required
                                        value={setting.minSleeps ? setting.minSleeps : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="cleaningFee">Cleaning fee</CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="cleaningFee"
                                        name="cleaningFee"
                                        value={setting.cleaningFee ? setting.cleaningFee : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="transactionFeeRate">Transaction fee(%)</CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="transactionFeeRate"
                                        name="transactionFeeRate"
                                        value={
                                            setting.transactionFeeRate
                                                ? setting.transactionFeeRate
                                                : ''
                                        }
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="taxRate">Tax(%)</CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="taxRate"
                                        name="taxRate"
                                        value={setting.taxRate ? setting.taxRate : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="refundableAmount">
                                    Refundable Security Deposit
                                </CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="refundableAmount"
                                        name="refundableAmount"
                                        value={
                                            setting.refundableAmount ? setting.refundableAmount : ''
                                        }
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol sm={6} lg={4}>
                            <CFormGroup>
                                <CLabel htmlFor="tour360">Tour360</CLabel>
                                <CInputGroup>
                                    <CInput
                                        type="text"
                                        id="tour360"
                                        name="tour360"
                                        value={setting.tour360 ? setting.tour360 : ''}
                                        onChange={onInputChange}
                                    />
                                </CInputGroup>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                    <CRow>
                        <CCol sm={6} lg={4}>
                            <CLabel htmlFor="furnished">
                                Furnished <span className="required">*</span>
                            </CLabel>
                            <br />
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio1"
                                    name="furnished"
                                    value={1}
                                    checked={setting.furnished === true}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" htmlFor="inline-radio1">
                                    Yes
                                </CLabel>
                            </CFormGroup>
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio2"
                                    name="furnished"
                                    value={0}
                                    checked={setting.furnished === false}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" check htmlFor="inline-radio2">
                                    No
                                </CLabel>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CLabel htmlFor="rentalParking">
                                Rental Parking <span className="required">*</span>
                            </CLabel>
                            <br />
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio1"
                                    name="rentalParking"
                                    value={1}
                                    checked={setting.rentalParking === true}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" htmlFor="inline-radio1">
                                    Yes
                                </CLabel>
                            </CFormGroup>
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio2"
                                    name="rentalParking"
                                    value={0}
                                    checked={setting.rentalParking === false}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" check htmlFor="inline-radio2">
                                    No
                                </CLabel>
                            </CFormGroup>
                        </CCol>
                        <CCol sm={6} lg={4}>
                            <CLabel htmlFor="petsConsidered">
                                Pets considered <span className="required">*</span>
                            </CLabel>
                            <br />
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio1"
                                    name="petsConsidered"
                                    value={1}
                                    checked={setting.petsConsidered === true}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" htmlFor="inline-radio1">
                                    Yes
                                </CLabel>
                            </CFormGroup>
                            <CFormGroup check inline>
                                <CInput
                                    className="form-check-input"
                                    type="radio"
                                    id="inline-radio2"
                                    name="petsConsidered"
                                    value={0}
                                    checked={setting.petsConsidered === false}
                                    onChange={onCheckboxChange}
                                />
                                <CLabel className="form-check-label" check htmlFor="inline-radio2">
                                    No
                                </CLabel>
                            </CFormGroup>
                        </CCol>
                    </CRow>
                </CCardBody>
            </CCollapse>
        </CCard>
    );
}
