import React, { Component } from 'react';
import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CInput,
    CRow,
    CCollapse,
    CLink,
    CTabPane,
    CTabContent,
    CNav,
    CNavItem,
    CNavLink
} from '@coreui/react';
import 'quill/dist/quill.snow.css';

import CIcon from '@coreui/icons-react/es/CIcon';
import TextEditor from '../../../components/admin/property/TextEditor';
import BedTypeSelection from '../../../components/admin/property/BedTypeSelection';
import AmenitySelection from '../../../components/admin/property/AmenitySelection';
import LocationWrapper from '../../../components/admin/property/LocationSelection';
import PropertySetting from '../../../components/admin/property/PropertySetting';
import SimilarPropertySelection from '../../../components/admin/property/SimilarPropertySelection';
import EditImage from '../../../components/admin/property/EditFeaturedImage';
import EditGallery from '../../../components/admin/property/EditGallery';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';
import PropertyCalendar from '../../../components/admin/property/PropertyCalendar';
import PropertyCarousel from '../../../components/admin/property/PropertyCarousel';

import { alertActions, categoryAction, mediaAction } from '../../../_actions';
import { amenityAction } from '../../../_actions/amenity.actions';
import { propertyAction } from '../../../_actions/property.actions';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

class PropertyEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeTab: '1',
            properties: [],
            categories: [],
            amenities: [],
            images: [],
            cards: {
                description: true,
                neighbourhood: true,
                transit: true,
                setting: true,
                bedTypeSelection: true,
                propertyId: true,
                amenitySelection: true,
                similarPropertySelection: true,
                category: true
            },
            accommodation: {
                name: '',
                category: 0,
                bookervilleID: '',
                description: '',
                neighbourhood: '',
                transit: '',
                address: '',
                location: {},
                rooms: [],
                amenities: [],
                galleryImgs: [],
                featuredImg: '',
                similarProperties: [],
                setting: {
                    price: '',
                    minPrice: '',
                    maxPrice: '',
                    minMonthPrice: '',
                    maxMonthPrice: '',
                    bathroomCount: '',
                    bedroomCount: '',
                    sleeps: '',
                    minSleeps: 2,
                    cleaningFee: '',
                    transactionFeeRate: '',
                    taxRate: '',
                    tour360: '',
                    refundableAmount: '',
                    furnished: '',
                    rentalParking: '',
                    petsConsidered: ''
                }
            },
            loading: false
        };
    }

    componentDidMount() {
        this.props.getAllCategory();
        this.props.getAllAmenity();
        this.props.getProperties();
        this.props.getProperty(this.props.match.params.id);
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.properties !== this.props.properties) {
            this.setState({ properties: this.props.properties });
        }
        if (prevProps.categories !== this.props.categories) {
            this.setState({ categories: this.props.categories });
        }
        if (prevProps.amenities !== this.props.amenities) {
            this.setState({ amenities: this.props.amenities });
        }
        if (
            prevProps.property.id !== this.props.property.id ||
            prevProps.property.slug !== this.props.property.slug
        ) {
            const { property } = this.props;
            const accommodation = {
                id: property.id,
                bookervilleID: property.bookerville_id,
                name: property.name,
                slug: property.slug,
                category: property.category,
                description: property.description,
                neighbourhood: property.neighbourhood,
                transit: property.transit,
                address: property.address,
                location: {
                    lat: property.lat,
                    lng: property.lng
                },
                rooms: property.property_rooms,
                featuredImg: property.featured_img,
                galleryImgs: property.gallery_imgs,
                amenities: property.amenities,
                similarProperties: property.similar_properties,
                setting: {
                    price: property.price,
                    minPrice: property.min_price,
                    maxPrice: property.max_price,
                    minMonthPrice: property.min_month_price,
                    maxMonthPrice: property.max_month_price,
                    bathroomCount: property.bathroom_count,
                    sharedBathroom: property.shared_bathroom,
                    bedroomCount: property.bedroom_count,
                    sleeps: property.sleeps,
                    minSleeps: property.min_sleeps,
                    cleaningFee: property.cleaning_fee,
                    transactionFeeRate: property.transactionfee_rate,
                    taxRate: property.tax_rate,
                    refundableAmount: property.refundable_amount,
                    furnished: property.furnished,
                    rentalParking: property.rental_parking,
                    petsConsidered: property.pets_considered,
                    tour360: property.tour360
                }
            };
            this.setState({
                accommodation: accommodation
            });
        }
    }

    toggleCard = (e, key) => {
        const { cards } = this.state;
        console.log('++++++toggle cards+++++++', this.state.cards[key]);
        const collapse = this.state.cards[key];
        this.setState({ ...this.state, cards: { ...cards, [key]: !collapse } });
        console.log('++++++toggle cards+++++++', this.state);
    };

    toggleTab = (tabIndex) => {
        this.setState({
            activeTab: tabIndex
        });
    };

    handleTextChange = (title, value) => {
        console.log('__title, value__', title, value);
        const { accommodation } = this.state;
        accommodation[title] = value;
        this.setState({
            ...this.state,
            accommodation: accommodation
        });
        console.log('__handleTextChange__', this.state.accommodation);
    };

    handleInputChange = (e) => {
        const { accommodation } = this.state;
        const intRegExp = new RegExp('^[0-9\b]+$');

        if (e.target.name === 'bookervilleID') {
            if (e.target.value === '' || intRegExp.test(e.target.value)) {
                accommodation[e.target.name] = e.target.value;
            } else {
                accommodation[e.target.name] = this.state.accommodation[e.target.name];
            }
        } else {
            accommodation[e.target.name] = e.target.value;
        }

        this.setState({
            ...this.state,
            accommodation: accommodation
        });
        console.log('__handleInputChange__', this.state.accommodation);
    };

    handleSettingChange = (e) => {
        const { setting } = this.state.accommodation;
        const intRegExp = new RegExp('^[0-9\b]+$');
        const floatRegExp = new RegExp('^[+-]?([0-9]+([.][0-9]*)?|[.][0-9]+)$');

        const intFields = [
            'price',
            'minPrice',
            'maxPrice',
            'minMonthPrice',
            'maxMonthPrice',
            'bedroomCount',
            'sleeps',
            'minSleeps',
            'cleaningFee',
            'refundableAmount'
        ];
        const floatFields = ['transactionFeeRate', 'taxRate', 'bathroomCount'];

        if (floatFields.indexOf(e.target.name) >= 0) {
            if (e.target.value === '' || floatRegExp.test(e.target.value)) {
                setting[e.target.name] = e.target.value;
            } else {
                setting[e.target.name] = this.state.accommodation.setting[e.target.name];
            }
        } else if (intFields.indexOf(e.target.name) >= 0) {
            if (e.target.value === '' || intRegExp.test(e.target.value)) {
                setting[e.target.name] = e.target.value;
            } else {
                setting[e.target.name] = this.state.accommodation.setting[e.target.name];
            }
        } else {
            setting[e.target.name] = e.target.value;
        }

        this.setState({
            ...this.state,
            accommodation: {
                ...this.state.accommodation,
                setting
            }
        });
        console.log('__handleSettingChange__', this.state.accommodation.setting);
    };

    handleCheckboxChange = (e) => {
        const { accommodation } = this.state;
        if (e.target.value === '1') {
            accommodation.setting[e.target.name] = true;
        }
        if (e.target.value === '0') {
            accommodation.setting[e.target.name] = false;
        }

        this.setState({
            ...this.state,
            accommodation: accommodation
        });
        console.log('+++++++++++++++++++++++handleCheckboxChange+++++++++++++++++++++', this.state);
    };

    handlePlaceChange = (place) => {
        const { accommodation } = this.state;
        accommodation.address = place.formatted_address;
        accommodation.location = {
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng()
        };
        this.setState({ accommodation: accommodation });
        console.log('++++++++handlePlaceChange+++++++++++', this.state);
    };

    handlePlaceReset = () => {
        const { accommodation } = this.state;
        accommodation.address = '';
        accommodation.location = {};
        this.setState({ accommodation });
    };

    handleRoomChange = (e, id) => {
        const { accommodation } = this.state;
        accommodation.rooms.filter((room, index) => {
            if (room.id === id) {
                room[e.target.name] = e.target.value;
            }
            console.log('+++++room++++++', room);
        });
        this.setState({ accommodation: accommodation });
        console.log('++++++++change rooms+++++', this.state.accommodation.rooms);
    };

    handleAddRoom = () => {
        const { accommodation } = this.state;
        const id = `_${accommodation.rooms.length + 1}`;
        accommodation.rooms.push({
            id: id,
            name: '',
            bed_type: 'Q'
        });
        this.setState({ accommodation: accommodation });
        console.log('+++++++add room+++++++', this.state.accommodation.rooms);
    };

    handleRemoveRoom = (room) => {
        const { accommodation } = this.state;
        const index = this.state.accommodation.rooms.indexOf(room);
        // accommodation.rooms.splice(index, 1)
        accommodation.rooms[index].name = '';
        accommodation.rooms[index].bed_type = '';
        if (index > -1) {
            this.setState({ accommodation: accommodation });
        }
        console.log('+++++++remove room+++++++', room, this.state.accommodation.rooms);
    };

    handleAmenitySelect = (amenity) => {
        let { accommodation } = this.state;
        if (amenity.checked === true) {
            accommodation.amenities = accommodation.amenities.filter(
                (item) => item.id !== amenity.id
            );
        } else {
            accommodation.amenities.push(amenity);
        }
        this.setState({ accommodation });
    };

    handleChangeFeaturedImage = (image) => {
        const { accommodation } = this.state;
        accommodation.featuredImg = image;
        this.setState({ accommodation: accommodation });
        this.handleSubmit();
        console.log('+++++handleChangeFeaturedImage+++++', image, this.state);
    };

    handleChangeGalleryImages = (images) => {
        const { accommodation } = this.state;
        accommodation.galleryImgs = images;
        this.setState({ accommodation: accommodation });
        console.log('+++++handleChangeGalleryImages+++++', images, this.state);
        this.handleSubmit();
    };

    handleSimilarPropertyChange = (e) => {
        let id = parseInt(e.target.value);
        const { accommodation } = this.state;

        if (this.state.accommodation.similarProperties.includes(id)) {
            accommodation.similarProperties = accommodation.similarProperties.filter(
                (item) => item !== id
            );
        } else {
            accommodation.similarProperties.push(id);
        }
        this.setState({ accommodation: accommodation });
        console.log(
            '++++++++++++++++handleSimilarPropertyChange++++++++++++++++++',
            id,
            this.state.accommodation.similarProperties
        );
    };

    handleSubmit = () => {
        this.state.accommodation.rooms.map((item) => {
            // delete id for new room
            if (typeof item.id === 'string') delete item.id;
        });
        let errors = '',
            settingsFields = [
                'price',
                'minPrice',
                'minMonthPrice',
                'bathroomCount',
                'bedroomCount',
                'sleeps',
                'minSleeps',
                'furnished',
                'rentalParking',
                'petsConsidered'
            ],
            propertyFields = [
                'name',
                'category',
                'bookervilleID',
                'description',
                'neighbourhood',
                'transit',
                'featuredImg'
            ];

        settingsFields.map((field) => {
            if (this.state.accommodation.setting[field] === '') {
                switch (field) {
                    case 'minPrice':
                        errors += `Starting nightly price is required<br>`;
                        break;
                    case 'minMonthPrice':
                        errors += `Starting monthly price is required<br>`;
                        break;
                    default:
                        errors += `${
                            field.charAt(0).toUpperCase() + field.slice(1)
                        } is required<br>`;
                }
            }
        });

        propertyFields.map((field) => {
            if (
                this.state.accommodation[field] === '' ||
                this.state.accommodation[field] === null
            ) {
                if (field === 'featuredImg') {
                    errors += `Featured image is required`;
                } else {
                    errors += `${field.charAt(0).toUpperCase() + field.slice(1)} is required<br>`;
                }
            }
        });

        if (errors !== '') {
            errors = '<div class="text-left">' + errors + '</div>';
            this.props.alertError(errors);
        } else {
            const property = {
                id: this.state.accommodation.id,
                bookerville_id: this.state.accommodation.bookervilleID,
                name: this.state.accommodation.name,
                category: this.state.accommodation.category,
                address: this.state.accommodation.address,
                lat: this.state.accommodation.location.lat,
                lng: this.state.accommodation.location.lng,
                description: this.state.accommodation.description,
                neighbourhood: this.state.accommodation.neighbourhood,
                transit: this.state.accommodation.transit,
                price: this.state.accommodation.setting.price,
                min_price: this.state.accommodation.setting.minPrice,
                max_price: this.state.accommodation.setting.price,
                min_month_price: this.state.accommodation.setting.minMonthPrice,
                max_month_price: this.state.accommodation.setting.price * 30,
                bedroom_count: this.state.accommodation.setting.bedroomCount,
                bathroom_count: this.state.accommodation.setting.bathroomCount,
                similar_properties: this.state.accommodation.similarProperties,
                sleeps: this.state.accommodation.setting.sleeps,
                min_sleeps: this.state.accommodation.setting.minSleeps,
                cleaning_fee: this.state.accommodation.setting.cleaningFee,
                transactionfee_rate: this.state.accommodation.setting.transactionFeeRate,
                tax_rate: this.state.accommodation.setting.taxRate,
                refundable_amount: this.state.accommodation.setting.refundableAmount,
                tour360: this.state.accommodation.setting.tour360,
                furnished: this.state.accommodation.setting.furnished,
                rental_parking: this.state.accommodation.setting.rentalParking,
                pets_considered: this.state.accommodation.setting.petsConsidered,
                property_rooms: this.state.accommodation.rooms,
                amenities: this.state.accommodation.amenities,
                featured_img: this.state.accommodation.featuredImg,
                gallery_imgs: this.state.accommodation.galleryImgs,
                images: this.state.images
            };
            if (this.state.accommodation.setting.cleaningFee === '') delete property.cleaning_fee;
            if (this.state.accommodation.setting.transactionFeeRate === '')
                delete property.transactionfee_rate;
            if (this.state.accommodation.setting.taxRate === '') delete property.tax_rate;
            if (this.state.accommodation.setting.refundableAmount === '')
                delete property.refundable_amount;
            this.props.updateProperty(property);
        }
    };

    render() {
        const { loading } = this.props;

        return (
            <div className="animated fadeIn">
                <CRow className="property-row">
                    <CCol lg={9}>
                        <CCard>
                            <CCardBody>
                                <CRow className="mt-lg-3 mb-lg-3 pl-3 pr-3">
                                    <CInput
                                        type="text"
                                        id="name"
                                        name="name"
                                        placeholder="Please add title here ..."
                                        value={
                                            this.state.accommodation.name &&
                                            this.state.accommodation.name
                                        }
                                        onChange={this.handleInputChange}
                                    />
                                </CRow>
                                <CNav variant="tabs">
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '1'}
                                            onClick={() => {
                                                this.toggleTab('1');
                                            }}>
                                            Description <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '2'}
                                            onClick={() => {
                                                this.toggleTab('2');
                                            }}>
                                            Location <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '3'}
                                            onClick={() => {
                                                this.toggleTab('3');
                                            }}>
                                            Neighbourhood <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '4'}
                                            onClick={() => {
                                                this.toggleTab('4');
                                            }}>
                                            Transit <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '5'}
                                            onClick={() => {
                                                this.toggleTab('5');
                                            }}>
                                            Setting <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '6'}
                                            onClick={() => {
                                                this.toggleTab('6');
                                            }}>
                                            Pricing <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '7'}
                                            onClick={() => {
                                                this.toggleTab('7');
                                            }}>
                                            Amenities <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                    <CNavItem custom>
                                        <CNavLink
                                            active={this.state.activeTab === '8'}
                                            onClick={() => {
                                                this.toggleTab('8');
                                            }}>
                                            Categories <span className="required">*</span>
                                        </CNavLink>
                                    </CNavItem>
                                </CNav>
                                <CTabContent className="mt-3" activeTab={this.state.activeTab}>
                                    <CTabPane tabId="1">
                                        <TextEditor
                                            show={this.state.cards.description}
                                            toggleCard={this.toggleCard}
                                            title="description"
                                            text={this.state.accommodation.description}
                                            handleChange={this.handleTextChange}
                                        />
                                    </CTabPane>
                                    <CTabPane tabId="2">
                                        <LocationWrapper
                                            address={this.state.accommodation.address}
                                            center={this.state.accommodation.location}
                                            onPlaceChange={this.handlePlaceChange}
                                            onReset={this.handlePlaceReset}
                                        />
                                    </CTabPane>
                                    <CTabPane tabId="3">
                                        <TextEditor
                                            show={this.state.cards.neighbourhood}
                                            toggleCard={this.toggleCard}
                                            title="neighbourhood"
                                            text={this.state.accommodation.neighbourhood}
                                            handleChange={this.handleTextChange}
                                        />
                                    </CTabPane>
                                    <CTabPane tabId="4">
                                        <TextEditor
                                            show={this.state.cards.transit}
                                            toggleCard={this.toggleCard}
                                            title="transit"
                                            text={this.state.accommodation.transit}
                                            handleChange={this.handleTextChange}
                                        />
                                    </CTabPane>
                                    <CTabPane tabId="5">
                                        <PropertySetting
                                            show={this.state.cards.setting}
                                            toggleCard={this.toggleCard}
                                            setting={this.state.accommodation.setting}
                                            onInputChange={this.handleSettingChange}
                                            onCheckboxChange={this.handleCheckboxChange}
                                        />
                                        <BedTypeSelection
                                            show={this.state.cards.bedTypeSelection}
                                            toggleCard={this.toggleCard}
                                            rooms={this.state.accommodation.rooms}
                                            onRoomChange={this.handleRoomChange}
                                            onRemoveRoom={this.handleRemoveRoom}
                                            onAddRoom={this.handleAddRoom}
                                        />
                                        <SimilarPropertySelection
                                            show={this.state.cards.similarPropertySelection}
                                            toggleCard={this.toggleCard}
                                            properties={this.state.properties}
                                            similarProperties={
                                                this.state.accommodation.similarProperties
                                            }
                                            onSimilarPropertyChange={
                                                this.handleSimilarPropertyChange
                                            }
                                        />
                                    </CTabPane>
                                    <CTabPane tabId="6">
                                        <PropertyCalendar
                                            propertyId={this.state.accommodation.id}
                                            price={
                                                this.state.accommodation.setting.price
                                            }
                                            minPrice={this.state.accommodation.setting.minPrice}
                                            maxPrice={this.state.accommodation.setting.maxPrice}
                                            minMonthPrice={
                                                this.state.accommodation.setting.minMonthPrice
                                            }
                                            maxMonthPrice={
                                                this.state.accommodation.setting.maxMonthPrice
                                            }
                                            onInputChange={this.handleSettingChange}
                                        />
                                    </CTabPane>
                                    <CTabPane tabId="7">
                                        {this.state.amenities &&
                                        this.state.accommodation.amenities ? (
                                            <AmenitySelection
                                                show={this.state.cards.amenitySelection}
                                                toggleCard={this.toggleCard}
                                                amenities={this.state.amenities}
                                                selectedAmenities={
                                                    this.state.accommodation.amenities
                                                }
                                                onAmenitySelect={this.handleAmenitySelect}
                                            />
                                        ) : (
                                            ''
                                        )}
                                    </CTabPane>
                                    <CTabPane tabId="8">
                                        {this.state.categories &&
                                        this.state.categories.length > 0 ? (
                                            <CInput
                                                type="select"
                                                name="category"
                                                id="category"
                                                value={this.state.accommodation.category}
                                                onChange={this.handleInputChange}>
                                                {this.state.categories &&
                                                    this.state.categories.map((item, index) => (
                                                        <option key={index} value={item.id}>
                                                            {item.name}
                                                        </option>
                                                    ))}
                                            </CInput>
                                        ) : (
                                            <CircleLoader />
                                        )}
                                    </CTabPane>
                                </CTabContent>
                            </CCardBody>
                        </CCard>
                    </CCol>
                    <CCol lg={3} className="property-action-card">
                        <CCard custom accentColor="danger">
                            <CCardHeader>
                                Property ID
                                <div className="card-header-actions">
                                    <CLink
                                        className="card-header-action"
                                        onClick={(e) => this.toggleCard(e, 'propertyId')}>
                                        <CIcon
                                            name={
                                                this.state.cards.propertyId
                                                    ? 'cil-chevron-bottom'
                                                    : 'cil-chevron-top'
                                            }
                                        />
                                    </CLink>
                                </div>
                            </CCardHeader>
                            <CCollapse custom show={this.state.cards.propertyId}>
                                <CCardBody>
                                    <CInput
                                        type="text"
                                        id="bookervilleID"
                                        name="bookervilleID"
                                        value={this.state.accommodation.bookervilleID}
                                        onChange={this.handleInputChange}
                                    />
                                    <Link
                                        to={`/property/${this.state.accommodation.slug}`}
                                        target="_blank">
                                        <CButton>Preview</CButton>
                                    </Link>
                                    <div className="property-gallery">
                                        {this.state.accommodation.galleryImgs &&
                                        this.state.accommodation.galleryImgs.length > 0 ? (
                                            <PropertyCarousel
                                                items={this.state.accommodation.galleryImgs}
                                            />
                                        ) : (
                                            ''
                                        )}
                                    </div>
                                    <EditGallery onSave={this.handleChangeGalleryImages} />
                                    <div className="featured-img-wrapper text-center">
                                        {this.state.accommodation.featuredImg && (
                                            <div
                                                style={{
                                                    backgroundImage: `url(${this.state.accommodation.featuredImg.file})`,
                                                    backgroundPosition: 'center',
                                                    backgroundSize: 'cover',
                                                    height: '100px'
                                                }}></div>
                                        )}
                                    </div>
                                    <EditImage onSave={this.handleChangeFeaturedImage} />
                                    <CRow>
                                        <CCol xs="12" sm="6" className="text-center">
                                            <CButton
                                                type="reset"
                                                color="primary"
                                                className="mr-1"
                                                onClick={this.handleSubmit}>
                                                {loading === true ? 'Wait...' : 'Save'}
                                            </CButton>
                                        </CCol>
                                        <CCol xs="12" sm="6" className="text-center">
                                            <CButton type="reset" color="light" className="mr-1">
                                                Cancel
                                            </CButton>
                                        </CCol>
                                    </CRow>
                                </CCardBody>
                            </CCollapse>
                        </CCard>
                    </CCol>
                </CRow>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    categories: state.categories.items,
    amenities: state.amenities.items,
    properties: state.properties.items,
    property: state.properties.activeItem,
    loading: state.properties.loading
});

const actionCreators = {
    getAllCategory: categoryAction.getAllCategory,
    getAllAmenity: amenityAction.getAllAmenity,
    getProperties: propertyAction.getPropertyList,
    getProperty: propertyAction.getProperty,
    updateProperty: propertyAction.updateProperty,
    updateMedia: mediaAction.updateMedia,
    deleteMedia: mediaAction.deleteMedia,
    alertError: alertActions.error
};

export default connect(mapStateToProps, actionCreators)(PropertyEdit);
