import React from 'react';
import { Link, withRouter } from 'react-router-dom';

import Logo from '../../assets/logo-horizontal.png';
// import whiteLogo from '../../../assets/logo-white.png';

import { Navbar, Nav, Container } from 'react-bootstrap';

import { connect } from 'react-redux';
import AOS from 'aos';
import $ from 'jquery';

class HeaderNavbar extends React.Component {
    constructor(props) {
        super(props);
        this.navbar = React.createRef();
    }

    componentDidMount() {
        console.log('__HeaderNavbar__');
        AOS.init({ once: true });
        let navEl = $('nav');

        $(function () {
            const navOffsetTop =
                $('#admin-nav').length > 0
                    ? $('#covid-banner').height() + $('#admin-nav').height()
                    : $('#covid-banner').height();
            navEl.css({ top: navOffsetTop });
            $(window).scrollTop() > 0 ? navEl.addClass('sticky') : navEl.removeClass('sticky');
        });

        $(window).on('scroll', function () {
            // $(window).scrollTop() > (navEl.innerHeight() + covidBannerOffset)
            $(window).scrollTop() > 0 ? navEl.addClass('sticky') : navEl.removeClass('sticky');
        });
        if (this.props.location.hash === '') {
            window.scrollTo({ top: 0 });
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.location !== this.props.location) {
            if (this.props.location.hash === '') {
                window.scrollTo({ top: 0 });
            }
        }
    }

    handleNav = (key) => {
        this.props.history.push(`/${key}`);
    };

    render() {
        return (
            <>
                <Navbar ref={this.navbar} bg="none" expand="lg" fixed="top" className="sticky">
                    <Container>
                        <Navbar.Brand
                            onClick={() => this.props.history.push(`/`)}
                            aria-label="Logo">
                            <Link to="/">
                                <img src={Logo} alt="Starsofboston Logo" className="logo" />
                            </Link>
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls="header-nav">
                            <span className="nav-span" />
                            <span />
                            <span />
                        </Navbar.Toggle>
                        <Navbar.Collapse id="header-nav">
                            <Nav onSelect={this.handleNav}>
                                <Nav.Item>
                                    <Nav.Link eventKey="property">Apartments</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="locations">Locations</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="medical">Medical/Corporate</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="services">Services</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="blog">Blog</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="about">About</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="contact">Contact</Nav.Link>
                                </Nav.Item>
                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
            </>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        authentication: state.authentication
    };
};

export default withRouter(connect(mapStateToProps)(HeaderNavbar));
