import { alertActions } from './alert.actions';
import { mediaConstants } from '../_constants';
import { mediaService } from '../_services/media.service';

export const mediaAction = {
    getAllMedia,
    createMedia,
    getMedia,
    updateMedia,
    updateBulkMedia,
    deleteMedia,
    addUploadedMedia
};

function getAllMedia(propertyId) {
    return (dispatch) => {
        dispatch(request());
        if (propertyId === null || propertyId === undefined) propertyId = '';
        mediaService.getAll(propertyId).then(
            (items) => {
                dispatch(success(items));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: mediaConstants.GET_ALL_REQUEST };
    }

    function success(items) {
        return { type: mediaConstants.GET_ALL_SUCCESS, items };
    }

    function failure(error) {
        return { type: mediaConstants.GET_ALL_FAILURE, error };
    }
}

function createMedia(media) {
    return (dispatch) => {
        dispatch(request());
        mediaService.create(media).then(
            (res) => {
                dispatch(success(res));
                dispatch(alertActions.success('File has been created successfully'));
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );
    };

    function request() {
        return { type: mediaConstants.CREATE_MEDIA_REQUEST };
    }

    function success(media) {
        return { type: mediaConstants.CREATE_MEDIA_SUCCESS, media };
    }

    function failure(error) {
        return { type: mediaConstants.CREATE_MEDIA_FAILURE, error };
    }
}

function getMedia(id) {
    return (dispatch) => {
        dispatch(request());
        mediaService.getById(id).then(
            (media) => dispatch(success(media)),
            (error) => dispatch(failure(error.toString()))
        );
    };

    function request() {
        return { type: mediaConstants.GET_MEDIA_REQUEST };
    }

    function success(media) {
        return { type: mediaConstants.GET_MEDIA_SUCCESS, media };
    }

    function failure(error) {
        return { type: mediaConstants.GET_MEDIA_FAILURE, error };
    }
}

function updateMedia(media) {
    return (dispatch) => {
        dispatch(request());
        mediaService.update(media).then(
            (media) => {
                dispatch(success(media));
                dispatch(alertActions.success('File has been updated successfully!'));
                // history.push('/admin/categories')
            },
            (error) => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
            }
        );

        function request() {
            return { type: mediaConstants.UPDATE_MEDIA_REQUEST };
        }

        function success(media) {
            return { type: mediaConstants.UPDATE_MEDIA_SUCCESS, media };
        }

        function failure(error) {
            return { type: mediaConstants.UPDATE_MEDIA_FAILURE, error };
        }
    };
}

function updateBulkMedia(items) {
    return (dispatch) => {
        mediaService.updateBulk(items).then(
            (items) => {
                console.log('success', items);
            },
            (error) => {
                console.log('error');
            }
        );
    };
}

function deleteMedia(id) {
    return (dispatch) => {
        dispatch(request(id));
        mediaService.delete(id).then(
            (res) => {
                dispatch(success(id));
                dispatch(alertActions.success('File has been deleted successfully'));
                // history.push('/admin/categories')
            },
            (error) => dispatch(failure(id, error.toString()))
        );
    };

    function request(id) {
        return { type: mediaConstants.DELETE_MEDIA_REQUEST, id };
    }

    function success(id) {
        return { type: mediaConstants.DELETE_MEDIA_SUCCESS, id };
    }

    function failure(id, error) {
        return { type: mediaConstants.DELETE_MEDIA_FAILURE, id, error };
    }
}

function addUploadedMedia(media) {
    return { type: mediaConstants.MEDIA_UPLOADED, media };
}
