import React from 'react';
import { CCol, CFormGroup, CInput, CInputGroup, CLabel, CRow } from '@coreui/react';
import CIcon from '@coreui/icons-react/es/CIcon';

export default function Room({ room, onRoomChange, onRemoveRoom }) {
    return (
        <CRow>
            <CCol xs="12" sm="5">
                <CFormGroup>
                    <CLabel htmlFor="price">Room Name :</CLabel>
                    <CInputGroup>
                        <CInput
                            type="text"
                            id="name"
                            name="name"
                            value={room.name}
                            onChange={(e) => onRoomChange(e, room.id)}
                        />
                    </CInputGroup>
                </CFormGroup>
            </CCol>
            <CCol xs="12" sm="5">
                <CFormGroup>
                    <CLabel htmlFor="bedType">BedType :</CLabel>
                    <CInput
                        type="select"
                        name="bed_type"
                        id="bedType"
                        value={room.bed_type}
                        onChange={(e) => onRoomChange(e, room.id)}>
                        <option value="T">Twin bed</option>
                        <option value="S">Sofa bed</option>
                        <option value="Q">Queen bed</option>
                        <option value="K">King bed</option>
                        <option value="C">Chair bed</option>
                        <option value="M">Murphy bed</option>
                        <option value="F">Full Bed</option>
                        <option value="P">Single Chair Sleeper Bed</option>
                    </CInput>
                </CFormGroup>
            </CCol>
            <CCol xs="12" sm="2">
                <CIcon name="cil-x-circle" onClick={() => onRemoveRoom(room)} />
            </CCol>
        </CRow>
    );
}
