import React from 'react';
import GuestReviewCard from './GuestReviewCard';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Slider from 'react-slick';
import ScrollableAnchor from 'react-scrollable-anchor';

const settings = {
    dots: false,
    arrows: true,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 1
            }
        },
        {
            breakpoint: 1200,
            settings: {
                slidesToShow: 2
            }
        }
    ]
};

function NextArrow(props) {
    const { className, onClick } = props;
    return <FontAwesomeIcon icon={faChevronRight} className={className} onClick={onClick} />;
}

function PrevArrow(props) {
    const { className, onClick } = props;
    return <FontAwesomeIcon icon={faChevronLeft} className={className} onClick={onClick} />;
}

export default function GuestReviews() {
    return (
        <ScrollableAnchor id={'guest-reviews'}>
            <section className="reviews-block">
                <h2>Guest Reviews</h2>
                <div className="container">
                    <div className="carousel-container position-relative">
                        <Slider {...settings}>
                            <GuestReviewCard key={4} id={88} rating={5} />
                            <GuestReviewCard key={1} id={89} rating={5} />
                            <GuestReviewCard key={2} id={90} rating={5} />
                            <GuestReviewCard key={3} id={91} rating={5} />
                        </Slider>
                    </div>
                </div>
            </section>
        </ScrollableAnchor>
    );
}
