import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CContainer,
    CForm,
    CFormFeedback,
    CFormGroup,
    CInput,
    CRow
} from '@coreui/react';
import React, { Component } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { userActions } from '../../../_actions';
import connect from 'react-redux/es/connect/connect';
import CircleLoader from '../../../components/common/Loaders/CircleLoader';

const validationSchema = Yup.object().shape({
    first_name: Yup.string(),
    last_name: Yup.string(),
    username: Yup.string()
        .min(5, `Username has to be at least 5 characters`)
        .required('Username is required'),
    email: Yup.string().email('Invalid email address').required('Email is required!'),
    role: Yup.string().required('Role is required!'),
    status: Yup.string().required('Status is required!'),
    password: Yup.string()
});

class EditUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: {},
            loading: false
        };
    }

    componentDidMount() {
        this.props.getUser(this.props.match.params.id);
    }

    UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.user !== this.props.user) {
            this.setState({
                user: {
                    id: nextProps.user.id,
                    first_name: nextProps.user.first_name,
                    last_name: nextProps.user.last_name,
                    username: nextProps.user.username,
                    email: nextProps.user.email,
                    role: nextProps.user.role.charAt(0).toUpperCase(),
                    status: nextProps.user.status.charAt(0).toUpperCase(),
                    password: nextProps.user.password
                }
            });
        }
    }

    handleSubmit = (fields) => {
        console.log(fields);
        this.props.updateUser(fields);
    };

    render() {
        const { user } = this.state;
        const { loading } = this.props;

        return (
            <div className="c-app c-default-layout flex-row align-items-center">
                <CContainer>
                    <CRow className="justify-content-center">
                        <CCol md="9" lg="7" xl="6">
                            <CCard className="mx-4">
                                <CCardHeader>
                                    <h3 className="text-center">Edit user</h3>
                                </CCardHeader>
                                <CCardBody className="p-4">
                                    {user && user.email ? (
                                        <Formik
                                            initialValues={user}
                                            validationSchema={validationSchema}
                                            onSubmit={(fields) => this.handleSubmit(fields)}>
                                            {({
                                                values,
                                                errors,
                                                touched,
                                                dirty,
                                                handleChange,
                                                handleBlur,
                                                handleSubmit,
                                                isValid,
                                                handleReset
                                            }) => (
                                                <CRow>
                                                    <CCol lg="12">
                                                        <CForm
                                                            onSubmit={handleSubmit}
                                                            noValidate
                                                            name="userForm">
                                                            <CFormGroup>
                                                                <CInput
                                                                    type="text"
                                                                    name="first_name"
                                                                    id="first_name"
                                                                    placeholder="First Name"
                                                                    autoComplete="given-name"
                                                                    valid={!errors.first_name}
                                                                    invalid={
                                                                        touched.first_name &&
                                                                        !!errors.first_name
                                                                    }
                                                                    autoFocus={true}
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.first_name}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.first_name}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CFormGroup>
                                                                <CInput
                                                                    type="text"
                                                                    name="last_name"
                                                                    id="last_name"
                                                                    placeholder="Last Name"
                                                                    autoComplete="family-name"
                                                                    valid={!errors.last_name}
                                                                    invalid={
                                                                        touched.last_name &&
                                                                        !!errors.last_name
                                                                    }
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.last_name}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.last_name}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CFormGroup>
                                                                <CInput
                                                                    type="text"
                                                                    name="username"
                                                                    id="username"
                                                                    placeholder="User Name"
                                                                    autoComplete="username"
                                                                    valid={!errors.username}
                                                                    invalid={
                                                                        touched.username &&
                                                                        !!errors.username
                                                                    }
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.username}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.username}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CFormGroup>
                                                                <CInput
                                                                    type="email"
                                                                    name="email"
                                                                    id="email"
                                                                    placeholder="Email"
                                                                    autoComplete="email"
                                                                    valid={!errors.email}
                                                                    invalid={
                                                                        touched.email &&
                                                                        !!errors.email
                                                                    }
                                                                    required
                                                                    onChange={handleChange}
                                                                    onBlur={handleBlur}
                                                                    value={values.email}
                                                                />
                                                                <CFormFeedback>
                                                                    {errors.email}
                                                                </CFormFeedback>
                                                            </CFormGroup>
                                                            <CRow>
                                                                <CCol
                                                                    xs="12"
                                                                    sm="6"
                                                                    className="text-center">
                                                                    <CFormGroup>
                                                                        <CInput
                                                                            type="select"
                                                                            name="role"
                                                                            id="role"
                                                                            valid={!errors.role}
                                                                            invalid={
                                                                                touched.role &&
                                                                                !!errors.role
                                                                            }
                                                                            required
                                                                            onBlur={handleBlur}
                                                                            value={values.role}
                                                                            onChange={handleChange}>
                                                                            <option
                                                                                value="E"
                                                                                selected>
                                                                                Editor
                                                                            </option>
                                                                            <option value="A">
                                                                                Admin
                                                                            </option>
                                                                            <option value="S">
                                                                                SuperAdmin
                                                                            </option>
                                                                        </CInput>
                                                                    </CFormGroup>
                                                                </CCol>
                                                                <CCol
                                                                    xs="12"
                                                                    sm="6"
                                                                    className="text-center">
                                                                    <CFormGroup>
                                                                        <CInput
                                                                            type="select"
                                                                            name="status"
                                                                            id="status"
                                                                            valid={!errors.status}
                                                                            invalid={
                                                                                touched.status &&
                                                                                !!errors.status
                                                                            }
                                                                            required
                                                                            onBlur={handleBlur}
                                                                            value={values.status}
                                                                            onChange={handleChange}>
                                                                            <option value="P">
                                                                                Pending
                                                                            </option>
                                                                            <option value="A">
                                                                                Allowed
                                                                            </option>
                                                                            <option value="B">
                                                                                Blocked
                                                                            </option>
                                                                        </CInput>
                                                                    </CFormGroup>
                                                                </CCol>
                                                            </CRow>
                                                            <CFormGroup>
                                                                <CInput
                                                                    type="password"
                                                                    name="password"
                                                                    id="password"
                                                                    placeholder="New password"
                                                                    valid={!errors.password}
                                                                    invalid={
                                                                        touched.password &&
                                                                        !!errors.password
                                                                    }
                                                                    required
                                                                    onBlur={handleBlur}
                                                                    value={values.password}
                                                                    onChange={handleChange}
                                                                />
                                                            </CFormGroup>
                                                            <CRow>
                                                                <CCol
                                                                    xs="12"
                                                                    sm="6"
                                                                    className="text-center">
                                                                    <CButton
                                                                        type="submit"
                                                                        color="success"
                                                                        className="mr-1"
                                                                        disabled={
                                                                            loading || !isValid
                                                                        }>
                                                                        {loading
                                                                            ? 'Wait...'
                                                                            : 'Save'}
                                                                    </CButton>
                                                                </CCol>
                                                                <CCol
                                                                    xs="12"
                                                                    sm="6"
                                                                    className="text-center">
                                                                    <CButton
                                                                        type="reset"
                                                                        color="danger"
                                                                        className="mr-1"
                                                                        onClick={handleReset}>
                                                                        Reset
                                                                    </CButton>
                                                                </CCol>
                                                            </CRow>
                                                        </CForm>
                                                    </CCol>
                                                </CRow>
                                            )}
                                        </Formik>
                                    ) : (
                                        <CircleLoader />
                                    )}
                                </CCardBody>
                            </CCard>
                        </CCol>
                    </CRow>
                </CContainer>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    loading: state.users.loading,
    user: state.users.activeItem
});

const actionCreators = {
    getUser: userActions.getUser,
    updateUser: userActions.updateUser
};

export default connect(mapStateToProps, actionCreators)(EditUser);
