import React from 'react';

import moment from 'moment';

import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { bookingActions } from '../../../_actions';
import WithPixel from '../../../components/WithPixel';
import WithGATracker from '../../../components/WithGATracker';

class CheckOutSuccess extends React.Component {
    componentDidMount() {
        if (this.props.booking.checkinDate === '') {
            this.props.history.goBack();
        }
    }

    componentWillUnmount() {
        this.props.addBookingInfo({
            propertyId: '',
            bookervilleID: '',
            checkinDate: '',
            checkoutDate: '',
            adultsCount: '',
            childrenCount: '',
            propertyFee: '',
            cleaningFee: '',
            refundableAmount: '',
            transactionFee: '',
            tax: '',
            total: '',
            nights: '',
            quote: '',
            guest: {}
        });
    }

    render() {
        const { booking } = this.props;
        return (
            <div>
                <section className="main-background-2"></section>
                <section>
                    <div className="container">
                        <div className="col-lg-6 col-md-8 col-sm-12 offset-lg-3 offset-md-2">
                            <h4 className="text-center">
                                Your booking has been confirmed! <br /> Your reservation ID is:{' '}
                                {booking.bookingId}
                            </h4>
                            <p>Arrival: {moment(booking.checkinDate).format('MMM DD, YYYY')}</p>
                            <p>Departure: {moment(booking.checkoutDate).format('MMM DD, YYYY')}</p>
                            <p>Rent: ${booking.propertyFee}</p>
                            <p>Cleaning Fee: ${booking.cleaningFee}</p>
                            <p>Security Deposit: ${booking.refundableAmount}</p>
                            <p>Transaction Fee: ${booking.transactionFee}</p>
                            <p>Tax: ${booking.tax}</p>
                            <p>Total: ${booking.total}</p>
                            <p>Please print this page for your records.</p>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    booking: state.booking
});

const actionCreators = {
    addBookingInfo: bookingActions.addBookingInfo
};

export default connect(
    mapStateToProps,
    actionCreators
)(WithPixel(WithGATracker(withRouter(CheckOutSuccess))));
