import React, { Component } from 'react';
import { propTypes } from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import { bookingActions } from '../../_actions';
import { connect } from 'react-redux';
import DatePicker from 'react-datepicker';
import DatePickerInput from './DatePickerInput';
import moment from 'moment';
import CircleLoader from '../common/Loaders/CircleLoader';

function formatToFloat(value) {
    return parseFloat(value.toFixed(2));
}

function validateNumber(value) {
    const reg = /^[0-9\b]+$/;
    if (value === '' || reg.test(value)) {
        return true;
    }
}

function isSameDay(a, b) {
    // if (!moment.isMoment(a) || !moment.isMoment(b)) return false;
    // // Compare least significant, most likely to change units first
    // // Moment's isSame clones moment inputs and is a tad slow
    // return a.date() === b.date()
    //     && a.month() === b.month()
    //     && a.year() === b.year();
    return a === b;
}

class BookingForm extends Component {
    state = {
        excludeDates: []
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        console.log('componentDidUpdate');
        if (this.props.property.id !== prevProps.property.id && this.props.property.checked_dates) {
            let array = []; // array of moment objects
            this.props.property.checked_dates.map((checked_date) => {
                let currDate = moment(new Date(checked_date.arrival_date)).startOf('day');
                let lastDate = moment(new Date(checked_date.departure_date)).startOf('day');
                do {
                    array.push(currDate.clone().toDate());
                } while (currDate.add(1, 'days').diff(lastDate) <= 0);
            });
            this.setState({
                excludeDates: array
            });
        }
        // if (this.props.booking.checkinDate !== '' && this.props.booking.checkoutDate !== '' && this.props.booking.adultsCount !== '') {
        //     this.props.addBookingInfo({
        //         propertyFee: this.calculateBooking(),
        //         cleaningFee: this.props.property.cleaning_fee,
        //         refundableAmount: this.props.property.refundable_amount,
        //         transactionFee: this.calculateTransactionFee(),
        //         tax: this.calculateTax(),
        //         total: this.calculateTotal(),
        //     })
        // }
    }

    renderDayContents = (day, date) => {
        if (moment(date).isBefore(moment().startOf('day'))) {
            return (
                <div className="past-date">
                    <h6>{day}</h6>
                </div>
            );
        } else {
            if (this.isDayBooked(date) === true) {
                return (
                    <div className="booked-date">
                        <h6>{day}</h6>
                    </div>
                );
            } else {
                return (
                    <div className="date-detail">
                        <h6>{day}</h6>
                        <span>${this.getPrice(date)}</span>
                    </div>
                );
            }
        }
    };

    getPrice = (date) => {
        let price = this.props.property.price;
        this.props.property.pricing_items.map((item) => {
            // if (moment(date).isBetween(item.start_date, item.end_date)) {
            //     price = item.price
            // }
            if (moment(date).diff(item.start_date) >= 0 && moment(date).diff(item.end_date) <= 0) {
                price = item.price;
            }
        });
        return price;
    };

    isDayBooked = (date) => {
        return this.state.excludeDates.some(
            (edate) => moment(date).isSame(moment(edate), 'date'),
            'day'
        );
    };

    handleCheckInChange = (checkinDate) => {
        const { checkoutDate } = this.props.booking;
        if (checkoutDate !== '') {
            this.addBookingInfo({
                checkinDate: checkinDate,
                checkoutDate: ''
            });
        } else {
            this.addBookingInfo({ checkinDate: checkinDate });
        }
        // if (checkoutDate !== '' && moment(checkoutDate).diff(moment(checkinDate)) < this.props.property.min_sleeps) {
        //     this.addBookingInfo({
        //         checkinDate: checkinDate,
        //         checkoutDate: ''
        //     })
        // } else {
        //     this.addBookingInfo({checkinDate: checkinDate})
        // }
    };

    handleCheckOutChange = (checkoutDate) => {
        this.addBookingInfo({ checkoutDate: checkoutDate });
    };

    handleAdultsCountChange = (e) => {
        if (validateNumber(e.target.value)) {
            this.addBookingInfo({ adultsCount: e.target.value });
        }
    };

    handleChildrenCountChange = (e) => {
        if (validateNumber(e.target.value)) {
            this.addBookingInfo({ childrenCount: e.target.value });
        }
    };

    addBookingInfo = (param) => {
        const { booking } = this.props;
        let obj = {
            ...booking,
            ...param
        };
        console.log('addBookingInfo', booking, obj);
        if (obj.checkinDate !== '' && obj.checkoutDate !== '' && obj.adultsCount !== '') {
            this.props.addBookingInfo({
                propertyId: this.props.property.id,
                ...param,
                ...this.calculateNights(obj.checkinDate, obj.checkoutDate),
                ...this.calculateBooking(obj.checkinDate, obj.checkoutDate)
            });
        } else {
            this.props.addBookingInfo({
                ...param,
                propertyFee: '',
                cleaningFee: '',
                refundableAmount: '',
                transactionFee: '',
                tax: '',
                total: ''
            });
        }
    };

    calculateNights = (checkinDate, checkoutDate) => {
        const nights = moment(checkoutDate).diff(moment(checkinDate), 'day');
        return { nights };
    };

    calculateBooking = (checkinDate, checkoutDate) => {
        let propertyFee = 0,
            tax,
            subTotal,
            transactionFee,
            total;

        let currDate = moment(checkinDate, 'YYYY-mm-dd').startOf('day');
        let lastDate = moment(checkoutDate, 'YYYY-mm-dd').startOf('day');
        do {
            console.log('getPrice', this.getPrice(currDate.clone().toDate()));
            propertyFee += this.getPrice(currDate.clone().toDate());
        } while (currDate.add(1, 'days').diff(lastDate) <= 0);

        tax = formatToFloat((this.props.property.tax_rate * propertyFee) / 100);
        subTotal = formatToFloat(
            propertyFee +
                tax +
                +this.props.property.cleaning_fee +
                this.props.property.refundable_amount
        );
        transactionFee = formatToFloat((this.props.property.transactionfee_rate * subTotal) / 100);
        total = formatToFloat((1 + this.props.property.transactionfee_rate / 100) * subTotal);
        return {
            propertyFee,
            tax,
            transactionFee,
            cleaningFee: this.props.property.cleaning_fee,
            refundableAmount: this.props.property.refundable_amount,
            total
        };
    };

    render() {
        const { checkinDate, checkoutDate, adultsCount, childrenCount } = this.props.booking;

        const { excludeDates } = this.state;

        return this.props.loading !== true ? (
            <div className="p-0">
                <div className="row">
                    <div className="col-12 col-lg-12 col-xl-6 d-flex flex-column">
                        <DatePicker
                            disabled={this.props.disabled}
                            selectsStart
                            startDate={checkinDate}
                            endDate={checkoutDate}
                            selected={checkinDate}
                            value={checkinDate}
                            className="form-control custom-form-control "
                            dateFormat="MM/dd/yyyy"
                            placeholderText="Check In"
                            customInput={<DatePickerInput />}
                            renderDayContents={this.renderDayContents}
                            excludeDates={excludeDates}
                            onChange={(date) => this.handleCheckInChange(date)}
                        />
                        <DatePicker
                            disabled={this.props.disabled}
                            selectEnd
                            startDate={checkinDate}
                            endDate={checkoutDate}
                            selected={checkoutDate}
                            value={checkoutDate}
                            minDate={moment(checkinDate)
                                .add(this.props.property.min_sleeps, 'day')
                                .toDate()}
                            className="form-control custom-form-control "
                            dateFormat="MM/dd/yyyy"
                            placeholderText="Check Out"
                            customInput={<DatePickerInput />}
                            renderDayContents={this.renderDayContents}
                            excludeDates={excludeDates}
                            onChange={(date) => this.handleCheckOutChange(date)}
                        />
                    </div>
                    <div className="col-12 col-lg-12 col-xl-6 d-flex flex-column">
                        <div className="input-group mb-3">
                            <div className=" input-group-append">
                                <span className="input-group-text bg-white">
                                    <FontAwesomeIcon icon={faUser} />
                                </span>
                            </div>
                            <input
                                type="text"
                                className="form-control custom-form-control"
                                placeholder="Adults"
                                value={adultsCount}
                                onChange={(count) => this.handleAdultsCountChange(count)}
                                aria-label="adults"
                                aria-describedby="adults"
                            />
                        </div>
                        <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                <span className="input-group-text bg-white">
                                    <FontAwesomeIcon icon={faUser} />
                                </span>
                            </div>
                            <input
                                type="text"
                                className="form-control custom-form-control"
                                placeholder="Children"
                                value={childrenCount}
                                onChange={(count) => this.handleChildrenCountChange(count)}
                                aria-label="Children"
                                aria-describedby="children"
                            />
                        </div>
                    </div>
                </div>
            </div>
        ) : (
            <CircleLoader />
        );
    }
}

const mapStateToProps = (state) => ({
    property: state.properties.activeItem,
    loading: state.properties.loading,
    booking: state.booking
});

const actionCreators = {
    addBookingInfo: bookingActions.addBookingInfo
};

export default connect(mapStateToProps, actionCreators)(BookingForm);
