import { bookingConstants } from '../_constants';

const initialState = {
    propertyId: '',
    bookervilleID: '',
    checkinDate: '',
    checkoutDate: '',
    adultsCount: '',
    childrenCount: '',
    propertyFee: '',
    cleaningFee: '',
    refundableAmount: '',
    transactionFee: '',
    tax: '',
    total: '',
    nights: '',
    quote: '',
    guest: {}
};

export function booking(state = initialState, action) {
    switch (action.type) {
        case bookingConstants.Add_BOOKING_INFOMATION:
            return {
                ...state,
                ...action.data
            };
        case bookingConstants.Add_BOOKING_PAYMENT_REQUEST:
            return {
                ...state,
                loading: true
            };
        case bookingConstants.Add_BOOKING_PAYMENT_SUCCESS:
            return {
                ...state,
                bookingId: action.data.booking_id,
                loading: false
            };
        case bookingConstants.Add_BOOKING_PAYMENT_FAILURE:
            return {
                ...state,
                loading: false
            };
        default:
            return state;
    }
}
