import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ScrollableAnchor from 'react-scrollable-anchor';
import PropertyCard from '../PropertyCard';
import CircleLoader from '../../common/Loaders/CircleLoader';
import { propertyAction } from '../../../_actions/property.actions';
import { alertActions } from '../../../_actions';
import { connect } from 'react-redux';

class LookRentals extends Component {
    componentDidMount() {
        this.props.getFilteredListing({ category: '1-Bedroom' });
    }

    render() {
        const { properties } = this.props;

        return (
            <ScrollableAnchor id={'look-rentals'}>
                <section className="look-rentals-block">
                    <div className="container">
                        <div className="d-flex justify-content-between">
                            <div>
                                <p>FIND YOUR IDEAL STAY</p>
                                <h2>Take a look at our rentals</h2>
                            </div>
                            <Link to="/property">
                                <button className="btn btn-outline-primary user-btn user-btn-outline float-right mb-4">
                                    Read More
                                </button>
                            </Link>
                        </div>
                        <div className="row">
                            {properties ? (
                                properties.map((property, index) => (
                                    <PropertyCard
                                        key={index}
                                        id={property.id}
                                        address={property.address}
                                        title={property.name}
                                        slug={property.slug}
                                        price={property.price}
                                        minPrice={property.min_price}
                                        maxPrice={property.max_price}
                                        minMonthPrice={property.min_month_price}
                                        maxMonthPrice={property.max_month_price}
                                        category={property.category}
                                        bedroomCount={property.bedroom_count}
                                        bathroomCount={property.bathroom_count}
                                        sharedBathroom={property.shared_bathroom}
                                        minSleeps={property.min_sleeps}
                                        sleeps={property.sleeps}
                                        rentalParking={property.rental_parking}
                                        furnished={property.furnished}
                                        petsConsidered={property.pets_considered}
                                        image={property.featured_img}
                                    />
                                ))
                            ) : (
                                <CircleLoader />
                            )}
                        </div>
                    </div>
                </section>
            </ScrollableAnchor>
        );
    }
}

const mapStateToProps = (state) => {
    const { properties } = state;
    return {
        properties: properties.items
    };
};

const actionCreators = {
    getFilteredListing: propertyAction.getFilteredListing,
    alertError: alertActions.error
};

export default connect(mapStateToProps, actionCreators)(LookRentals);
