import React, { Component } from 'react';

import { connect } from 'react-redux';
import CircleLoader from './Loaders/CircleLoader';
import { Link } from 'react-router-dom';

class EditableBlock extends Component {
    render() {
        const { id, texts, authentication, lg, sm } = this.props;
        return (
            <div className="edit-text-block">
                {texts && texts.length > 0 ? '' : <CircleLoader />}
                {lg ? (
                    <h2 className="text-blue">
                        {texts && texts.length > 0 ? texts[id].title : ''}
                    </h2>
                ) : sm ? (
                    <h5 className="text-blue">
                        {texts && texts.length > 0 ? texts[id].title : ''}
                    </h5>
                ) : (
                    <h4 className="text-blue">
                        {texts && texts.length > 0 ? texts[id].title : ''}
                    </h4>
                )}
                {texts && texts.length > 0 ? (
                    <div dangerouslySetInnerHTML={{ __html: texts[id].text }} />
                ) : (
                    ''
                )}
                {authentication.user && texts && texts.length > 0 && (
                    <div className="text-center">
                        <Link to={`/admin/text/${texts[id].id}`} className="edit-btn">
                            Edit
                        </Link>
                    </div>
                )}
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const { authentication, texts } = state;
    return {
        authentication: authentication,
        texts: texts.items
    };
};

export default connect(mapStateToProps, null)(EditableBlock);
